import React, { Component, Fragment } from 'react';
import 'emoji-mart/css/emoji-mart.css'
import { Picker } from 'emoji-mart'
import cx from 'classnames'
import OutsideClickHandler from 'react-outside-click-handler';

const MAX_LENGTH = 40
class ElemMenuEditor extends Component {
  state = {
    focused: false,
    showEmojiList: false
  }

  componentWillReceiveProps = (newProps) => {
    if (newProps.item && newProps.item.template && newProps.item.template.length) {
      if (`${this.props.item.text} {%${newProps.item.template}%}`.length < MAX_LENGTH) {
        this.props.onChangeText(`${this.props.item.text} {%${newProps.item.template}%}`)
      }

      this.props.onSetTemplate('')
      this.editor.focus()
    }
  }

  setEditor = (editor) => {
    this.editor = editor;
  };

  showLabel = () => {
    const {
      label
    } = this.props
    if (label) {
      return <div className="constructor-elemmenu__label constructor-elemmenu__label_question">Вопрос:</div>
    }
    return <div />
  }

  render() {
    const { focused, showEmojiList } = this.state;
    const {
      placeholder,
      modifier,
      onChangeText,
      toolbar,
      item,
      onSelectItem,
      onOpenFloatMenu,
      currentElem,
      onSelectElem
    } = this.props

    if (currentElem && currentElem.msgId === item.id) {
      setTimeout(() => {
        this.editor && this.editor.focus()
        onSelectElem({ ...currentElem, msgId: '' })
      }, 100);
    }

    return (
      <Fragment>
        <OutsideClickHandler
          onOutsideClick={() => {
            this.setState({ focused: false })
          }}
        >
          {
            toolbar && focused
              ? <div className="constructor-elemmenu__toolbar"
                onClick={() => {
                  this.setState({ focused: true })
                }}
              >
                <div
                  className='constructor-elemmenu__template'
                  onClick={(e) => {
                    onSelectItem(item.id)
                    onOpenFloatMenu(e, 'showTemplate')
                  }
                  }>{'{}'}
                </div>
                <div
                  className='constructor-elemmenu__emoji'
                  onClick={(e) => {
                    this.setState({ showEmojiList: true })
                  }
                  }>
                  <svg width="13" height="12" viewBox="0 0 13 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <g clipPath="url(#clip0)">
                      <path d="M4.91209 4.81355C5.18569 4.53995 5.18569 4.09208 4.91209 3.81848C4.63849 3.54488 4.19062 3.54488 3.91689 3.81848L3.84569 3.88968L3.77449 3.81848C3.50089 3.54488 3.05302 3.54488 2.77942 3.81848C2.50582 4.09208 2.50582 4.53995 2.77942 4.81355L3.84569 5.87982L4.91209 4.81355Z" fill="white" />
                      <path d="M8.6553 3.81848L8.5841 3.88968L8.5129 3.81848C8.2393 3.54488 7.79143 3.54488 7.5177 3.81848C7.2441 4.09208 7.2441 4.53995 7.5177 4.81355L8.58396 5.87982L9.65023 4.81355C9.92383 4.53995 9.92383 4.09208 9.65023 3.81848C9.37676 3.54488 8.9289 3.54488 8.6553 3.81848Z" fill="white" />
                      <path d="M6.21484 0C2.90644 0 0.214844 2.6916 0.214844 6C0.214844 9.3084 2.90644 12 6.21484 12C9.52324 12 12.2148 9.3084 12.2148 6C12.2148 2.6916 9.52324 0 6.21484 0ZM6.21484 11.3333C3.27404 11.3333 0.88151 8.9408 0.88151 6C0.88151 3.0592 3.27404 0.666667 6.21484 0.666667C9.15564 0.666667 11.5482 3.0592 11.5482 6C11.5482 8.9408 9.15564 11.3333 6.21484 11.3333Z" fill="white" />
                      <path d="M8.36281 7.20083C7.15948 8.2735 5.27401 8.2735 4.07068 7.20083C3.93321 7.07816 3.72255 7.0903 3.60015 7.2279C3.47761 7.36536 3.48975 7.57603 3.62721 7.69856C4.35308 8.34563 5.28495 8.66936 6.21668 8.66936C7.14841 8.66936 8.08028 8.34576 8.80628 7.69856C8.94375 7.57603 8.95588 7.36536 8.83335 7.2279C8.71081 7.09043 8.50028 7.07816 8.36281 7.20083Z" fill="white" />
                    </g>
                    <defs>
                      <clipPath id="clip0">
                        <rect width="12" height="12" fill="white" transform="translate(0.214844)" />
                      </clipPath>
                    </defs>
                  </svg>
                </div>
                <div
                  className={
                    cx(
                      'constructor-elemmenu__counter',
                      {
                        'constructor-elemmenu__counter_limit': item.text.length > MAX_LENGTH
                      })
                  }><span>{item.text.trim().length}</span> / {MAX_LENGTH}
                </div>
              </div> : ''

          }
          <OutsideClickHandler
            onOutsideClick={() => {
              this.setState({ showEmojiList: false })
            }}
          >
            {showEmojiList ?
              <Picker
                set='apple'
                onSelect={(emoji) => {
                  onChangeText(`${item.text}${emoji.native}`)
                  this.setState({ showEmojiList: false })
                  this.editor.focus()
                }}
                style={{ position: 'absolute', left: '4px', width: '280px' }}
              />
              : ''}
          </OutsideClickHandler>
          <textarea
            type="text"
            ref={this.setEditor}
            className={
              cx(
                'constructor-elemmenu__input',
                'constructor-elemmenu__input_textarea',
                `${modifier ? `constructor-elemmenu__input_${modifier}` : ''}`,
                {
                  'constructor-elemmenu__input_focused': focused
                })
            }
            value={item ? item.text : ''}
            placeholder={placeholder}
            onChange={(e) => {
              if (e.target.value.length <= MAX_LENGTH) {
                onChangeText(e.target.value)
              }
            }}
            onFocus={() => { this.setState({ focused: true }) }}
          />
        </OutsideClickHandler>
      </Fragment>
    )
  }
}

export default ElemMenuEditor;
