import React, { Component } from 'react'
import { connect } from 'react-redux';

import { constructorBotActions } from '../../actions/index'

import ElemMenuMessages from '../../components/constructor/ElemMenu/message/ElemMenuMessages'

class ElemMenuBotMessagesController extends Component {
  handleFloatMenu = (e, floatMenuType) => {
    const { openFloatMenu } = this.props
    if (floatMenuType === 'answerCreate') {
      openFloatMenu(
        'auto',
        e.target.closest('.constructor-elemmenu').getBoundingClientRect().left - 25,
        0,
        floatMenuType
      )
    } else {
      openFloatMenu(
        e.target.getBoundingClientRect().top,
        e.target.closest('.constructor-elemmenu').getBoundingClientRect().left,
        'auto',
        floatMenuType
      )
    }
  }

  render() {
    const {
      constructorBot,
      unlinkElem,
      selectItem,
      changeItem,
      copyItem,
      removeItem,
      moveItem,
      addBtn,
      removeBtn,
      selectBtn,
      selectElem,
      setTemplate,
      removeAnswer,
    } = this.props

    const { currentElem, currentBtn, fields } = constructorBot

    if (!currentElem.id || currentElem.id.startsWith('link_')) {
      return null
    }

    return (
      <ElemMenuMessages
        fields={fields}
        currentElem={currentElem}
        currentBtn={currentBtn}
        handleFloatMenu={this.handleFloatMenu}
        onRemoveAnswer={removeAnswer}
        onSelectItem={selectItem}
        onCopyItem={copyItem}
        onRemoveItem={removeItem}
        onMoveItem={moveItem}
        onChangeItem={changeItem}
        onSelectElem={selectElem}
        onSelectBtn={selectBtn}
        onAddBtn={addBtn}
        onRemoveBtn={removeBtn}
        onSetTemplate={setTemplate}
        onUnlinkElem={unlinkElem}
      />
    )
  }
}

const mapStateToProps = (state) => {
  const { constructorBot, loading, error } = state

  return {
    constructorBot,
    loading,
    error
  };
};

const mapDispatchToProps = {
  changeTitle: constructorBotActions.title.change,
  unlinkElem: constructorBotActions.elems.unlink,
  selectElem: constructorBotActions.elems.select,
  copyElem: constructorBotActions.elems.copy,
  removeElem: constructorBotActions.elems.remove,
  addBtn: constructorBotActions.button.add,
  removeBtn: constructorBotActions.button.remove,
  selectBtn: constructorBotActions.button.select,
  removeAnswer: constructorBotActions.answer.remove,
  selectItem: constructorBotActions.item.select,
  copyItem: constructorBotActions.item.copy,
  removeItem: constructorBotActions.item.remove,
  moveItem: constructorBotActions.item.move,
  changeItem: constructorBotActions.item.change,
  removeFile: constructorBotActions.file.remove,
  removeImage: constructorBotActions.image.remove,
  setTemplate: constructorBotActions.template.set,
  openFloatMenu: constructorBotActions.floatmenu.open,
};

export default connect(mapStateToProps, mapDispatchToProps)(ElemMenuBotMessagesController)