import React from 'react';
import classnames from 'classnames';
import { Link, useRouteMatch } from 'react-router-dom';

import Icon from '../Icon';

import './SidebarLink.scss';

export default function SidebarLink({ to, match, label, icon, iconWidth = 16, develop }) {
  const routeMatch = useRouteMatch(match || to);

  const classes = classnames({
    'sidebar-item': true,
    'sidebar-is-active': !!routeMatch,
    'sidebar-is-iconed': !!icon,
  });

  if (develop) {
    return (
      <div className={classes}>
        {
          !!icon && <div className='sidebar-icon'>
            <Icon name={icon} width={iconWidth} />
          </div>
        }
        <div className="sidebar-link sidebar-link_develop">{label} <span className="sidebar-link-develop">(в разработке)</span></div>
      </div>
    )
  }

  return (
    <div className={classes}>
      <Link
        to={to}
        className='sidebar-link'
      >
        {
          !!icon && <div className='sidebar-icon'>
            <Icon name={icon} width={iconWidth} />
          </div>
        }

        {label}
      </Link>
      {/*
        !!children && <SidebarSubmenu
          className='sidebar-submenu'
          items={children}
        />
      */}
    </div>
  );
}
