
const UrlHelper = {}

export const buildQuery = (data) => {
  var query = [];
  for (var key in data) {
    if (!data.hasOwnProperty(key)) {
      continue;
    }
    query.push(encodeURIComponent(key) + '=' + encodeURIComponent(data[key]));
  }
  return query.length ? query.join('&') : '';
}

export const parseUrl = (url) => {
  if (!UrlHelper.aCache.hasOwnProperty(url)) {
    var tmp = document.createElement('a');
    tmp.href = url;
    UrlHelper.aCache[url] = tmp;
  }
  return UrlHelper.aCache[url];
}

export const clearPath = (url) => {
  var tmp = parseUrl(url);
  return tmp.protocol + '//' + tmp.hostname;
};

export const getDomain = (url) => {
  let domain = 'oс.test'

  if (window.location.hostname.includes('localhost') || window.location.hostname.includes('optima.chat')) {
    domain = 'optima.chat'
  }

  return domain
};