import React, { useState, useEffect, useRef, Fragment } from 'react';
import cx from 'classnames'
import AutosizeInput from 'react-input-autosize';

const ElemMenuTitle = ({
  currentElem,
  onEditTitle,
  onChangeTitle,
  error
}) => {
  // const [title, setTitle] = useState(currentElem.data ? currentElem.data.text : '');
  const [name, changeName] = useState(currentElem.data ? currentElem.data.text : '');
  const [focus, changeFocus] = useState(false);

  const inputRef = useRef(null);

  if (error) {
    return <h1>{error}</h1>
  }

  const handleKeyDown = (e) => {
    if (e.key === 'Enter' || e.key === 'Escape') {
      inputRef.current.blur()
      changeFocus(false)
    }
  };

  // useEffect(() => {
  //   if (!currentElem.data.edit) {
  //     setTitle(currentElem.data.text)
  //   }
  // }, [currentElem.data.edit, currentElem.data.text]);

  if (currentElem.type === 'input') {
    return null
  }

  return (
    <div className={
      cx(
        'constructor-title constructor-title_elemmenu',
        {
          'constructor-title_focus': focus
        }
      )
    }>
      <AutosizeInput
        ref={inputRef}
        type="text"
        className="constructor-title__input"
        value={name}
        onChange={(e) => {
          if (e.target.value.length < 35) {
            changeName(e.target.value)
          }
        }}
        onFocus={(e) => {
          changeFocus(true)
          e.target.select()
        }}
        onBlur={(e) => {
          if (e.target.value.length === 0) {
            changeName(currentElem.data.text)
          }
          changeFocus(false)
        }}
        onKeyDown={handleKeyDown}
      />
      <div className="constructor-title__tooltip">Переименовать</div>
    </div>
  )

  // return (
  //   <div className={
  //     cx(
  //       'constructor-elemmenu__title',
  //       `constructor-elemmenu__title_${currentElem.type}`,
  //     )
  //   }>
  //     {currentElem.data.edit
  //       ? <Fragment>  <input className="constructor-elemmenu__title-edit" type="text"
  //         value={title} onChange={(e) => setTitle(e.target.value)} />
  //         <div className="constructor-elemmenu__title-submit"
  //           onClick={() => {
  //             if (title.trim().length) {
  //               onChangeTitle(title)
  //             } else {
  //               setTitle(currentElem.data.text)
  //             }

  //             onEditTitle(false)
  //           }}
  //         />
  //         <div className="constructor-elemmenu__title-clear"
  //           onClick={() => {
  //             setTitle(currentElem.data.text)
  //             onEditTitle(false)
  //           }}
  //         />
  //       </Fragment>
  //       : <div className="constructor-elemmenu__title-text"
  //         onClick={() => onEditTitle(true)}>{currentElem.data.text}</div>
  //     }
  //   </div>
  // )
};

export default ElemMenuTitle;