import React from 'react';

import './PageHeader.scss';
import Button from '../Button'

export default function PageHeader({ title, action, link, onClick }) {
  const renderActions = () => {
    if (!action) {
      return null
    }

    return (
      <Button onClick={onClick}>
        {action}
      </Button>
    )
  }

  const renderLinks = () => {
    if (!link) {
      return null
    }

    return (
      <Button href={link}>Оплатить</Button>
    )
  }

  return (
    <header className='page-header'>
      <div className='page-header-title'>
        {title}
      </div>

      <div className='page-header-actions'>
        {renderActions()}
        {renderLinks()}
      </div>
    </header>
  );
}
