import React, { Fragment, Component } from 'react'
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom'
import cx from 'classnames'

import PageLayout from '../components/common/PageLayout';

import SentMailingsTable from '../components/common/SentMailingsTable';
import PlannedMailingsTable from '../components/common/PlannedMailingsTable';
import DraftMailingsTable from '../components/common/DraftMailingsTable';
import TrashMailingsTable from '../components/common/TrashMailingsTable';

import { settingsActions } from '../actions/index'

import getCookie from '../const/getCookie'

class MailingsController extends Component {
  state = {
    currentTab: 'sent'
  }

  componentDidMount() {
    const { getBasicData } = this.props
    // getBasicData()
  }

  showTabsHeader = () => {
    const tabs = [
      { label: 'Отправленные', value: 'sent' },
      { label: 'Запланированные', value: 'planned' },
      { label: 'Черновики', value: 'drafts' },
      { label: 'Удаленные', value: 'trash' },
    ]

    return (
      <div className='tabs'>
        {tabs.map((tab, i) => {
          return (
            <div
              key={i}
              className={
                cx(
                  'tabs-item',
                  {
                    'tabs-item_active': tab.value === this.state.currentTab
                  })
              }
              onClick={() => this.setState({ currentTab: tab.value })}
            >{tab.label}</div>
          )
        })}
      </div>
    )
  }

  showTabsContent = () => {
    const { currentTab } = this.state

    switch (currentTab) {
      case 'sent':
        return <div className='tabs-content'>
          <SentMailingsTable
            items={[
              {
                name: 'Рассылка от 22.06.2020',
                sentAt: '22.08.2020',
                createdAt: '22.06.2020',
                dispatched: 100,
                viewed: 1,
                clicks: 1,
              }
            ]}
          />
        </div>

      case 'planned':
        return <div className='tabs-content'>
          <PlannedMailingsTable
            items={[
              {
                name: 'Рассылка от 22.06.2020',
                scheduledOn: '22.08.2020',
                createdAt: '22.06.2020',
                dispatched: 100,
                viewed: 0,
                clicks: 0,
              }
            ]}
          />
        </div>

      case 'drafts':
        return <div className='tabs-content'>
          <DraftMailingsTable
            items={[
              {
                name: 'Рассылка от 22.06.2020',
                createdAt: '22.06.2020',
              }
            ]}
          />
        </div>

      case 'trash':
        return <div className='tabs-content'>
          <TrashMailingsTable
            items={[
              {
                name: 'Рассылка от 22.06.2020',
                createdAt: '22.06.2020',
                deletedAt: '22.09.2020',
              }
            ]}
          />
        </div>

      default:
        return null
    };
  }

  render() {
    const { settings } = this.props

    return (
      <PageLayout
        title="Рассылки"
      >
        {this.showTabsHeader()}
        {this.showTabsContent()}
      </PageLayout>
    );
  }
}

const mapStateToProps = (state) => {
  const { settings, loading, error } = state

  return {
    settings,
    loading,
    error,
  };
};

const mapDispatchToProps = {
  getBasicData: settingsActions.basic.request,
};

export default connect(mapStateToProps, mapDispatchToProps)(MailingsController)