import React, { useState, useEffect } from 'react';

import ReactFlow, { isEdge, Controls, useStoreState } from 'react-flow-renderer';

import StartNode from './StartNode';
import MessageNode from './MessageNode';
import ActionNode from './ActionNode';
import ConditionNode from './ConditionNode';
import DelayNode from './DelayNode';

const connectionLineStyle = { stroke: '#2E2E2E' };

const NodesDebugger = () => {
  // const state = useStoreState(state => state);
  return null;
}


const NodeFlow = ({
  flowType,
  elems,
  fields,
  currentElem,
  onSelectElem,
  onCopyElem,
  onRemoveElem,
  onLinkElem,
  onUnlinkElem,
  onCoordsElem,
  onSelectBtn,
  openFloatMenu,
  onOpenFloatMenu,
  onCloseFloatMenu
}) => {
  const [connectCls, setConnectCls] = useState('');
  useEffect(() => {
    const links = elems.filter(elem => elem.type.includes('linkNode'))

    if (links) {
      links.forEach(link => {
        const source = document.querySelector(`[data-nodeid=${link.source.slice(0, -8)}]`)

        if (source) {
          if (source.closest('.react-flow__node-messageNode')) {
            setTimeout(() => {
              if (document.querySelector(`.${link.id}`)) {
                document.querySelector(`.${link.id}`).classList.add('messageLink')
              }
            }, 100);
          }
          if (source.closest('.react-flow__node-actionNode')) {
            setTimeout(() => {
              if (document.querySelector(`.${link.id}`)) {
                document.querySelector(`.${link.id}`).classList.add('actionLink')
              }
            }, 100);
          }
          if (source.closest('.react-flow__node-conditionNode')) {
            setTimeout(() => {
              if (document.querySelector(`.${link.id}`)) {
                document.querySelector(`.${link.id}`).classList.add('conditionLink')
              }
            }, 100);
          }
          if (source.closest('.react-flow__node-delayNode')) {
            setTimeout(() => {
              if (document.querySelector(`.${link.id}`)) {
                document.querySelector(`.${link.id}`).classList.add('delayLink')
              }
            }, 100);
          }
        }
      });
    }
  }, [elems]);

  const nodeTypes = {
    input: (props) => {
      return <StartNode
        {...props}
      />
    },
    messageNode: (props) => {
      return <MessageNode
        {...props}
        fields={fields}
        flowType={flowType}
        isActive={currentElem.id === props.id}
        onCopyElem={onCopyElem}
        onRemoveElem={onRemoveElem}
        onUnlinkElem={onUnlinkElem}
      />
    },
    actionNode: (props) => {
      return <ActionNode
        {...props}
        flowType={flowType}
        isActive={currentElem.id === props.id}
        onCopyElem={onCopyElem}
        onRemoveElem={onRemoveElem}
        onUnlinkElem={onUnlinkElem}
      />
    },
    conditionNode: (props) => {
      return <ConditionNode
        {...props}
        isActive={currentElem.id === props.id}
        onCopyElem={onCopyElem}
        onRemoveElem={onRemoveElem}
        onUnlinkElem={onUnlinkElem}
      />
    },
    delayNode: (props) => {
      return <DelayNode
        {...props}
        isActive={currentElem.id === props.id}
        onCopyElem={onCopyElem}
        onRemoveElem={onRemoveElem}
        onUnlinkElem={onUnlinkElem}
      />
    }
  };

  const onPaneClick = (e) => {
    onSelectElem({})
    onCloseFloatMenu()
    onRemoveNodesActiveClass()
  }

  const onFitView = (e) => {
    onSelectElem({})
    onCloseFloatMenu()
    onRemoveNodesActiveClass()
  }

  const onElementClick = (event, elem) => {
    if (isEdge(elem)) {
      onRemoveElem([elem])
      if (!elem.id.includes('btn_link')) {
        onUnlinkElem(`${elem.source}`, elem.target)
      }
    }
    else if (elem.type === 'input') {
      onSelectElem({})
      onCloseFloatMenu()
      onRemoveNodesActiveClass()
    } else {
      if (event.target.className.includes('flow-block__placeholder_message')
        || event.target.className.includes('flow-block__message')
        || event.target.className.includes('flow-block__placeholder_question')
        || event.target.className.includes('flow-block__question')
      ) {
        let msgId = ''

        if (event.target.closest('.flow-block__message')) {
          msgId = event.target.closest('.flow-block__message').id
        }

        if (event.target.closest('.flow-block__question')) {
          msgId = event.target.closest('.flow-block__question').id
        }

        onSelectElem({
          ...elem, data: { ...elem.data, edit: false }, msgId
        })
      } else if (event.target.className.includes('flow-block__button')) {
        const { id } = event.target.dataset
        let btn
        elem.data.items.forEach(item => {
          if (item.buttons) {
            btn = item.buttons.find(b => b.id === id)
          }
        })
        onSelectElem({ ...elem, data: { ...elem.data, edit: false } })
        onSelectBtn(btn)
        onOpenFloatMenu(
          document.querySelector(`#${btn.id}`).getBoundingClientRect().top,
          document.querySelector('.constructor-elemmenu').getBoundingClientRect().left,
          'auto',
          'buttonNextStep'
        )
      } else {
        onSelectElem({ ...elem, data: { ...elem.data, edit: false } })

        const links = elems.filter(el => el.type.includes('linkNode'))

        if (links) {
          onRemoveNodesActiveClass()

          links.forEach(link => {
            if (link.source.slice(0, -8) === elem.id && document.querySelector(`.${link.id}`)) {
              document.querySelector(`.${link.id}`).classList.add('active')
            }
          })
        }

        if (event.target.className.includes('flow-block__placeholder')) {
          switch (elem.type) {
            case 'messageNode':
              openFloatMenu('addMessage')
              break
            case 'actionNode':
              openFloatMenu('addAction')
              break
            case 'conditionNode':
              openFloatMenu('addCondition')
              break
            default:
              break
          }
        }
      }
    }
  }

  const onElementsRemove = (elems) => {
    onRemoveElem(elems.filter(item => item.type !== 'input'))
  }

  const onConnect = (props) => {
    const { source, target } = props
    if ((source.includes('__output')
      || source.includes('__btn')
      || source.includes('__if')
      || source.includes('__else'))
      && source !== target
      && source.slice(0, -4) !== target
      && source.slice(0, -6) !== target
      && source.slice(0, -8) !== target
      && !source.includes(target)
      && !target.includes('__output')
    ) {
      onLinkElem(source, target, source.includes('__btn'))
    }
  }

  const onRemoveNodesActiveClass = () => {
    const elems = document.querySelectorAll(".react-flow__edge-linkNode");

    [].forEach.call(elems, function (el) {
      el.classList.remove("active");
    });
  }

  return (
    <ReactFlow
      className={connectCls}
      elements={elems}
      onPaneClick={onPaneClick}
      onElementClick={onElementClick}
      onElementsRemove={onElementsRemove}
      onConnect={onConnect}
      onConnectStart={(event, node) => {
        setConnectCls('connection')
      }}
      onConnectStop={(event, node) => {
        setConnectCls('')
      }}
      onNodeDragStop={(event, node) => {
        onSelectElem({})
        onCloseFloatMenu()
        onCoordsElem(node)
      }}
      nodeTypes={nodeTypes}
      connectionLineStyle={connectionLineStyle}
    >
      <Controls
        onFitView={onFitView}
      />
      <NodesDebugger />
    </ReactFlow>
  );
};

export default NodeFlow;
