import React, { useState, useEffect, Fragment } from 'react';
import OutsideClickHandler from 'react-outside-click-handler';
import cx from 'classnames'

import transliterate from '../../../const/transliterate'

import './Select.scss';

const Select = ({
  placeholder,
  options,
  activeOption,
  isNewField,
  modifier,
  update,
  onCreateField,
  onChange,
}) => {
  const [open, toggleOpen] = useState(false);
  const [title, setTitle] = useState(placeholder);
  const [activeOpt, setActiveOpt] = useState(activeOption);
  const [newField, toggleNewField] = useState(false);
  const [newFieldType, toggleNewFieldType] = useState(false);
  const [newFieldTitle, setNewFieldTitle] = useState('');
  const [activeNewFieldOpt, setActiveNewFieldOpt] = useState({ value: null, label: 'Тип поля' });
  const [error, setError] = useState({});

  const newFieldTypeOptions = [
    { value: 'string', label: 'Строка' },
    { value: 'number', label: 'Число' },
  ]

  useEffect(() => {
    if (update) {
      setActiveOpt(activeOption)
    }
  }, [activeOption, update]);

  return (
    <OutsideClickHandler
      onOutsideClick={() => {
        toggleOpen(false)
      }}
    >
      <div className={
        cx(
          'select',
          `select_${modifier}`
        )
      }>
        <div className={
          cx(
            'select-title',
            { 'select-title_open': open }
          )
        }
          onClick={() => {
            toggleOpen(!open)
            toggleNewField(false)
          }}
        >{activeOpt ? activeOpt.label : title}</div>
        {open ? <div className="select-list">
          {options.map((item, i) => {
            return (
              <div
                key={i}
                className="select-item"
                onClick={() => {
                  setActiveOpt(item);
                  toggleOpen(false)
                  setTitle(item.label)
                  onChange(item)
                }}
              >{item.label}</div>
            )
          })}
          {isNewField
            ? <Fragment>
              <div className="select-create"
                onClick={() => toggleNewField(!newField)}
              >{newField ? 'создание поля' : '+ создать новое поле'}</div>

              {newField ? <div className="select-form">
                <div className="constructor-elemmenu__row constructor-elemmenu__row_bottom">
                  <div className="select-info">?
                  <div className="select-tooltip">Информация об имени поля</div>
                  </div>
                  <input
                    type="text"
                    className={
                      cx(
                        'constructor-elemmenu__input',
                        { 'constructor-elemmenu__input_error': error.newFieldTitle }
                      )
                    }

                    placeholder='Название поля'
                    value={newFieldTitle}
                    onChange={(e) => {
                      setError({ ...error, newFieldTitle: false })
                      setNewFieldTitle(e.target.value)
                    }}
                  />
                </div>
                <div className="constructor-elemmenu__row constructor-elemmenu__row_bottom">
                  <div className="select-info">?
                  <div className="select-tooltip">Информация о типе поля</div>
                  </div>
                  <div className={
                    cx(
                      'select-title',
                      {
                        'select-title_open': newFieldType,
                        'select-title_error': error.newFieldType
                      }
                    )
                  }
                    onClick={() => toggleNewFieldType(!newFieldType)}
                  >{activeNewFieldOpt.label}</div>
                  {newFieldType
                    ? <div className="select-list select-list_newfield">
                      {newFieldTypeOptions.map((item, i) => {
                        return (
                          <div
                            key={i}
                            className="select-item"
                            onClick={() => {
                              toggleNewFieldType(false)
                              setActiveNewFieldOpt(item)
                              setError({ ...error, newFieldType: false })
                            }}
                          >{item.label}</div>
                        )
                      })}
                    </div>
                    : null
                  }
                </div>
                <div className="constructor-elemmenu__button-submit"
                  onClick={(e) => {
                    e.stopPropagation()
                    if (newFieldTitle) {
                      if (activeNewFieldOpt.value) {
                        onCreateField(newFieldTitle, transliterate(newFieldTitle), activeNewFieldOpt.value)
                        setNewFieldTitle('')
                        setActiveNewFieldOpt({ label: 'Тип поля', value: null })
                        toggleNewField(false)
                      } else {
                        setError({ ...error, newFieldType: true })
                      }

                    } else {
                      setError({ ...error, newFieldTitle: true })
                    }
                  }}
                >Подтвердить</div>
              </div>
                : null}

            </Fragment>
            : null
          }
        </div> : null}
      </div>
    </OutsideClickHandler>
  )

};

export default Select;