import React, { memo } from 'react';
import cx from 'classnames'
import { Handle } from 'react-flow-renderer';

import getNumEnding from '../../../const/getNumEnding'

export default memo(({
  id,
  data,
  isActive,
  onCopyElem,
  onUnlinkElem,
  onRemoveElem
}) => {
  const periods = {
    second: ['секунду', 'секунды', 'секунд'],
    minute: ['минуту', 'минуты', 'минут'],
    hour: ['час', 'часа', 'часов'],
    day: ['день', 'дня', 'дней'],
    week: ['неделю', 'недели', 'недель'],
  }

  const days = {
    mon: 'Пн.',
    tue: 'Вт.',
    wed: 'Ср.',
    thu: 'Чт.',
    fri: 'Пт.',
    sat: 'Сб.',
    sun: 'Вс.',
  };

  const { text, value, period, exact,
    fromTo: { fromHour, fromMinute, toHour, toMinute },
    schedule } = data

  const showSchedule = () => {
    if (!exact) {
      return null
    }

    const activeDays = Object.keys(schedule).map(day => {
      if (schedule[day]) {
        return `${days[day]},`
      }
      return null
    })

    return (
      <span>
        С {fromHour}:{fromMinute} до {toHour}:{toMinute}, в {activeDays}
      </span>
    )
  }

  return (
    <div className={cx(
      'flow-block',
      'flow-block_delay',
      {
        'flow-block_active': isActive
      }
    )}>
      <div className='flow-block__copy' onClick={() => onCopyElem(id)} />
      <div className='flow-block__delete' onClick={() => onRemoveElem(id)} />
      <div className='flow-block__title flow-block__title_delay'>{text}</div>
      <Handle
        type="target"
        position="left"
        style={{ background: '#fff', top: 20, border: '1px solid #6336E9' }}
        onConnect={({ source, target }) => onUnlinkElem(source, target)}
      />
      <div className='flow-block__delay'>
        Ожидать {value} {getNumEnding(value, periods[period.value ? period.value : period])} а затем продолжить &nbsp;
        {showSchedule()}
      </div>
      <div className="flow-block__nextstep">
        Следующий шаг
      <Handle type="source" position="right" id="output"
          style={{ bottom: 7, top: 'auto', border: '1px solid #6336E9' }} />
      </div>
    </div>
  );
});
