import React, { Component } from 'react'
import { connect } from 'react-redux';

import { constructorBotActions } from '../../actions/index'

import ElemMenu from '../../components/constructor/ElemMenu'

class ElemMenuBotController extends Component {

  handleFloatMenu = (e, floatMenuType) => {
    const { openFloatMenu } = this.props
    openFloatMenu(
      e.target.getBoundingClientRect().top,
      e.target.closest('.constructor-elemmenu').getBoundingClientRect().left,
      'auto',
      floatMenuType
    )
  }

  handleFileChange = (e) => {
    const { uploadFile, closeFloatMenu } = this.props
    const files = e.target.files

    uploadFile(files)
    closeFloatMenu()
  };

  handleImgChange = (imageList) => {
    const { uploadImage, closeFloatMenu } = this.props
    uploadImage(imageList)
    closeFloatMenu()
  };

  render() {
    const {
      constructorBot: {
        elems,
        tags,
        fields,
        currentElem,
        currentBtn,
        currentItem,
        floatMenu
      },
      editTitle,
      changeTitle,
      renameBtn,
      linkElem,
      addLinkElem,
      unlinkElem,
      addUrl,
      removeUrl,
      addMessage,
      addQuestion,
      addAnswer,
      addAction,
      addCondition,
      setTemplate,
      createField,
      closeFloatMenu
    } = this.props

    if (!currentElem.type) {
      return null
    }

    return (
      <ElemMenu
        flowType='bot'
        elems={elems}
        tags={tags}
        fields={fields}
        currentElem={currentElem}
        currentBtn={currentBtn}
        currentItem={currentItem}
        floatMenu={floatMenu.type}
        top={floatMenu.top}
        left={floatMenu.left}
        bottom={floatMenu.bottom}
        onOpenFloatMenu={this.handleFloatMenu}
        onCloseFloatMenu={closeFloatMenu}
        onEditTitle={editTitle}
        onChangeTitle={changeTitle}
        onLinkElem={linkElem}
        onAddLinkElem={addLinkElem}
        onUnlinkElem={unlinkElem}
        onChangeFile={this.handleFileChange}
        onChangeImg={this.handleImgChange}
        onAddUrl={addUrl}
        onRemoveUrl={removeUrl}
        onAddMessage={addMessage}
        onAddQuestion={addQuestion}
        onAddAnswer={addAnswer}
        onRenameBtn={renameBtn}
        onAddAction={addAction}
        onAddCondition={addCondition}
        onSetTemplate={setTemplate}
        onCreateField={createField}
      />
    )
  }
}

const mapStateToProps = (state) => {
  const { constructorBot, loading, error } = state

  return {
    constructorBot,
    loading,
    error
  };
};

const mapDispatchToProps = {
  changeTitle: constructorBotActions.title.change,
  editTitle: constructorBotActions.title.edit,
  openFloatMenu: constructorBotActions.floatmenu.open,
  closeFloatMenu: constructorBotActions.floatmenu.close,
  renameBtn: constructorBotActions.button.rename,
  linkElem: constructorBotActions.elems.link,
  addLinkElem: constructorBotActions.elems.addlink,
  unlinkElem: constructorBotActions.elems.unlink,
  addUrl: constructorBotActions.url.add,
  removeUrl: constructorBotActions.url.remove,
  addMessage: constructorBotActions.message.add,
  addQuestion: constructorBotActions.question.add,
  uploadImage: constructorBotActions.image.upload.request,
  uploadFile: constructorBotActions.file.upload.request,
  addAnswer: constructorBotActions.answer.add,
  addAction: constructorBotActions.act.add,
  addCondition: constructorBotActions.condition.item.add,
  setTemplate: constructorBotActions.template.set,
  createField: constructorBotActions.fields.create,
};

export default connect(mapStateToProps, mapDispatchToProps)(ElemMenuBotController)