import React, { useState } from 'react';

import DataTable from '../DataTable';
// import BotInfo from '../BotInfo';
import Icon from '../Icon';
import Switch from '../Switch';
import KeywordsColumn from './modules/KeywordsColumn';

import './KeywordsTable.scss';

export default function KeywordsTable({ keywords, bots, onSetCurrentElem, onToggleState, onOpenModal }) {
  return (
    <DataTable>
      <DataTable.HeadRow>
        <DataTable.HeadCell type="switch" />
        <DataTable.HeadGroup>
          <DataTable.HeadCell className='column'>
            Ключевые слова
        </DataTable.HeadCell>
          <DataTable.HeadCell className='column'>
            Бот
        </DataTable.HeadCell>
          <DataTable.HeadCell type="actions" />
        </DataTable.HeadGroup>
      </DataTable.HeadRow>

      <DataTable.Body>
        {keywords.map((item, index) => (
          <DataTable.Row
            key={index}
            isDisabled={!item.enabled}
          >
            <Switch
              enabled={item.enabled}
              onChange={(value) => {
                onToggleState({ ...item, enabled: value })
              }} />
            <DataTable.Group>
              <DataTable.Cell className='column'>
                <KeywordsColumn
                  keywords={item.keywords}
                />
              </DataTable.Cell>
              <DataTable.Cell className='column'>
                {item.actions && item.actions.length ? <div className='bot'>
                  {item.actions.map((item, i) => {
                    return `${bots.find(bot => bot.referral === item.value).title}`
                  })}
                  &nbsp;
                  &nbsp;
                  <Icon
                    name={item.enabled ? 'play' : 'pause'}
                    width={12}
                    className='bot-state'
                  />
                </div> : null}
              </DataTable.Cell>
              <DataTable.Cell type="actions">
                <DataTable.Actions
                  actions={[
                    {
                      type: 'edit',
                      handler: () => {
                        onSetCurrentElem({ keywordsId: item.id })
                        onOpenModal('editKeywords')
                      }
                    },
                    {
                      type: 'remove',
                      handler: () => {
                        onSetCurrentElem({ keywordsId: item.id })
                        onOpenModal('removeKeywords')
                      }
                    },
                  ]}
                />
              </DataTable.Cell>
            </DataTable.Group>

          </DataTable.Row>
        ))}
      </DataTable.Body>
    </DataTable>
  );
}
