import React, { createRef, useRef, useState } from 'react';

import DataTable from '../DataTable';
import ReadableDate from '../ReadableDate';
import Percentage from '../Percentage';
import Icon from '../Icon';

import styles from './PlannedMailingsTable.module.scss';

export default function PlannedMailingsTable({ items = [], onDelete, ...rest }) {
  return (
    <DataTable
      {...rest}
    >
      <DataTable.HeadRow>
        <DataTable.Cell className={styles['mailing-icon-column']} />
        <DataTable.HeadCell className={styles['mailing-name-column']}>
          Название
        </DataTable.HeadCell>
        <DataTable.HeadCell className={styles['mailing-date-column']}>
          Запланирована отправка
        </DataTable.HeadCell>
        <DataTable.HeadCell className={styles['mailing-date-column']}>
          Дата создания
        </DataTable.HeadCell>
        <DataTable.HeadCell className={styles['mailing-count-column']}>
          Отправлено
        </DataTable.HeadCell>
        <DataTable.HeadCell className={styles['mailing-count-column']}>
          Просмотрено
        </DataTable.HeadCell>
        <DataTable.HeadCell className={styles['mailing-clicks-column']}>
          Кликов
        </DataTable.HeadCell>
        <DataTable.HeadCell type="actions" />
      </DataTable.HeadRow>

      <DataTable.Body>
        {(() => {
          if (!items.length) {
            return (
              <DataTable.Placeholder>
                Нет ни одной рассылки
              </DataTable.Placeholder>
            )
          }

          return items.map((item, index) => (
            <DataTable.Row
              key={index}
              className={styles['table-row']}
            >
              <DataTable.Cell className={styles['mailing-icon-column']}>
                <Icon name="schedule" width={20} />
              </DataTable.Cell>
              <DataTable.Cell className={styles['mailing-name-column']}>
                {item.name}
              </DataTable.Cell>
              <DataTable.Cell className={styles['mailing-date-column']}>
                <ReadableDate date={item.scheduledOn} />
              </DataTable.Cell>
              <DataTable.Cell className={styles['mailing-date-column']}>
                <ReadableDate date={item.createdAt} />
              </DataTable.Cell>
              <DataTable.Cell className={styles['mailing-count-column']}>
                {item.dispatched}
              </DataTable.Cell>
              <DataTable.Cell className={styles['mailing-count-column']}>
                <Percentage
                  value={item.viewed}
                  from={item.dispatched}
                />
              </DataTable.Cell>
              <DataTable.Cell className={styles['mailing-clicks-column']}>
                <Percentage
                  value={item.clicks}
                  from={item.dispatched}
                />
              </DataTable.Cell>
              <DataTable.Cell type="actions">
                <DataTable.Actions
                  actions={[
                    {
                      type: 'copy',
                      handler: () => console.log('copy')
                    },
                    {
                      type: 'remove',
                      handler: () => console.log('remove')
                    },
                  ]}
                />
              </DataTable.Cell>
            </DataTable.Row>
          ))
        })()}
      </DataTable.Body>
    </DataTable>
  );
}
