import React from 'react';

import Button from '../../common/Button'
import Modal from '../Modal'

const RemoveSubscribers = ({ onCloseModal, onOpenModal, onRemoveSubscribers, onSetCurrentElem }) => {
  return (
    <Modal
      title='Вы действительно хотите удалить этих подписчиков?'
      onClose={onCloseModal}
    >
      <div className="modal-content">
        Внимание! Все данные профилей и переписки будут удалены без возможности восстановления
      </div>
      <div className="modal-footer" >
        <div className="modal-buttons">
          <Button modifier='cancel' onClick={() => {
            onSetCurrentElem({})
            onCloseModal()
          }}>Отмена</Button>
          <Button modifier='remove' onClick={() => {
            onRemoveSubscribers()
            onSetCurrentElem({})
            onCloseModal()
          }}>Удалить подписчиков</Button>
        </div>
      </div>
    </Modal>
  );
};

export default RemoveSubscribers;