import { createActions } from 'redux-actions';

export default createActions({
  BASIC: {
    REQUEST: null,
    SUCCESS: (data) => (data),
    FAILURE: null
  },
  SOCKET: {
    LOADING: {
      ADD: (loadArr) => (loadArr),
      REMOVE: (msg) => (msg),
    },
    RECEIVE: (msg) => (msg),
  },
  CURRENTELEM: {
    SET: (options) => (options),
  },
  CHANNEL: {
    ADD: (token, type, options) => ({ token, type, options }),
    REMOVE: (connectorId, type) => ({ connectorId, type })
  },
  BOT: {
    ADD: (title, type, options) => ({ title, type, options }),
    REMOVE: (botId, type) => ({ botId, type }),
    TOGGLE: (bot) => (bot),
    SAVE: null
  },
  KEYWORDS: {
    ADD: (keywords, activeBot, keywordsId) => ({ keywords, activeBot, keywordsId }),
    REMOVE: (keywordsId, type) => ({ keywordsId, type }),
    TOGGLE: (keywords) => (keywords)
  },
  AUDIENCE: {
    SET: (subscribersIds) => (subscribersIds),
    REMOVE: null,
    TAG: {
      ADD: (chats, tags) => ({ chats, tags }),
      REMOVE: (chats, tags) => ({ chats, tags }),
    },
    FILTERS: {
      SET: (filters) => (filters),
    }
  },
  AUTOMAILING: {
    ADD: (title) => (title),
    REMOVE: (automailingId) => (automailingId),
  },
});