import React, { Fragment } from 'react';

import ElemMenuMessageItem from './ElemMenuMessageItem'
import ElemMenuQuestionItem from './ElemMenuQuestionItem'
import ElemMenuImageItem from './ElemMenuImageItem'
import ElemMenuFileItem from './ElemMenuFileItem'

const ElemMenuMessages = ({
  fields,
  currentElem,
  onUnlinkElem,
  onSelectBtn,
  onAddBtn,
  onRemoveBtn,
  onSelectItem,
  onCopyItem,
  onRemoveItem,
  onMoveItem,
  onChangeItem,
  onRemoveAnswer,
  onSetTemplate,
  handleFloatMenu,
  onSelectElem
}) => {
  const showContent = () => {
    const { items } = currentElem.data
    if (!items.length) {
      return null
    }

    return items.map((item, i) => {
      switch (item.itemType) {
        case 'message':
          return (
            <ElemMenuMessageItem
              key={i}
              message={item}
              currentElem={currentElem}
              onUnlinkElem={onUnlinkElem}
              onSelectItem={onSelectItem}
              onCopyItem={onCopyItem}
              onRemoveItem={onRemoveItem}
              onMoveItem={onMoveItem}
              onChangeItem={onChangeItem}
              onSelectBtn={onSelectBtn}
              onAddBtn={onAddBtn}
              onRemoveBtn={onRemoveBtn}
              onSetTemplate={onSetTemplate}
              onSelectElem={onSelectElem}
              onOpenFloatMenu={handleFloatMenu}
            />
          )

        case 'question':
          return <ElemMenuQuestionItem
            key={i}
            question={item}
            fields={fields}
            currentElem={currentElem}
            onSelectItem={onSelectItem}
            onCopyItem={onCopyItem}
            onRemoveItem={onRemoveItem}
            onMoveItem={onMoveItem}
            onChangeItem={onChangeItem}
            onRemoveAnswer={onRemoveAnswer}
            onSetTemplate={onSetTemplate}
            onSelectElem={onSelectElem}
            onOpenFloatMenu={handleFloatMenu}
          />

        case 'image':
          return <ElemMenuImageItem
            key={i}
            image={item}
            currentElem={currentElem}
            onCopyItem={onCopyItem}
            onRemoveItem={onRemoveItem}
            onMoveItem={onMoveItem}
          />

        case 'file':
          return <ElemMenuFileItem
            key={i}
            file={item}
            currentElem={currentElem}
            onCopyItem={onCopyItem}
            onRemoveItem={onRemoveItem}
            onMoveItem={onMoveItem}
          />

        default:
          return null
      }
    })
  }

  return (
    <Fragment>
      {showContent()}
      <div
        className="constructor-elemmenu__add constructor-elemmenu__add_message"
        onClick={(e) => handleFloatMenu(e, 'addMessage')}
      >+&nbsp;{'Добавить контент'}</div>
    </Fragment>
  )
};

export default ElemMenuMessages;