import React, { Fragment, Component } from 'react'
import { connect } from 'react-redux';
class StatsController extends Component {

  render() {
    return (
      <Fragment>
        <h1>Раздел находится в разработке</h1>
      </Fragment>
    )
  }
}

const mapStateToProps = (state) => {
  const { settings, loading, error } = state

  return {
    settings,
    loading,
    error,
  };
};

export default connect(mapStateToProps)(StatsController)