import React, { useState, Fragment } from 'react';
import cx from 'classnames'

import ElemMenuConditionItem from './ElemMenuConditionItem'
import ElemMenuNextStepMatch from './ElemMenuNextStepMatch'

const ElemMenuCondition = ({
  elems,
  currentElem,
  floatMenu,
  onLinkElem,
  onUnlinkElem,
  onRemoveConditionItem,
  onChangeConditionForm,
  handleFloatMenu,
  onCloseFloatMenu
}) => {
  const [popup, togglePopup] = useState(false);

  const showConditionForm = () => {
    return (
      <div className='constructor-elemmenu__fit'>Подписчик соответствует&nbsp;
        <div className={
          cx(
            'constructor-elemmenu__popup',
            { 'constructor-elemmenu__popup_active': popup }
          )
        }
        >
          <div className={
            cx(
              'constructor-elemmenu__popup-item',
              { 'constructor-elemmenu__popup-item_active': currentElem.form === 'all' }
            )
          }
            onClick={() => {
              onChangeConditionForm('all')
              togglePopup(false)
            }}
          >всем из следующих условий</div>
          <div className={
            cx(
              'constructor-elemmenu__popup-item',
              { 'constructor-elemmenu__popup-item_active': currentElem.form === 'any' }
            )
          }
            onClick={() => {
              onChangeConditionForm('any')
              togglePopup(false)
            }}
          >любому из следующих условий</div>
        </div>
        <span className='constructor-elemmenu__fit-text'
          onClick={() => togglePopup(true)}
        >
          {currentElem.form === 'all' ? 'всем из следующих условий' : 'любому из следующих условий'}
        </span>
      </div>
    )
  }

  const showContent = () => {
    const { items } = currentElem.data
    if (!items.length) {
      return null
    }

    return items.map((item, i) => {
      return (
        <ElemMenuConditionItem
          key={i}
          item={item}
          onRemoveConditionItem={onRemoveConditionItem}
          onOpenFloatMenu={handleFloatMenu}
        />
      )
    })
  }

  const showNextIfStep = () => {
    if (currentElem.data.nextIfStep) {
      return (
        <div className={
          cx(
            'constructor-elemmenu__nextelem',
            `constructor-elemmenu__nextelem_${currentElem.data.nextIfStep.type.slice(0, -4)}`,
          )
        }>
          <div className="constructor-elemmenu__nextelem-close"
            onClick={() => {
              onUnlinkElem(`${currentElem.id}__if`, currentElem.data.nextIfStep.id)
            }}
          />
          <div className="constructor-elemmenu__nextelem-text">{currentElem.data.nextIfStep.data.text}</div>
        </div>
      )
    }

    return (
      <div
        className="constructor-elemmenu__nextstep"
        onClick={(e) => handleFloatMenu(e, 'nextIfStep')}
      >Указать следующий шаг</div>
    )
  }

  const showNextElseStep = () => {
    if (currentElem.data.nextElseStep) {
      return (
        <div className={
          cx(
            'constructor-elemmenu__nextelem',
            `constructor-elemmenu__nextelem_${currentElem.data.nextElseStep.type.slice(0, -4)}`,
          )
        }>
          <div className="constructor-elemmenu__nextelem-close"
            onClick={() => {
              onUnlinkElem(`${currentElem.id}__else`, currentElem.data.nextElseStep.id)
            }}
          />
          <div className="constructor-elemmenu__nextelem-text">{currentElem.data.nextElseStep.data.text}</div>
        </div>
      )
    }

    return (
      <div
        className="constructor-elemmenu__nextstep"
        onClick={(e) => handleFloatMenu(e, 'nextElseStep')}
      >Указать следующий шаг</div>
    )
  }

  return (
    <Fragment>
      {showConditionForm()}
      {showContent()}
      <div
        className="constructor-elemmenu__add constructor-elemmenu__add_condition"
        onClick={(e) => handleFloatMenu(e, 'addCondition')}
      >+&nbsp;{'Добавить условие'}</div>
      <div className="constructor-elemmenu__match">Если соответствует</div>
      {showNextIfStep()}

      <ElemMenuNextStepMatch
        elems={elems}
        currentElem={currentElem}
        floatMenu={floatMenu.type}
        top={floatMenu.top}
        left={floatMenu.left}
        bottom={floatMenu.bottom}
        onLinkElem={onLinkElem}
        onClose={onCloseFloatMenu}
      />
      <div className="constructor-elemmenu__mismatch">Если не соответствует</div>
      {showNextElseStep()}
    </Fragment>
  )
};

export default ElemMenuCondition;