import React, { useState, Fragment } from 'react';
import OutsideClickHandler from 'react-outside-click-handler';
import cx from 'classnames'
import fuzzysort from 'fuzzysort'
import { v4 as uuidv4 } from 'uuid';

import Button from '../Button'

import './Menu.scss'

const Menu = ({
  options,
  placeholder,
  activeOption,
  onSubmit
}) => {
  const [open, toggleOpen] = useState(false);
  const [title, setTitle] = useState(placeholder);
  const [wrap, setWrap] = useState('');
  const [fieldList, toggleFieldList] = useState(false);
  const [searchValue, setSearchValue] = useState('');
  const [tags, setTag] = useState([]);
  let filteredOptions = options

  if (searchValue.length) {
    const data = fuzzysort.go(searchValue, options, {
      keys: ['label']
    })

    filteredOptions = data.map(i => i.obj)
  }

  const addTag = (text) => {
    setTag([...tags, { id: `tag_${uuidv4()}`, text }])
  }

  const removeTag = (id) => {
    setTag(tags.filter(tag => tag.id !== id))
  }

  const showCreateMenu = () => {
    if (wrap === 'addtag') {
      return (
        <div className="menu__choice-create"
          onClick={() => {
            addTag(searchValue)
            setSearchValue('')
            toggleFieldList(false)
          }}
        >+&nbsp;Создать метку {searchValue}</div>
      )
    }

    return (
      <div className="menu__choice-create">Метка не найдена</div>
    )
  }

  return (
    <OutsideClickHandler
      onOutsideClick={() => {
        toggleOpen(false)
        setWrap('')
      }}
    >
      {wrap
        ? <div className='menu-wrap'>
          <div className={`menu-wrap__title menu-wrap__title_${wrap}`}>{title}</div>
          <OutsideClickHandler
            onOutsideClick={() => {
              toggleFieldList(false)
            }}
          >
            <div className="menu__choice-select">
              <input
                type="text"
                placeholder='Введите название метки'
                className={
                  cx(
                    'menu__input',
                    { 'menu__input_active': fieldList }
                  )
                }
                onFocus={() => toggleFieldList(true)}
                value={searchValue}
                onChange={(e) => setSearchValue(e.target.value)}
              />
              {fieldList
                ? <div className="menu__choice-list">
                  {filteredOptions.length
                    ? filteredOptions.map((field, i) => {
                      return (
                        <div key={i} className="menu__choice-item"
                          onClick={(e) => {
                            addTag(field.label)
                            toggleFieldList(!fieldList)
                            setSearchValue('')
                          }}
                        >{field.label}</div>
                      )
                    })
                    : showCreateMenu()
                  }
                </div>
                : null}
            </div>
          </OutsideClickHandler>
          <div className="menu-tags">
            {tags.map((tag) => {
              return (
                <div className={
                  cx(
                    'constructor-elemmenu__tag',
                  )
                } key={tag.id}>
                  #<span className='constructor-elemmenu__tag-txt'>{tag.text}</span>
                  <span className='constructor-elemmenu__tag-remove'
                    onClick={(e) => {
                      removeTag(tag.id)
                    }}
                  >×</span>
                </div>
              )
            })}
          </div>
          <div className="menu-buttons">
            <Button
              className="button"
              modifier='cancel'
              onClick={() => {
                toggleOpen(false)
                setTag([])
                setWrap('')
              }}
            >Отмена</Button>
            <Button
              className="button"
              onClick={() => {
                toggleOpen(false)
                onSubmit(tags, wrap)
                setTag([])
                setWrap('')
              }}
            >Подтвердить</Button>
          </div>
        </div>
        : <div className='menu'>
          <div className={
            cx(
              'menu-title',
              { 'menu-title_open': open }
            )
          }
            onClick={() => {
              toggleOpen(!open)
            }}
          >{activeOption ? activeOption.label : title}</div>
          {open ? <div className="menu-list">
            <div
              className="menu-item"
              onClick={() => {
                toggleOpen(false)
                setWrap('addtag')
                setTitle('Добавить метку')
              }}
            >Добавить метку</div>
            <div
              className="menu-item"
              onClick={() => {
                toggleOpen(false)
                setWrap('removetag')
                setTitle('Удалить метку')
              }}
            >Удалить метку</div>
          </div> : null}
        </div>
      }

    </OutsideClickHandler>
  )

};

export default Menu;


