import React from 'react';

import DataTable from '../DataTable';
import Percentage from '../Percentage';

import './AutoMailingsTableDetail.scss';

export default function AutoMailingsTableDetail({ items = [], onEdit, onDelete, ...rest }) {
  return (
    <DataTable
      {...rest}
    >
      <DataTable.HeadRow>
        <DataTable.HeadCell width={50} />

        <DataTable.HeadCell width={275}>
          Время отправки
      </DataTable.HeadCell>

        <DataTable.HeadGroup>
          <DataTable.HeadCell type="drag" />
          <DataTable.HeadCell className='mailing-name-column'>
            Рассылки
        </DataTable.HeadCell>
          <DataTable.HeadCell width={125}>
            Отправлено
        </DataTable.HeadCell>
          <DataTable.HeadCell width={125}>
            Просмотрено
        </DataTable.HeadCell>
          <DataTable.HeadCell width={125}>
            Кликов
        </DataTable.HeadCell>
          <DataTable.HeadCell type="actions" />
        </DataTable.HeadGroup>
      </DataTable.HeadRow>

      <DataTable.Body className='table-body'>
        {
          items.map((item, index) => {
            return (
              <DataTable.Row
                key={index}
                className='table-row'
              >
                <DataTable.Cell width={50} className='automailings-row-number'>
                  {item.id || index}
                </DataTable.Cell>

                <DataTable.Cell width={275}>
                  <div className='automailings-scheduled-at'>
                    {item.scheduledAt}
                  </div>
                </DataTable.Cell>

                <DataTable.Group className='table-group'>
                  <DataTable.Cell width={30}>
                    <DataTable.DragControl className='drag-control' />
                  </DataTable.Cell>
                  <DataTable.Cell className='mailing-name-column'>
                    {item.name}
                  </DataTable.Cell>
                  <DataTable.Cell width={120}>
                    {item.dispatched}
                  </DataTable.Cell>
                  <DataTable.Cell width={120}>
                    <Percentage
                      value={item.viewed}
                      from={item.dispatched}
                    />
                  </DataTable.Cell>
                  <DataTable.Cell width={120}>
                    <Percentage
                      value={item.clicks}
                      from={item.dispatched}
                    />
                  </DataTable.Cell>
                  <DataTable.Cell type="actions">
                    <DataTable.Actions
                      actions={[
                        {
                          type: 'edit',
                          handler: () => onEdit(item.id)
                        },
                        {
                          type: 'copy',
                          handler: () => console.log('copy')
                        },
                        {
                          type: 'remove',
                          handler: onDelete
                        },
                      ]}
                    />
                  </DataTable.Cell>
                </DataTable.Group>
              </DataTable.Row>
            )
          })
        }
      </DataTable.Body>
    </DataTable>
  )
}
