import React, { useState, useRef } from 'react';
import { CopyToClipboard } from 'react-copy-to-clipboard';

import Button from '../../common/Button'
import Modal from '../Modal'

const CopyBotLink = ({ onCloseModal }) => {
  const [value, setValue] = useState('optima.chat/bIEeZ9c9waBn8YckQNLs0X/Bot?node-id=4507%3A1277d3rfd...');
  const [copied, setCopied] = useState(false);
  const inputRef = useRef(null);

  const handleCopy = () => {
    inputRef.current.select()
    setCopied(true)
    setTimeout(() => {
      setCopied(false)
      inputRef.current.blur()
    }, 2000);
  }

  return (
    <Modal
      title='Ссылка для запуска бота?'
      onClose={onCloseModal}
    >
      <div className="modal-content modal-content_left">
        Данную ссылку вы можете вставить в кнопку на сайте, рассылку, таргетированную рекламу и т.д.
        После перехода по ней пользователь сможет начать выполнения сценария данного бота.
        <br />
        <br />
        <CopyToClipboard text={value}
          onCopy={handleCopy}>
          <input
            className='modal-input'
            type="text"
            value={value}
            ref={inputRef}
            onChange={(e) => setValue(e.target.value)}
          />
        </CopyToClipboard>
        <div style={{ height: '20px', color: '#00BAFF', fontSize: '12px' }}>
          {copied ? 'Ссылка скопирована в буфер' : ''}
        </div>
      </div>
      <div className="modal-footer" >
        <div className="modal-buttons">
          <CopyToClipboard text={value}
            onCopy={handleCopy}>
            <Button modifier='copy'>Скопировать</Button>
          </CopyToClipboard>
        </div>
      </div>
    </Modal>
  );
};

export default CopyBotLink;