import React from 'react';

import Button from '../../common/Button'
import Icon from '../../common/Icon'

import Modal from '../Modal'

import getNumEnding from '../../../const/getNumEnding'
import { buildQuery } from '../../../const/urlHelper'

export default function AddVkontakteAccount({ basic, currentElem, onCloseModal, onSetCurrentElem, onAddChannel }) {
  const { vkGroups } = currentElem

  if (!vkGroups || !vkGroups.length) {
    return null
  }

  const runOAuth = (groupId) => {
    const { vkAuthAppId, vkAuthCallback } = basic

    const vkAuthUrl = 'https://oauth.vk.com/authorize?'
      + buildQuery({
        client_id: vkAuthAppId,
        redirect_uri: `${vkAuthCallback}?mode=pageCode&groupId=${groupId}`,
        display: 'page',
        response_type: 'code',
        group_ids: groupId,
        v: '5.74',
        scope: 'manage,messages,photos,docs',
      });

    const width = 825;
    const height = 540;
    const centerPosition = { top: 0, left: 0 }
    //открываем окно в popup'е
    const popupWindow = window.open(vkAuthUrl, 'VK Auth',
      'height=' + height + ',width=' + width + (centerPosition !== null
        ? ',left=' + centerPosition.left + ',top=' + centerPosition.top
        : ''));

    setTimeout(function () {
      popupWindow.postMessage('wh-vk-oauth-result', '*')
    }, 2000);

    window.addEventListener('message', function (e) {
      if (e.data && e.data.source !== '@devtools-page') {
        popupWindow.close()
        try {
          const eventData = JSON.parse(e.data);
          if (eventData['data'].hasOwnProperty('code')) {
            const { id, name, screen_name, photo_200 } = vkGroups[0]
            onAddChannel(null, 'vkontakte', { code: eventData['data']['code'], id, name, screen_name, photo_200 })
          }
        } catch (err) {
          console.log(err, e);
        }
      }
    })
  };

  return (
    <Modal
      title='Создание нового бота Вконтакте'
      onClose={onCloseModal}
    >
      <section className='account-addition'>
        <div className='account-addition-disclaimer'>
          <Icon
            name="vkontakte"
            width={30}
            className='account-addition-type'
          />
          <span>В вашем аккаунте Вконтакте {getNumEnding(vkGroups.length, ['группа', 'группы', 'групп'])}.</span>
        </div>
        <div>
          <p className='account-addition-pages-disclaimer'>
            Выберите группу, к которой будут привязаны созданные на нашем сервисе боты:
            </p>
          <section className='account-addition-pages-list'>
            {vkGroups.map(item => {
              return (
                <div className='account-addition-pages-item' key={item.id}>
                  <img src={item.photo_50} alt="" />
                  <span>{item.name} ({item.screen_name})</span>
                  <Button
                    onClick={() => {
                      runOAuth(item.id)
                      onCloseModal()
                    }}
                  >
                    Подключить
            </Button>
                </div>
              )
            })}

          </section>
        </div>
      </section>
    </Modal>
  );
}
