import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux';
import { authActions } from '../actions/index'

import { emailPattern } from '../const/validate-patterns'
import { getDomain } from '../const/urlHelper'

import {
  Link,
} from 'react-router-dom'

class ForgetController extends Component {
  state = {
    email: '',
    emailError: '',
  }

  componentWillUnmount() {
    const { authDrop } = this.props
    authDrop()
  }

  handleSubmit = () => {
    const { authForget } = this.props
    const { email } = this.state

    if (!emailPattern.test(email)) {
      this.setState({ emailError: 'Укажите корректный email' })
      return
    }

    authForget(email)
  }

  render() {
    const { auth } = this.props
    const { email, emailError } = this.state

    if (auth.loading) {
      return <h2>Loading...</h2>
    }

    if (auth.forgetsuccess) {
      return (
        <div className="auth__block">
          <Link className="auth__back" to={`/`}>×</Link>
          <div className="auth__title">Восстановление пароля</div>
          <div className="auth__info">Письмо со ссылкой восстановления пароля было выслано на указанный эл.адрес.</div>
          <div className="auth__footer">
            <a href={`http://auth.${getDomain()}/signup`}>Зарегистрироваться</a>
            <div className="auth__delimeter">|</div>
            <Link to={`/auth`}>Войти</Link>
          </div>
        </div>
      )
    }

    return (
      <div className="auth__block">
        <Link className="auth__back" to={`/`}>×</Link>
        <div className="auth__title">Восстановление пароля</div>
        <div className="auth__info">
          Введите адрес эл.почты, который был указан при регистрации,
          на него будет отправлена ссылка для восстановления пароля.
      </div>
        <div className="auth__field">
          <input
            value={email}
            onChange={(e) => this.setState({ email: e.target.value, emailError: '' })}
            type="text"
            className="auth__input"
            placeholder='Введите email'
          />
          <div className="auth__error">{emailError}</div>
          <div className="auth__error">{auth.error}</div>
        </div>
        <div className="auth__button"
          onClick={this.handleSubmit}
        >Восстановить пароль</div>
        <div className="auth__footer">
          <a href={`http://auth.${getDomain()}/signup`}>Зарегистрироваться</a>
          <div className="auth__delimeter">|</div>
          <Link to={`/auth`}>Войти</Link>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  const { auth } = state

  return {
    auth,
  };
};

const mapDispatchToProps = {
  authForget: authActions.auth.forget.request,
  authDrop: authActions.auth.drop,
};

export default connect(mapStateToProps, mapDispatchToProps)(ForgetController)