import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux';

import ModalController from './ModalController'

import PageLayout from '../components/common/PageLayout';
import Placeholder from '../components/common/Placeholder';
import Button from '../components/common/Button'
import KeywordsTable from '../components/common/KeywordsTable';

import { settingsActions, modalActions } from '../actions/index'

class KeywordsController extends Component {
  state = {
    action: '+ Добавить ключевое слово'
  }

  showKeywords = () => {
    const { settings: { loading, data }, openModal, setCurrentElem, toggleKeywordsState } = this.props

    if (!data.keywords.length) {
      return (
        <Placeholder
          name="keywords"
          width={300}
        >
          Ключевые слова позволяют автоматически запускать неограниченное количество ботов, в случае если пользователь напишет любое из таких слов в сообщении.

          <Placeholder.Subtitle>
            Например, ваш клиент в своем сообщении напишет слово “стоит”. По этому слову будет автоматически запущен бот, рассказывающий про цену.
        </Placeholder.Subtitle>
          <Button
            onClick={() => openModal('addKeywords')}
          >
            Создать ключевое слово
        </Button>
        </Placeholder>
      )
    }

    return (
      <KeywordsTable
        keywords={data.keywords}
        bots={data.bots}
        onOpenModal={openModal}
        onSetCurrentElem={setCurrentElem}
        onToggleState={toggleKeywordsState}
      />
    )
  }

  render() {
    const { openModal } = this.props
    const { action } = this.state

    return (
      <Fragment>
        <PageLayout
          title="Ключевые слова"
          action={action}
          onClick={() => openModal('addKeywords')}
        >
          {this.showKeywords()}
        </PageLayout>
        <ModalController />
      </Fragment>

    );
  }
}

const mapStateToProps = (state) => {
  const { settings, loading, error } = state

  return {
    settings,
    loading,
    error,
  };
};

const mapDispatchToProps = {
  getBasicData: settingsActions.basic.request,
  toggleKeywordsState: settingsActions.keywords.toggle,
  setCurrentElem: settingsActions.currentelem.set,
  openModal: modalActions.modal.open,
  closeModal: modalActions.modal.close,
};

export default connect(mapStateToProps, mapDispatchToProps)(KeywordsController)