import React, { Fragment } from 'react';

const ElemMenuImageItem = ({
  image,
  onCopyItem,
  onRemoveItem,
  onMoveItem,
}) => {
  return (
    <Fragment>
      <div className="constructor-elemmenu__label">Изображение</div>
      <div className='constructor-elemmenu__item constructor-elemmenu__upload'>
        <div className="constructor-elemmenu__operations">
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_delete"
            onClick={() => onRemoveItem(image.id)}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_up"
            onClick={() => onMoveItem(image.id, 'up')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_down"
            onClick={() => onMoveItem(image.id, 'down')}
          />
        </div>
        <div className="constructor-elemmenu__upload-item constructor-elemmenu__upload-item_img">
          <img className='constructor-elemmenu__upload-img' src={image.preview_320} alt="" />
        </div>
      </div>
    </Fragment>
  )

};

export default ElemMenuImageItem;