import React from 'react';

import DataTable from '../DataTable';
import ReadableDate from '../ReadableDate';
import Percentage from '../Percentage';
import Icon from '../Icon';

import './SentMailingsTable.scss';

export default function SentMailingsTable({ items = [], onDelete, ...rest }) {
  return (
    <DataTable
      {...rest}
    >
      <DataTable.HeadRow>
        <DataTable.Cell className='mailing-icon-column' />
        <DataTable.HeadCell className='mailing-name-column'>
          Название
        </DataTable.HeadCell>
        <DataTable.HeadCell className='mailing-date-column'>
          Дата отправки
        </DataTable.HeadCell>
        <DataTable.HeadCell className='mailing-date-column'>
          Дата создания
        </DataTable.HeadCell>
        <DataTable.HeadCell className='mailing-count-column'>
          Отправлено
        </DataTable.HeadCell>
        <DataTable.HeadCell className='mailing-count-column'>
          Просмотрено
        </DataTable.HeadCell>
        <DataTable.HeadCell className='mailing-clicks-column'>
          Кликов
        </DataTable.HeadCell>
        <DataTable.HeadCell type="actions" />
      </DataTable.HeadRow>

      <DataTable.Body>
        {(() => {
          if (!items.length) {
            return (
              <DataTable.Placeholder>
                Нет ни одной рассылки
              </DataTable.Placeholder>
            )
          }

          return items.map((item, index) => (
            <DataTable.Row
              key={index}
              className='table-row'
            >
              <DataTable.Cell className='mailing-icon-column'>
                <Icon name="mail-sent" width={40} />
              </DataTable.Cell>
              <DataTable.Cell className='mailing-name-column'>
                {item.name}
              </DataTable.Cell>
              <DataTable.Cell className='mailing-date-column'>
                <ReadableDate date={item.sentAt} />
              </DataTable.Cell>
              <DataTable.Cell className='mailing-date-column'>
                <ReadableDate date={item.createdAt} />
              </DataTable.Cell>
              <DataTable.Cell className='mailing-count-column'>
                {item.dispatched}
              </DataTable.Cell>
              <DataTable.Cell className='mailing-count-column'>
                <Percentage
                  value={item.viewed}
                  from={item.dispatched}
                />
              </DataTable.Cell>
              <DataTable.Cell className='mailing-clicks-column'>
                <Percentage
                  value={item.clicks}
                  from={item.dispatched}
                />
              </DataTable.Cell>
              <DataTable.Cell type="actions">
                <DataTable.Actions
                  actions={[
                    {
                      type: 'copy',
                      handler: () => console.log('copy')
                    },
                  ]}
                />
              </DataTable.Cell>
            </DataTable.Row>
          ))
        })()}
      </DataTable.Body>
    </DataTable>
  );
}
