import React from 'react';

import DataTable from '../DataTable';
import Icon from '../Icon';
import ProjectInfo from '../ProjectInfo';
import Switch from '../Switch';

import './BotsTable.scss';

export default function BotsTable({ items, onOpenModal, onSetCurrentElem, onToggleState }) {
  return (
    <DataTable>
      <DataTable.HeadRow>
        <DataTable.HeadCell className='switch-column' />
        <DataTable.HeadCell className='name-column'>
          Название бота
      </DataTable.HeadCell>
        <DataTable.HeadCell className='account-column'>
          Аккаунт
      </DataTable.HeadCell>
        <DataTable.HeadCell className='date-column'>
          Дата создания
      </DataTable.HeadCell>
        <DataTable.HeadCell className='count-column'>
          Кол-во подписчиков
      </DataTable.HeadCell>
        <DataTable.HeadCell className='link-column' />
        <DataTable.HeadCell type="actions" />
      </DataTable.HeadRow>

      <DataTable.Body>
        {(() => {
          return items.map((item, index) => (
            <DataTable.Row
              className={`table-row ${item.enabled ? '' : 'table-row_disabled'}`}
            >
              <DataTable.Cell className='switch-column'>
                <Switch
                  enabled={item.enabled}
                  onChange={(value) => {
                    onToggleState({ ...item, enabled: value })
                  }} />
              </DataTable.Cell>
              <DataTable.Cell className='type-column'>
                <Icon name={item.adapterType} width={30} />
              </DataTable.Cell>
              <DataTable.Cell className='name-column'>
                {item.title}
              </DataTable.Cell>
              <DataTable.Cell className='account-column'>
                <ProjectInfo withType={false} info={item.adapterConnectorId} />
              </DataTable.Cell>
              <DataTable.Cell className='date-column'>
                {/*<ReadableDate date={item.createdAt} />*/}
                {item.date}
              </DataTable.Cell>
              <DataTable.Cell className='count-column'>
                {item.subscribers}
              </DataTable.Cell>
              <DataTable.Cell className='link-column'>
                <div
                  className='link-column-text'
                  onClick={() => onOpenModal('copyBotLink')}>
                  Ссылка для запуска
                </div>
              </DataTable.Cell>
              <DataTable.Cell type="actions">
                <DataTable.Actions
                  actions={[
                    {
                      type: 'edit',
                      handler: () => {
                        window.location = item.linkPath
                      }
                    },
                    {
                      type: 'remove',
                      handler: () => {
                        onSetCurrentElem({ botId: item.id, type: item.type })
                        onOpenModal('removeBot')
                      }
                    },
                  ]}
                />
              </DataTable.Cell>
            </DataTable.Row>
          ))
        })()}
      </DataTable.Body>
    </DataTable>
  );
}
