import React, { Fragment } from 'react';

import ElemMenuEditor from '../ElemMenuEditor'

const ElemMenuQuestionItem = ({
  question,
  currentElem,
  fields,
  onRemoveAnswer,
  onSelectItem,
  onCopyItem,
  onRemoveItem,
  onMoveItem,
  onChangeItem,
  onSetTemplate,
  onSelectElem,
  onOpenFloatMenu
}) => {
  const showAnswerSettings = () => {
    const {
      answerField,
      answerCorrect,
      answerRepeat,
      readyAnswers,
    } = question.data

    if (!answerField && !answerCorrect && !answerRepeat && !readyAnswers) {
      return (
        <div className="constructor-elemmenu__button constructor-elemmenu__button_add"
          onClick={(e) => {
            onOpenFloatMenu(e, 'answerCreate')
            onSelectItem(question.id)
          }}
        >Настроить ответ</div>
      )
    }

    const answers = readyAnswers.split(',').map(answer => {
      return answer.trim().length ? answer.trim() : null
    }).filter(a => !!a)

    const saveField = fields.find(field => field.value === answerField)

    const fieldLabel = saveField ? saveField.label : ''

    return (
      <div className='constructor-elemmenu__settings'
        onClick={(e) => {
          onOpenFloatMenu(e, 'answerCreate')
          onSelectItem(question.id)
        }}
      >
        <div className='constructor-elemmenu__settings-item'>{answerField ? `Сохранить в поле "${fieldLabel}"` : null}</div>
        <div className='constructor-elemmenu__settings-item'>{answerCorrect ? `Проверять корректность ответа` : null}</div>
        <div className='constructor-elemmenu__settings-item'>{answerRepeat ? `Повторить вопрос, если ответ не получен` : null}</div>
        <div className='constructor-elemmenu__settings-item'>{readyAnswers ? `Добавить ${answers.length} готовых ответов` : null}</div>
        <div className='constructor-elemmenu__settings-remove'
          onClick={(e) => {
            e.stopPropagation()
            onRemoveAnswer(question.id)
          }}
        >×</div>
      </div>
    )
  }

  return (
    <Fragment>
      <div className="constructor-elemmenu__label">Вопрос-ответ</div>
      <div className="constructor-elemmenu__question" key={question.id} >
        <div className="constructor-elemmenu__operations">
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_delete"
            onClick={() => onRemoveItem(question.id)}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_up"
            onClick={() => onMoveItem(question.id, 'up')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_down"
            onClick={() => onMoveItem(question.id, 'down')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_copy"
            onClick={() => onCopyItem(question.id)}
          />
        </div>
        <div className="constructor-elemmenu__label constructor-elemmenu__label_question">Вопрос:</div>
        <ElemMenuEditor
          placeholder='Введите сюда текст сообщения'
          currentElem={currentElem}
          item={question}
          text={question.text}
          toolbar={true}
          label='question'
          onSelectItem={onSelectItem}
          onSetTemplate={onSetTemplate}
          onChangeText={(txt) => onChangeItem(question.id, txt)}
          onOpenFloatMenu={onOpenFloatMenu}
          onSelectElem={onSelectElem}
        />
        <div className="constructor-elemmenu__label constructor-elemmenu__label_answer">Ответ:</div>
        {showAnswerSettings()}
      </div>
    </Fragment>
  )

};

export default ElemMenuQuestionItem;