import React from 'react';

import Button from '../../common/Button'
import Modal from '../Modal'

const CancelSubscribe = ({ onCloseModal, onOpenModal, onCancelSubscribe }) => {
  return (
    <Modal
      title='Вы уверены, что хотите отменить продление подписки?'
      onClose={onCloseModal}
    >
      <div className="modal-content">
        Обратите внимание, что после окончания оплаченного периода все платные функции будут отключены до момента продления платной подписки.
        После отмены продления подписки вы сможете возобновить ее в любой момент.
      </div>
      <div className="modal-footer" >
        <div className="modal-buttons">
          <Button modifier='cancel' onClick={() => {
            onCloseModal()
          }}>Отмена</Button>
          <Button modifier='remove' onClick={() => {
            onCancelSubscribe()
            onCloseModal()
          }}>Подтвердить</Button>
        </div>
      </div>
    </Modal>
  );
};

export default CancelSubscribe;