import React, { useState, Fragment } from 'react';
import OutsideClickHandler from 'react-outside-click-handler';
import FilterDetail from '../FilterDetail';

import './Filter.scss'

const Filter = ({
  options,
  placeholder,
  onChange,
  onAddRule,
}) => {
  const [open, toggleOpen] = useState(false);
  const [menuDetail, toggleMenuDetail] = useState('');

  const showFilterMenu = () => {
    return (
      <div className="filter-menu">
        {menuDetail
          ? <FilterDetail
            options={options}
            menuDetail={menuDetail}
            onAddRule={onAddRule}
            onCancel={() => toggleMenuDetail('')}
            onClose={() => toggleMenuDetail('')}
          />
          : <Fragment>
            <div className="filter-title">Добавить фильтр</div>
            <div className="filter-item"
              onClick={(e) => {
                toggleMenuDetail('tag')
                e.stopPropagation()
              }}
            >Метка</div>
            <div className="filter-item"
              onClick={(e) => {
                toggleMenuDetail('lastContact')
                e.stopPropagation()
              }}
            >Дата последнего контакта</div>
            <div className="filter-item"
              onClick={(e) => {
                toggleMenuDetail('firstContact')
                e.stopPropagation()
              }}
            >Дата подписки</div>
            {/*<div className="filter-item"
              onClick={(e) => {
                toggleMenuDetail('onlinechats')
                e.stopPropagation()
              }}
            >Количество онлайн-чатов</div>*/}
            <div className="filter-subtitle">Системные поля</div>
            <div className="filter-item"
              onClick={(e) => {
                toggleMenuDetail('name')
                e.stopPropagation()
              }}
            >Имя</div>
            <div className="filter-item"
              onClick={(e) => {
                toggleMenuDetail('email')
                e.stopPropagation()
              }}
            >Email</div>
            <div className="filter-item"
              onClick={(e) => {
                toggleMenuDetail('phone')
                e.stopPropagation()
              }}
            >Телефон</div>
            {/*<div className="filter-subtitle">Пользовательские поля</div>
            <div className="filter-item"
              onClick={(e) => {
                toggleMenuDetail('interest')
                e.stopPropagation()
              }}
            >Заинтересован</div>
            <div className="filter-item"
              onClick={(e) => {
                toggleMenuDetail('test')
                e.stopPropagation()
              }}
            >Тестовое</div>*/}
          </Fragment>
        }
      </div>
    )
  }

  return (
    <div>
      <div className="filter">
        <div
          className="filter-button"
          onClick={() => toggleOpen(!open)}
        >
          <OutsideClickHandler
            onOutsideClick={() => {
              toggleOpen(false)
            }}
          >
            {open ? showFilterMenu() : <div />}
          </OutsideClickHandler>
        </div>
        <input
          type="text"
          className="filter-input"
          placeholder={placeholder}
          onChange={onChange}
        />
        <div className="filter-search" />
      </div>
    </div>
  )

};

export default Filter;