import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux';

import { constructorBotActions, modalActions } from '../../actions/index'

import ModalController from '../ModalController'
import NodeFlow from '../../components/constructor/NodeFlow/NodeFlow'

class CanvasBotController extends Component {

  openFloatMenu = (floatMenuType) => {
    const { openFloatMenu } = this.props
    openFloatMenu(
      document.querySelector('.constructor-elemmenu__add').getBoundingClientRect().top,
      document.querySelector('.constructor-elemmenu').getBoundingClientRect().left,
      'auto',
      floatMenuType
    )
  }

  handleRemoveElem = (id) => {
    const { constructorBot, removeElem, openModal } = this.props
    const { removeElemWithoutConfirm } = constructorBot

    if (Array.isArray(id)) {
      removeElem(id)
    } else {
      if (removeElemWithoutConfirm) {
        removeElem([{ id }])
      } else {
        openModal('removeElem')
      }
    }
  }
  componentDidMount() {
    const { getElems } = this.props
    getElems()
  }

  render() {
    const {
      constructorBot,
      selectElem,
      copyElem,
      linkElem,
      unlinkElem,
      coordsElem,
      selectBtn,
      openFloatMenu,
      closeFloatMenu
    } = this.props

    const { elems, currentElem, fields } = constructorBot

    return (
      <Fragment>
        <NodeFlow
          flowType='bot'
          elems={elems}
          fields={fields}
          currentElem={currentElem}
          onSelectElem={selectElem}
          onCopyElem={copyElem}
          onRemoveElem={this.handleRemoveElem}
          onLinkElem={linkElem}
          onUnlinkElem={unlinkElem}
          onCoordsElem={coordsElem}
          onSelectBtn={selectBtn}
          openFloatMenu={this.openFloatMenu}
          onOpenFloatMenu={openFloatMenu}
          onCloseFloatMenu={closeFloatMenu}
        />
        <ModalController />
      </Fragment>

    )
  }
}

const mapStateToProps = (state) => {
  const { constructorBot, loading, error } = state

  return {
    constructorBot,
    loading,
    error
  };
};

const mapDispatchToProps = {
  getElems: constructorBotActions.elems.fetch.request,
  addElem: constructorBotActions.elems.add,
  copyElem: constructorBotActions.elems.copy,
  selectElem: constructorBotActions.elems.select,
  removeElem: constructorBotActions.elems.remove,
  linkElem: constructorBotActions.elems.link,
  unlinkElem: constructorBotActions.elems.unlink,
  coordsElem: constructorBotActions.elems.coords,
  openFloatMenu: constructorBotActions.floatmenu.open,
  closeFloatMenu: constructorBotActions.floatmenu.close,
  selectBtn: constructorBotActions.button.select,
  openModal: modalActions.modal.open,
  closeModal: modalActions.modal.close,
};

export default connect(mapStateToProps, mapDispatchToProps)(CanvasBotController)