import React, { Component, Fragment } from 'react';

import ElemMenuEditor from '../ElemMenuEditor'
class ElemMenuMessageItem extends Component {
  componentWillReceiveProps = (newProps) => {
    const { message, currentElem, onSelectBtn, onOpenFloatMenu } = this.props
    const item = currentElem.data.items.find(item => item.id === message.id)
    const newItem = newProps.currentElem.data.items.find(item => item.id === message.id)

    if (newItem && newItem.buttons && item && item.buttons && newItem.buttons.length > item.buttons.length) {
      setTimeout(() => {
        const btn = newItem.buttons[newItem.buttons.length - 1]
        onSelectBtn(btn)
        onOpenFloatMenu({ target: document.getElementById(btn.id) }, 'buttonNextStep')
      }, 0);
    }
  }

  render() {
    const {
      message,
      currentElem,
      onSelectElem,
      onUnlinkElem,
      onSelectItem,
      onCopyItem,
      onRemoveItem,
      onMoveItem,
      onChangeItem,
      onSelectBtn,
      onAddBtn,
      onRemoveBtn,
      onSetTemplate,
      onOpenFloatMenu
    } = this.props

    return (
      <Fragment>
        <div className="constructor-elemmenu__label">Текст + кнопки</div>
        <div className="constructor-elemmenu__item constructor-elemmenu__message" key={message.id} >
          <div className="constructor-elemmenu__operations">
            <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_delete"
              onClick={() => onRemoveItem(message.id)}
            />
            <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_up"
              onClick={() => onMoveItem(message.id, 'up')}
            />
            <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_down"
              onClick={() => onMoveItem(message.id, 'down')}
            />
            <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_copy"
              onClick={() => onCopyItem(message.id, message.buttons ? message.buttons.length : 0)}
            />
          </div>
          <ElemMenuEditor
            placeholder='Введите сюда текст сообщения'
            currentElem={currentElem}
            item={message}
            toolbar={true}
            onSelectItem={onSelectItem}
            onChangeText={(txt) => onChangeItem(message.id, txt)}
            onSetTemplate={onSetTemplate}
            onOpenFloatMenu={onOpenFloatMenu}
            onSelectElem={onSelectElem}
          />
          {
            message.buttons && message.buttons.length ? <div className='constructor-elemmenu__buttons'>
              {message.buttons.map((btn) => {
                return (
                  <div
                    className='constructor-elemmenu__button'
                    id={btn.id}
                    key={btn.id}
                    onClick={(e) => {
                      onSelectBtn(btn)
                      onOpenFloatMenu(e, 'buttonNextStep')
                    }}
                  >
                    <span className='constructor-elemmenu__button-txt'>{btn.data.text}</span>
                    <span className='constructor-elemmenu__button-remove'
                      onClick={(e) => {
                        e.stopPropagation()
                        onSelectBtn(btn)
                        onRemoveBtn(message.id)
                        if (btn.data.nextStep) {
                          onUnlinkElem(`${currentElem.id}__${btn.id}`, btn.data.nextStep.id)
                        }
                        if (btn.data.nextStepId) {
                          onUnlinkElem(`${currentElem.id}__${btn.id}`, btn.data.nextStepId)
                        }
                      }}
                    />
                  </div>
                )
              })}
            </div> : null
          }
          <div className="constructor-elemmenu__button constructor-elemmenu__button_add"
            onClick={() => {
              onAddBtn(message.id)
            }}>+&nbsp;Добавить кнопку</div>
        </div>
      </Fragment>
    )
  }



};

export default ElemMenuMessageItem;