import React, { memo, Fragment } from 'react';
import cx from 'classnames'
import { Handle } from 'react-flow-renderer';

const NAMES_MAP = {
  addTag: 'Добавить метки',
  removeTag: 'Удалить метки',
  addField: 'Установить значение для польз. поля',
  removeField: 'Очистить пользовательское поле',
  increaseField: 'Увеличить числовое значение \n пользовательского поля на',
  decreaseField: 'Уменьшить числовое значение \n пользовательского поля на',
  sendEmail: 'Отправить данные пользователя на email',
}

export default memo(({
  flowType,
  id,
  data,
  isActive,
  onCopyElem,
  onUnlinkElem,
  onRemoveElem
}) => {
  const { items, text } = data

  const showTag = (item, itemType) => {
    return (
      <div className='flow-block__actions-item' key={item.id}>
        <div className={`flow-block__${itemType}`}>{NAMES_MAP[item.itemType]}</div>
        {
          item.tags && item.tags.length ? <div className='flow-block__tags'>
            {item.tags.map((tag) => {
              return (
                <span
                  className={cx(
                    'flow-block__tag',
                    {
                      'flow-block__tag_add': itemType === 'addTag',
                      'flow-block__tag_remove': itemType === 'removeTag',
                    }
                  )}
                  key={tag.id}>#{tag.text}</span>
              )
            })}
          </div> : <span className='flow-block__tag'>Добавьте сюда метку</span>
        }
      </div>
    )
  }

  const showField = (item, itemType) => {
    if (itemType === 'addField') {
      return (
        <div className='flow-block__actions-item' key={item.id}>
          <div className={`flow-block__${itemType}`}>{NAMES_MAP[item.itemType]}</div>
          {
            item.data.field ?
              <Fragment>
                <div className='flow-block__field'>Установить пользовательское поле</div>
                Поле {item.data.field} =  {item.data.value}
              </Fragment>
              : <span className='flow-block__field'>Добавьте сюда пользовательское поле</span>
          }
        </div>
      )
    }

    if (itemType === 'removeField') {
      return (
        <div className='flow-block__actions-item' key={item.id}>
          <div className={`flow-block__${itemType}`}>{NAMES_MAP[item.itemType]}</div>
          {
            item.data.field ?
              <Fragment>
                <div className='flow-block__field'>Очистить пользовательское поле</div>
                Поле {item.data.field}
              </Fragment>
              : <span className='flow-block__field'>Добавьте сюда пользовательское поле</span>
          }
        </div>
      )
    }
  }

  const showIncreaseDecrease = (item, itemType) => {
    return (
      <div className='flow-block__actions-item' key={item.id}>
        <div className={`flow-block__${itemType}`}>{NAMES_MAP[item.itemType]}</div>
        {item.data.value
          ? <span className='flow-block__field'>Поле {item.data.field} &nbsp;
            {item.data.type === 'increase' ? 'увеличить' : 'уменьшить'} &nbsp;
          на {item.data.value}</span>
          : <span className='flow-block__field'>Добавьте сюда пользовательское поле</span>
        }
      </div>
    )
  }


  const showEmail = (item, itemType) => {
    return (
      <div className='flow-block__actions-item' key={item.id}>
        <div className={`flow-block__${itemType}`}>{NAMES_MAP[item.itemType]}</div>
        {
          item.emails && item.emails.length ? <div className='flow-block__fields'>
            {item.emails.map((email) => {
              return (
                <span className='flow-block__field'
                  key={email.id}>{email.text}</span>
              )
            })}
          </div> : <span className='flow-block__field'>Добавьте сюда email</span>
        }
      </div>
    )
  }

  const showSubscribe = (item, itemType) => {
    const TITLES = {
      subscribe: 'Подписать на бота',
      unsubscribe: 'Отписать от бота'
    }

    return (
      <div className='flow-block__actions-item' key={item.id}>
        <div className={`flow-block__${itemType}`}>{TITLES[itemType]}</div>
      </div>
    )
  }

  const showStop = (item, itemType) => {
    return (
      <div className='flow-block__actions-item' key={item.id}>
        <div className={`flow-block__stop`}>Остановить бота</div>
      </div>
    )
  }

  return (
    <div className={cx(
      'flow-block',
      'flow-block_action',
      {
        'flow-block_active': isActive
      }
    )}>
      {flowType === 'bot' ? <Fragment>
        <div className='flow-block__copy' onClick={() => onCopyElem(id)} />
        <div className='flow-block__delete' onClick={() => { onRemoveElem(id) }} />
      </Fragment> : null}
      <div className='flow-block__title flow-block__title_action'>{text}</div>
      <Handle
        type="target"
        position="left"
        style={{ background: '#fff', top: 20, border: '1px solid #F77545' }}
        onConnect={({ source, target }) => onUnlinkElem(source, target)}
      />

      <div className='flow-block__actions'>
        {items && items.length ? items.map((item, i) => {

          switch (item.itemType) {
            case 'addTag':
            case 'removeTag':
              return showTag(item, item.itemType)

            case 'addField':
            case 'removeField':
              return showField(item, item.itemType)

            case 'increaseField':
            case 'decreaseField':
              return showIncreaseDecrease(item, item.itemType)

            case 'sendEmail':
              return showEmail(item, item.itemType)

            case 'subscribe':
            case 'unsubscribe':
              return showSubscribe(item, item.itemType)

            case 'stop':
              return showStop(item, item.itemType)

            default:
              return null
          }
        }) : <div className="flow-block__placeholder flow-block__placeholder_action">Добавьте сюда событие</div>
        }
      </div>

      {flowType === 'bot' ? <div className="flow-block__nextstep">
        Следующий шаг
      <Handle type="source" position="right" id="output"
          style={{ bottom: 7, top: 'auto', border: '1px solid #F77545' }} />
      </div> : null}
    </div>
  );
});
