import React, { Component } from 'react'
import { connect } from 'react-redux';

import { constructorBotActions } from '../../actions/index'

import ElemMenuCondition from '../../components/constructor/ElemMenu/condition/ElemMenuCondition'

class ElemMenuBotConditionsController extends Component {

  handleFloatMenu = (e, floatMenuType) => {
    const { openFloatMenu } = this.props
    openFloatMenu(
      e.target.getBoundingClientRect().top,
      e.target.closest('.constructor-elemmenu').getBoundingClientRect().left,
      'auto',
      floatMenuType
    )
  }

  render() {
    const {
      constructorBot: {
        elems,
        currentElem,
        floatMenu
      },
      linkElem,
      unlinkElem,
      removeConditionItem,
      changeConditionForm,
      closeFloatMenu
    } = this.props

    if (!currentElem.id || currentElem.id.startsWith('link_')) {
      return null
    }

    return (
      <ElemMenuCondition
        elems={elems}
        currentElem={currentElem}
        floatMenu={floatMenu.type}
        top={floatMenu.top}
        left={floatMenu.left}
        bottom={floatMenu.bottom}
        onLinkElem={linkElem}
        onUnlinkElem={unlinkElem}
        onRemoveConditionItem={removeConditionItem}
        onChangeConditionForm={changeConditionForm}
        onCloseFloatMenu={closeFloatMenu}
        handleFloatMenu={this.handleFloatMenu}
      />
    )
  }
}

const mapStateToProps = (state) => {
  const { constructorBot, loading, error } = state

  return {
    constructorBot,
    loading,
    error
  };
};

const mapDispatchToProps = {
  openFloatMenu: constructorBotActions.floatmenu.open,
  closeFloatMenu: constructorBotActions.floatmenu.close,
  linkElem: constructorBotActions.elems.link,
  unlinkElem: constructorBotActions.elems.unlink,
  removeConditionItem: constructorBotActions.condition.item.remove,
  changeConditionForm: constructorBotActions.condition.form,
};

export default connect(mapStateToProps, mapDispatchToProps)(ElemMenuBotConditionsController)