import React, { useState, Fragment } from 'react';

import Modal from '../Modal'

import Button from '../../common/Button'
import BotSelect from '../../common/BotSelect';

import './EditKeywordsModal.scss';

const EditKeywordsModal = ({ bots, dataKeywords, currentElem, onCloseModal, onAddKeywords }) => {
  const elem = dataKeywords.find(item => item.id === currentElem.keywordsId)
  const bot = bots.find(item => item.referral === elem.actions[0].value)
  const [keywords, setKeywords] = useState(elem.keywords.join(','));
  const [activeBot, setActiveBot] = useState({ label: bot.title, type: bot.adapterType, value: bot.referral });
  let keywordsArr = []


  const showKeywords = () => {
    if (!keywords.trim().length) {
      return null
    }

    keywordsArr = keywords.split(',').filter(item => !!item.trim())

    return (
      <Fragment>
        {keywordsArr.map((item, i) => {
          return (
            <div className="keywords-item" key={i}>{item}
              <span className='keywords-item-close'
                onClick={() => {
                  const txt = keywordsArr.filter(answer => answer !== item).join(',').trim()
                  setKeywords(txt)
                }}
              >×</span>
            </div>
          )
        })}
      </Fragment>
    )
  }

  const options = bots.map(item => {
    return {
      label: item.title,
      value: item.referral,
      type: item.adapterType,
    }
  })

  return (
    <Modal
      title='Редактирование ключевого слова'
      onClose={onCloseModal}
    >
      <div className="modal-content">
        <div className='add-keyword-form-tags'>
          <input
            className='modal-input'
            placeholder="Введите слова через запятую (например, “привет, сколько стоит, найти” и т.д.)"
            value={keywords}
            onChange={(e) => setKeywords(e.target.value)}
          />
          <div className="keywords">
            {showKeywords()}
          </div>
        </div>

        <BotSelect
          placeholder='Какой бот должен запуститься?'
          options={options}
          activeOption={activeBot}
          onChange={(option) => {
            setActiveBot(option)
          }}
        />

        <div className="modal-footer" >
          <div className="modal-buttons">
            <Button modifier='cancel' onClick={() => {
              onCloseModal()
            }}>Отмена</Button>
            <Button onClick={() => {
              onAddKeywords(keywordsArr, activeBot, elem.id)
              onCloseModal()
            }}>Изменить</Button>
          </div>
        </div>
      </div>
    </Modal>
  );
};

export default EditKeywordsModal;