import React from 'react';

import Button from '../../common/Button'
import Modal from '../Modal'

const RemoveAccount = ({ onCloseModal, onRemoveChannel, onSetCurrentElem }) => {
  return (
    <Modal
      title='Вы уверены что хотите удалить аккаунт?'
      onClose={onCloseModal}
    >
      <div className="modal-content">
        Аккаунт будет полностью удален из системы.
      </div>
      <div className="modal-footer" >
        <div className="modal-buttons">
          <Button modifier='cancel' onClick={() => {
            onSetCurrentElem({})
            onCloseModal()
          }}>Отмена</Button>
          <Button modifier='remove' onClick={() => {
            onRemoveChannel()
            onSetCurrentElem({})
            onCloseModal()
          }}>Удалить аккаунт</Button>
        </div>
      </div>
    </Modal>
  );
};

export default RemoveAccount;