import React, { useState, Fragment } from 'react';
import OutsideClickHandler from 'react-outside-click-handler';
import fuzzysort from 'fuzzysort'
import cx from 'classnames'
import DatePicker, { registerLocale } from 'react-datepicker'
import MaskedTextInput from 'react-text-mask'
import 'react-datepicker/dist/react-datepicker.css'
import ru from 'date-fns/locale/ru'
import Editor from '../../constructor/ElemMenu/ElemMenuEditor'
import moment from 'moment'

import './FilterDetail.scss'

moment.locale('ru')
registerLocale('ru', ru)

const FilterDetail = ({
  menuDetail,
  options,
  onAddRule,
  onCancel,
  onClose
}) => {
  const [activeMenuItem, setActiveMenuItem] = useState('');
  const [text, setText] = useState('');
  const [beforeDate, setBeforeDate] = useState(null);
  const [afterDate, setAfterDate] = useState(null);
  const [onDate, setOnDate] = useState(null);
  const [fieldList, toggleFieldList] = useState(false);
  const [searchValue, setSearchValue] = useState('');

  let filteredOptions = options

  if (searchValue.length) {
    const data = fuzzysort.go(searchValue, options, {
      keys: ['label']
    })

    filteredOptions = data.map(i => i.obj)
  }

  const TITLES = {
    tag: 'Метка',
    name: 'Имя',
    email: 'Email',
    phone: 'Телефон',
    lastContact: 'Дата последнего контакта',
    firstContact: 'Дата подписки'
  }

  const showDateTime = () => {
    switch (activeMenuItem) {
      // case 'before':
      //   return (
      //     <Fragment>
      //       <div>Кол-во дней:</div>
      //       <input
      //         className='filter-input filter-input_number'
      //         min="1"
      //         step="1"
      //         type="number"
      //       />&nbsp;
      //       дней назад
      //     </Fragment>
      //   )

      // case 'after':
      //   return (
      //     <Fragment>
      //       <div>Кол-во дней:</div>
      //       через&nbsp;
      //       <input
      //         className='filter-input filter-input_number'
      //         min="1"
      //         step="1"
      //         type="number"
      //       />&nbsp;
      //       дней
      //     </Fragment>
      //   )
      case 'before':
        return (
          <Fragment>
            <div>Дата:</div>
            <DatePicker
              selected={beforeDate}
              className='constructor-elemmenu__datepicker'
              locale='ru'
              dateFormat='dd.MM.yyyy'
              placeholderText='ДД.ММ.ГГГГ'
              onChange={setBeforeDate}
              onSelect={setBeforeDate}
              customInput={
                <MaskedTextInput
                  type='text'
                  mask={[/\d/, /\d/, '.', /\d/, /\d/, '.', /\d/, /\d/, /\d/, /\d/]}
                />
              }
            />
          </Fragment>
        )

      case 'after':
        return (
          <Fragment>
            <div>Дата:</div>
            <DatePicker
              selected={afterDate}
              className='constructor-elemmenu__datepicker'
              locale='ru'
              dateFormat='dd.MM.yyyy'
              placeholderText='ДД.ММ.ГГГГ'
              onChange={setAfterDate}
              onSelect={setAfterDate}
              customInput={
                <MaskedTextInput
                  type='text'
                  mask={[/\d/, /\d/, '.', /\d/, /\d/, '.', /\d/, /\d/, /\d/, /\d/]}
                />
              }
            />
          </Fragment>
        )

      case 'on':
        return (
          <Fragment>
            <div>Дата:</div>
            <DatePicker
              selected={onDate}
              className='constructor-elemmenu__datepicker'
              locale='ru'
              dateFormat='dd.MM.yyyy'
              placeholderText='ДД.ММ.ГГГГ'
              onChange={setOnDate}
              onSelect={setOnDate}
              customInput={
                <MaskedTextInput
                  type='text'
                  mask={[/\d/, /\d/, '.', /\d/, /\d/, '.', /\d/, /\d/, /\d/, /\d/]}
                />
              }
            />
          </Fragment>
        )

      default:
        return null
    }
  }

  const showLeftCol = () => {
    switch (menuDetail) {
      case 'tag':
        if (!activeMenuItem) {
          setActiveMenuItem('is')
        }
        return (
          <div className="filter-detail-col">
            <div className="filter-detail-list">
              <div className={
                cx(
                  'filter-detail-item',
                  { 'filter-detail-item_active': activeMenuItem === 'is' }
                )
              }
                onClick={(e) => {
                  e.stopPropagation()
                  setActiveMenuItem('is')
                }}>Соответствует</div>
              <div className={
                cx(
                  'filter-detail-item',
                  { 'filter-detail-item_active': activeMenuItem === 'is_not' }
                )
              }
                onClick={(e) => {
                  e.stopPropagation()
                  setActiveMenuItem('is_not')
                }}>Не соответствует</div>
            </div>
          </div>
        )

      case 'name':
      case 'email':
      case 'phone':
        if (!activeMenuItem) {
          setActiveMenuItem('is')
        }

        return (
          <div className="filter-detail-col">
            <div className="filter-detail-list">
              <div className={
                cx(
                  'filter-detail-item',
                  { 'filter-detail-item_active': activeMenuItem === 'is' }
                )
              }
                onClick={(e) => {
                  e.stopPropagation()
                  setActiveMenuItem('is')
                }}>Соответствует</div>
              <div className={
                cx(
                  'filter-detail-item',
                  { 'filter-detail-item_active': activeMenuItem === 'is_not' }
                )
              }
                onClick={(e) => {
                  e.stopPropagation()
                  setActiveMenuItem('is_not')
                }}>Не соответствует</div>
              <div className={
                cx(
                  'filter-detail-item',
                  { 'filter-detail-item_active': activeMenuItem === 'contains' }
                )
              }
                onClick={(e) => {
                  e.stopPropagation()
                  setActiveMenuItem('contains')
                }}>Содержит</div>
              <div className={
                cx(
                  'filter-detail-item',
                  { 'filter-detail-item_active': activeMenuItem === 'does_not_contain' }
                )
              }
                onClick={(e) => {
                  e.stopPropagation()
                  setActiveMenuItem('does_not_contain')
                }}>Не содержит</div>
              <div className={
                cx(
                  'filter-detail-item',
                  { 'filter-detail-item_active': activeMenuItem === 'has_any_value' }
                )
              }
                onClick={(e) => {
                  e.stopPropagation()
                  setText('')
                  setActiveMenuItem('has_any_value')
                }}>Любое значение</div>
              <div className={
                cx(
                  'filter-detail-item',
                  { 'filter-detail-item_active': activeMenuItem === 'is_unknown' }
                )
              }
                onClick={(e) => {
                  e.stopPropagation()
                  setText('')
                  setActiveMenuItem('is_unknown')
                }}>Неизвестно</div>
            </div>
          </div>
        )
      case 'lastContact':
      case 'firstContact':
        if (!activeMenuItem) {
          setActiveMenuItem('before')
        }
        return (
          <div className="filter-detail-col">
            <div className="filter-detail-list">
              <div className={
                cx(
                  'filter-detail-item',
                  { 'filter-detail-item_active': activeMenuItem === 'before' }
                )
              }
                onClick={(e) => {
                  e.stopPropagation()
                  setActiveMenuItem('before')
                }}
              >До</div>
              <div className={
                cx(
                  'filter-detail-item',
                  { 'filter-detail-item_active': activeMenuItem === 'after' }
                )
              }
                onClick={(e) => {
                  e.stopPropagation()
                  setActiveMenuItem('after')
                }}
              >После</div>
              <div className={
                cx(
                  'filter-detail-item',
                  { 'filter-detail-item_active': activeMenuItem === 'on' }
                )
              }
                onClick={(e) => {
                  e.stopPropagation()
                  setActiveMenuItem('on')
                }}
              >Конкретная дата</div>
            </div>
          </div>
        )

      default:
        return null
    }
  }

  const showRightCol = () => {
    switch (menuDetail) {
      case 'tag':
        return (
          <div className={
            cx(
              'filter-detail-col',
              'filter-detail-col_padding',
            )
          }
            onClick={(e) => e.stopPropagation()}
          >
            <OutsideClickHandler
              onOutsideClick={() => {
                toggleFieldList(false)
              }}
            >
              <div className="menu__choice-select">
                <input
                  type="text"
                  placeholder='Название метки'
                  className={
                    cx(
                      'menu__input',
                      { 'menu__input_active': fieldList }
                    )
                  }
                  onFocus={(e) => {
                    toggleFieldList(true)
                  }}
                  value={searchValue}
                  onChange={(e) => setSearchValue(e.target.value)}
                />
                {fieldList
                  ? <div className="menu__choice-list menu__choice-list_xs">
                    {filteredOptions.length
                      ? filteredOptions.map((field, i) => {
                        return (
                          <div key={i} className="menu__choice-item"
                            onClick={(e) => {
                              toggleFieldList(!fieldList)
                              setSearchValue(field.label)
                            }}
                          >{field.label}</div>
                        )
                      })
                      : <div className="menu__choice-create">Метка не найдена</div>
                    }
                  </div>
                  : null}
              </div>
            </OutsideClickHandler>
          </div>
        )


      case 'name':
      case 'email':
      case 'phone':
        return (
          <div className={
            cx(
              'filter-detail-col',
              'filter-detail-col_padding',
            )
          }
            onClick={(e) => e.stopPropagation()}
          >
            {activeMenuItem === 'is_unknown' || activeMenuItem === 'has_any_value'
              ? null :
              <Editor
                placeholder='Введите значение'
                text={text}
                onChangeText={setText}
              />
            }
          </div>
        )

      case 'lastContact':
      case 'firstContact':
        return (
          <div className={
            cx(
              'filter-detail-col',
              'filter-detail-col_padding',
              'filter-detail-col_wide'
            )
          }
            onClick={(e) => e.stopPropagation()}
          >
            {showDateTime()}
          </div>
        )

      default:
        return null
    }
  }

  const showFooter = () => {
    return (
      <div className="menu-buttons">
        <div className="button button_cancel"
          onClick={() => {
            onCancel()
          }}
        >Отмена</div>
        <div className="button button_submit"
          onClick={() => {
            if (menuDetail === 'tag') {
              onAddRule({ label: TITLES[menuDetail], type: "userTag", predicate: activeMenuItem, argument: searchValue })
            }

            if (menuDetail === 'lastContact' || menuDetail === 'firstContact') {
              let argument = ''
              switch (activeMenuItem) {
                case 'before':
                  argument = +beforeDate
                  break
                case 'after':
                  argument = +afterDate
                  break
                case 'on':
                  argument = +onDate
                  break
                default:
                  break
              }
              onAddRule({ label: TITLES[menuDetail], type: menuDetail, predicate: activeMenuItem, argument })
            }

            console.log('menuDetail');
            console.log(menuDetail);
            console.log('activeMenuItem');
            console.log(activeMenuItem);

            if (['name', 'email', 'phone'].includes(menuDetail)) {
              onAddRule({ label: TITLES[menuDetail], attribute: menuDetail, type: 'attribute', predicate: activeMenuItem, argument: text })
            }

            onCancel()
            onClose()
          }}
        >Подтвердить</div>
      </div>
    )
  }

  return (
    <div className="filter-detail">
      <div className="filter-detail-back"
        onClick={(e) => {
          e.stopPropagation()
          onCancel()
        }}>‹</div>
      <div className="filter-detail-title">{TITLES[menuDetail]}</div>
      <div className="filter-detail-content">
        {showLeftCol()}
        {showRightCol()}
      </div>
      {showFooter()}
    </div>
  )
}



export default FilterDetail;