import React, { Fragment, Component } from 'react'
import { connect } from 'react-redux';
import cx from 'classnames';
import { Redirect } from 'react-router-dom'
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';

import PageLayout from '../components/common/PageLayout';

import Button from '../components/common/Button'

import { settingsActions } from '../actions/index'

import getCookie from '../const/getCookie'

import styles from './Payment.module.scss';

const MARKS = {
  0: 'До 500 подписчиков',
  1: '1К',
  2: '2.5 К',
  3: '5 К',
  4: '10 К',
  5: '15 К',
  6: '20 К',
  7: '30 К',
  8: '40 К',
  9: '50 К',
  10: '100 000 К',
  11: '150 000 К',
  12: '200 000 К',
  13: 'До 300 000 подписчиков',
};

const PRICES = {
  0: 490,
  1: 1490,
  2: 2490,
  3: 3490,
  4: 4490,
  5: 5490,
  6: 6490,
  7: 7490,
  8: 8490,
  9: 9490,
  10: 10490,
  11: 11490,
  12: 12490,
  13: 13490,
};

const { createSliderWithTooltip } = Slider;
const Tooltipped = createSliderWithTooltip(Slider);

class PaymentController extends Component {
  state = {
    tariff: 0
  }

  componentDidMount() {
    // const { getBasicData } = this.props
    // getBasicData()
  }

  render() {
    // const { settings } = this.props
    const { tariff } = this.state

    const MAPPED_MARKS = Object.keys(MARKS).map(key => {
      const mark = MARKS[key];

      if (+key === tariff) {
        return {
          label: <span className={styles['active-mark']}>{mark}</span>,
        }
      }

      return mark;
    });

    return (
      <PageLayout
        title="Оплата"
      >

        <article className={styles.tariff}>
          <div className={styles['tariff-item']}>
            <strong>Текущий тариф:</strong> Пробный
          </div>
          <div className={styles['tariff-item']}>
            <strong>Срок действия:</strong> Осталось 14 дней
          </div>
          <div className={styles['tariff-item']}>
            <strong>Стоимость:</strong> {PRICES[tariff].toLocaleString('ru')} руб.
          </div>
          <div className={cx(styles['tariff-item'], styles['tariff-item-date'])}>
            Дата следующего списания 01.11.2020
          </div>
        </article>

        <div className={styles.calculator}>
          <p className={styles.disclaimer}>
            Стоимость ежемесячной подписки зависит от общего кол-ва уникальных подписчиков во всех ботах:
          </p>

          <div className={styles['calculator-range-wrapper']}>
            <Tooltipped
              min={0}
              max={13}
              marks={MAPPED_MARKS}
              step={null}
              onChange={(value) => {
                this.setState({ tariff: value })
              }}
              defaultValue={tariff}
              tipProps={{
                visible: true,
              }}
              tipFormatter={
                () => <div className={styles['calculator-tooltip']}>
                  <span className={styles['calculator-tooltip-price']}>{PRICES[tariff].toLocaleString('ru')} руб.</span>
                  в месяц
                </div>
              }
            />
          </div>

          <div className={styles['calculator-results']}>
            <div className={styles['selected-tariff']}>
              <div className={styles['selected-tariff-item']}>
                <strong>Ваш тариф:</strong> {MARKS[tariff]}
              </div>
              <div className={styles['selected-tariff-item']}>
                <strong>Срок:</strong> 1 месяц
              </div>
            </div>

            <Button
              variant="primary"
              modifier='xl'
              onClick={() => {
                window.pay(PRICES[tariff])
              }}
            >
              Оплатить {PRICES[tariff].toLocaleString('ru')} руб.
            </Button>
          </div>
        </div>
      </PageLayout>
    );
  }
}

const mapStateToProps = (state) => {
  const { settings, loading, error } = state

  return {
    settings,
    loading,
    error,
  };
};

const mapDispatchToProps = {
  getBasicData: settingsActions.basic.request,
};

export default connect(mapStateToProps, mapDispatchToProps)(PaymentController)