import { handleActions } from 'redux-actions';
import produce from 'immer'
import moment from 'moment'

import {
  settingsActions,
} from '../actions'

moment.locale('ru')

const { basic, currentelem, socket, audience, automailing } = settingsActions

const initialState = {
  sessionId: null,
  basic: null,
  data: {
    adapters: [],
    bots: [],
    keywords: [],
    chats: [],
    tags: []
  },
  currentElem: {
    id: null,
    options: {}
  },
  subscribers: [],
  filters: {
    quantor: 'all',
    rules: []
  },
  loading: [],
  error: null,
}

const log = (obj) => {
  console.log(JSON.parse(JSON.stringify(obj)))
}

export default handleActions(
  {
    [basic.success.toString()]: produce((draft, action) => {
      draft.basic = action.payload
    }),

    [currentelem.set.toString()]: produce((draft, action) => {
      draft.currentElem = action.payload
    }),

    [socket.loading.add.toString()]: produce((draft, action) => {
      draft.loading = draft.loading.concat(action.payload)
    }),

    [socket.loading.remove.toString()]: produce((draft, action) => {
      const { event } = action.payload

      draft.loading = draft.loading.filter(item => item !== event)
    }),

    [socket.receive.toString()]: produce((draft, action) => {
      const { data } = action.payload

      if (data.error) {
        draft.error = data.error
      }

      if (data.adapters) {
        draft.data.adapters = data.adapters.filter(item => item.enabled)
      }

      if (data.chats) {
        draft.data.chats = data.chats.map(item => {
          return {
            id: item.id,
            name: item.user.name,
            createdAt: moment.unix(item.createdAt).format('DD.MM.YYYY'),
          }
        })
      }

      if (data.tags) {
        draft.data.tags = data.tags
      }

      if (Array.isArray(data) && data.length) {
        if (data[0].contentTag === 'eventUpdate') {
          draft.data.bots = data.map(item => {
            return {
              date: moment.unix(item.createdAt).format('DD.MM.YYYY'),
              ...item
            }
          })
        }

        if (data[0].keywords) {
          draft.data.keywords = data
        }
      }
    }),

    [audience.filters.set.toString()]: produce((draft, action) => {
      draft.filters = action.payload
    }),

    [audience.set.toString()]: produce((draft, action) => {
      draft.subscribers = action.payload
    }),

    [automailing.add.toString()]: produce((draft, action) => {
      console.log('automailing.add');
      // draft.filters = action.payload
    }),

    [automailing.remove.toString()]: produce((draft, action) => {
      console.log('automailing.remove');
      // draft.subscribers = action.payload
    }),
  },
  initialState
);
