import React, { memo, Fragment } from 'react';
import cx from 'classnames'
import { Handle } from 'react-flow-renderer';

export default memo((props) => {
  const {
    flowType,
    id,
    data,
    isActive,
    onCopyElem,
    onUnlinkElem,
    onRemoveElem,
  } = props

  const { items, text } = data

  const showText = (txt, placeholder, modifier, answers) => {
    if (txt && txt.trim().length) {
      return txt
    } else {
      return <div className={
        cx(
          `flow-block__placeholder flow-block__placeholder_${modifier}`,
          {
            'flow-block__placeholder_answer': answers && answers.length,
          }
        )
      }
      >{placeholder}</div>
    }
  }

  const showMessage = (msg) => {
    return (
      <div className={
        cx(
          'flow-block__message',
          {
            'flow-block__message_text': msg.text && msg.text.trim().length,
            'flow-block__message_buttons': msg.buttons && msg.buttons.length
          }
        )
      }
        key={msg.id}
        id={msg.id}
      >
        {showText(msg.text, 'Добавьте сюда текст сообщения', 'message')}
        {
          msg.buttons && msg.buttons.length ? <div className='flow-block__buttons'>
            {msg.buttons.map((btn) => {
              return (
                <div
                  className='flow-block__button'
                  data-id={btn.id}
                  key={btn.id}
                >
                  {btn.data ? btn.data.text : ''}
                  <Handle type="source" position="right" id={btn.id} style={{ background: '#00BAFF' }} />
                </div>
              )
            })}
          </div> : null
        }
      </div>
    )
  }

  const showQuestion = (quest) => {
    const answers = quest.data.readyAnswers ? quest.data.readyAnswers.split(',').map(answer => {
      return answer.trim().length ? answer.trim() : null
    }).filter(a => !!a) : []

    return (
      <Fragment key={quest.id}>
        <div className={
          cx(
            'flow-block__question',
            {
              'flow-block__question_text': quest.text && quest.text.trim().length,
              'flow-block__question_answers': quest.data.readyAnswers
            }
          )
        }
          key={quest.id}
          id={quest.id}
        >
          {showText(quest.text, 'Добавьте сюда вопрос для пользователя', 'question', answers)}
          {answers.length ? answers.map((answer, i) => {
            return (
              <div key={i} className="flow-block__answers">
                <div className="flow-block__answer">{answer}</div>
              </div>
            )
          }) : null}
        </div>
        <div className="flow-block__answerwait">
          Ожидается ответ пользователя (24 часа)
        </div>
      </Fragment>
    )
  }

  const showImage = (img) => {
    return (
      <Fragment key={img.id}>
        <img className='flow-block__img' src={img.preview_320} alt="" />
      </Fragment>
    )
  }

  const showFile = (file) => {
    return (
      <Fragment key={file.id}>
        <div className='flow-block__file'><div className='flow-block__filename'>{file.name}</div></div>
      </Fragment>
    )
  }

  return (
    <div className={cx(
      'flow-block',
      'flow-block_message',
      {
        'flow-block_active': isActive
      }
    )}>
      {flowType === 'bot' ? <Fragment>
        <div className='flow-block__copy' onClick={() => onCopyElem(id)} />
        <div className='flow-block__delete' onClick={() => onRemoveElem(id)} />
      </Fragment> : null}
      <div className='flow-block__title flow-block__title_message'>{text}</div>
      {flowType === 'bot' ? <Handle
        type="target"
        position="left"
        style={{ background: '#fff', top: 20, border: '1px solid #00BAFF' }}
        onConnect={({ source, target }) => onUnlinkElem(source, target)}
      /> : null}

      <div className='flow-block__messages'>
        {items && items.length ? items.map((item, i) => {
          switch (item.itemType) {
            case 'message':
              return showMessage(item)

            case 'question':
              return showQuestion(item)

            case 'image':
              return showImage(item)

            case 'file':
              return showFile(item)

            default:
              return null
          }
        }) : <div className="flow-block__placeholder">+&nbsp;{'Добавить контент'}</div>
        }
      </div>
      {flowType === 'bot' ? <div className="flow-block__nextstep">
        Следующий шаг
      <Handle
          type="source"
          position="right"
          id="output"
          style={{ bottom: 7, top: 'auto', border: '1px solid #00BAFF' }} />
      </div> : null}
    </div>
  );
});
