import { handleActions } from 'redux-actions';
import produce from 'immer'

import {
  modalActions,
} from '../actions'

const { modal } = modalActions

const initialState = {
  modal: '',
}

const log = (obj) => {
  console.log(JSON.parse(JSON.stringify(obj)))
}

export default handleActions(
  {
    [modal.open.toString()]: produce((draft, action) => {
      draft.modal = action.payload
    }),

    [modal.close.toString()]: produce((draft, action) => {
      draft.modal = ''
    }),
  },
  initialState
);
