import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux';
import fuzzysort from 'fuzzysort'
import moment from 'moment'
import { Redirect } from 'react-router-dom'

import ModalController from './ModalController'

import PageLayout from '../components/common/PageLayout'
import UsersTable from '../components/common/UsersTable'
import Filter from '../components/common/Filter'
import Menu from '../components/common/Menu'
import Loader from '../components/common/Loader'

import { settingsActions, modalActions } from '../actions/index'

import getCookie from '../const/getCookie'

moment.locale('ru')

const PREDICATES_MAP = {
  'is': '=',
  'is_not': '≠',
  'before': 'до',
  'after': 'после',
  'exact': 'соответствует',
  'contains': 'содержит',
  'does_not_contain': 'не содержит',
  'has_any_value': 'любое значение',
  'is_unknown': 'неизвестно',
}

let chats = []
class AudienceController extends Component {
  state = {
    value: '',
    selected: [],
  }

  componentDidMount() {
    const { basic, settings: { data }, getBasicData } = this.props
    if (!basic) {
      getBasicData()
    }

    if (data && data.chats) {
      this.setState({ items: data.chats })
    }
  }

  handleSelect = (isChecked, name, value) => {
    const { setSubscribers } = this.props

    if (isChecked) {
      this.setState({
        selected: [...this.state.selected, value]
      }, () => {
        setSubscribers(this.state.selected)
      });
    } else {
      this.setState({
        selected: this.state.selected.filter(item => item !== value)
      }, () => {
        setSubscribers(this.state.selected)
      });
    }
  };

  handleSelectAll = (isChecked, name, value) => {
    if (isChecked) {
      this.setState({ selected: chats.map(item => item.id) })
    } else {
      this.setState({ selected: [] })
    }
  }

  handleChange = (e) => {
    this.setState({ value: e.target.value })
  }

  handleAddRule = (rule) => {
    const { settings: { filters }, setFilters } = this.props
    setFilters({ ...filters, rules: [...filters.rules, rule] })
  }

  handleQuantor = () => {
    const { settings: { filters }, setFilters } = this.props
    if (filters.quantor === 'all') {
      setFilters({ ...filters, quantor: 'any' })
    } else {
      setFilters({ ...filters, quantor: 'all' })
    }
  }

  showAudience = () => {
    const { settings: { loading, data }, openModal } = this.props
    const { value, selected } = this.state

    if (loading.length) {
      return <Loader />
    }

    if (!data.chats.length) {
      return null
    }

    chats = data.chats

    if (value.length) {
      const filtered = fuzzysort.go(value, data.chats, {
        keys: ['name']
      })

      chats = filtered.map(i => i.obj)
    }

    return (
      <Fragment>
        <UsersTable
          items={chats}
          selected={selected}
          onSelect={this.handleSelect}
          onSelectAll={this.handleSelectAll}
          onOpenModal={openModal}
        />
      </Fragment>
    )
  }

  showFiltersList = () => {
    const { settings: { filters } } = this.props

    return (
      filters.rules.map((item, i) => {
        return (
          <div className="filter-list-item" key={i} >
            {item.label}&nbsp;
            {PREDICATES_MAP[item.predicate]}&nbsp;
            {Number.isInteger(item.argument) ? moment(item.argument).format('DD.MM.YYYY') : item.argument}
            <div className="filter-remove" onClick={() => {
              const { settings: { filters }, setFilters } = this.props
              setFilters({
                ...filters,
                rules: filters.rules.filter(rule => {
                  return !(rule.type === item.type && rule.predicate === item.predicate && rule.argument === item.argument)
                })
              })
            }}>×</div>
          </div>
        )
      })
    )
  }

  render() {
    const { settings: { data, filters }, addTag, removeTag } = this.props
    const { selected } = this.state

    const options = data.tags.map(tag => {
      return {
        label: tag,
        value: tag
      }
    })

    return (
      <Fragment>
        <PageLayout
          title="Аудитория"
        >
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Filter
              placeholder='Поиск пользователя по имени'
              options={options}
              onChange={this.handleChange}
              onAddRule={this.handleAddRule}
            />
            <div style={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              position: 'relative',
              width: '425px',
              height: '50px',
              whiteSpace: 'nowrap'
            }}>
              <span>Выбрано подписчиков {selected.length} из {data ? data.chats.length : 0}</span>
              <Menu
                placeholder='Массовые действия'
                options={options}
                onSubmit={(tags, action) => {
                  if (action === 'addtag') {
                    addTag(selected, tags)
                  }
                  if (action === 'removetag') {
                    removeTag(selected, tags)
                  }
                  this.setState({ selected: [] })
                }}
              />
            </div>

          </div>
          {filters.rules.length ?
            <div style={{ display: 'flex' }}>
              Подписчик соответствует: <div
                onClick={this.handleQuantor}
                style={{
                  display: 'inline-block',
                  color: '#00BAFF',
                  cursor: 'pointer',
                  borderBottom: '1px dashed #00BAFF',
                  margin: '0 10px',
                  height: '20px'
                }}
              >
                {filters.quantor === 'all' ? 'всем из следующих условий' : 'любому из следующих условий'}
              </div>
              {this.showFiltersList()}
            </div>
            : null
          }
          {this.showAudience()}
        </PageLayout>
        <ModalController />
      </Fragment>

    );
  }
}

const mapStateToProps = (state) => {
  const { basic, settings, loading, error } = state

  return {
    basic,
    settings,
    loading,
    error,
  };
};

const mapDispatchToProps = {
  getBasicData: settingsActions.basic.request,
  addTag: settingsActions.audience.tag.add,
  removeTag: settingsActions.audience.tag.remove,
  setFilters: settingsActions.audience.filters.set,
  setSubscribers: settingsActions.audience.set,
  setCurrentElem: settingsActions.currentelem.set,
  openModal: modalActions.modal.open,
  closeModal: modalActions.modal.close,
};

export default connect(mapStateToProps, mapDispatchToProps)(AudienceController)