import React, { useState, useRef, useEffect } from 'react';

import './FloatMenu.scss';

const FloatMenu = ({ title, coords, modifier, children, onClose }) => {
  const [top, setCoordsTop] = useState(coords.top);
  const menuRef = useRef(null);

  useEffect(() => {
    const menuHeight = menuRef.current.getBoundingClientRect().height
    if (+top + menuHeight > window.innerHeight) {
      setCoordsTop(window.innerHeight - menuHeight)
    }
  }, [top]);

  return (
    <div className={`constructor-floatmenu ${modifier ? `constructor-floatmenu_${modifier}` : ''}`}
      ref={menuRef}
      style={{ top: `${top}px`, left: `${coords.left}px`, bottom: `${coords.bottom}px` }}>
      <div className="constructor-floatmenu__close" onClick={onClose}>×</div>
      {title ? <div className="constructor-floatmenu__title">{title}</div> : null}
      <div className="constructor-floatmenu__content">{children}</div>
    </div>
  )
};

export default FloatMenu;