import React, { useState } from 'react';

import Button from '../../common/Button';
import Icon from '../../common/Icon';

import Modal from '../Modal'

export default function AddViberAccount({ onOpenModal, onCloseModal, onAddChannel }) {
  const [value, setValue] = useState('');

  return (
    <Modal
      title='Добавить аккаунт Viber'
      onBack={() => onOpenModal('addAccount')}
      onClose={onCloseModal}
    >
      <section className='account-addition'>
        <div className='account-addition-disclaimer'>
          <Icon
            name="viber"
            width={30}
            className='account-addition-type'
          />
          <span>Для добавления аккаунта Viber необходимо создать отдельного бота Telegram и ввести его токен в поле ниже:</span>
        </div>
        <input
          type="text"
          placeholder="Введите сюда Токен-ключ"
          className='modal-input'
          value={value}
          onChange={(e => setValue(e.target.value))}
        />

        <footer className='telegram-bot-add-form-footer'>
          <Button
            onClick={() => {
              onAddChannel(value, 'viber')
              onCloseModal()
            }}
          >
            Добавить бота Viber
      </Button>

          <span className='telegram-bot-add-more'>Подробнее о добавлении бота Viber</span>
        </footer>
        <section
          className='telegram-bot-add-instruction'
          data-opened={true}
        >
          <h3 className='telegram-bot-add-instruction-header'>Как создать бота и где взять токен ключ?</h3>
          <p>1. Перейдите в Viber Admin Panel и авторизуйтесь с помощью своего мобильного телефона.</p>
          <p>2.Перейдите в раздел правого меню “Создать бот”, далее заполните все поля справа и нажмите Create</p>
          <p>3. Всплывет окошко успешного создания бота с токеном вида <code className='telegram-bot-add-token-code'>4m25e782w64048f1-154cf82152c03c19-584bff1cka653c4b</code></p>
          <p>4. Скопируйте и вставьте токен в поле выше в данном окне.</p>
        </section>
      </section>
    </Modal>
  );
}
