import React, { useState, useRef, Fragment } from 'react';
import cx from 'classnames'
import fuzzysort from 'fuzzysort'
import OutsideClickHandler from 'react-outside-click-handler';

const NAMES_MAP = {
  addTag: 'Добавить метки',
  removeTag: 'Удалить метки',
}

const ElemMenuTagItem = ({
  item,
  options,
  onCopyItem,
  onRemoveItem,
  onMoveItem,
  onAddTag,
  onRemoveTag,
}) => {
  const [fieldList, toggleFieldList] = useState(false);
  const [searchValue, setSearchValue] = useState('');

  let filteredOptions = options

  if (searchValue.length && options.length) {
    const data = fuzzysort.go(searchValue, options, {
      keys: ['label']
    })

    filteredOptions = data.map(i => i.obj)
  }

  const showCreateMenu = () => {
    return (
      <div className="constructor-elemmenu__choice-create"
        onClick={() => {
          onAddTag(item.id, searchValue)
          setSearchValue('')
          toggleFieldList(false)
        }}
      >+&nbsp;Создать метку {searchValue}</div>
    )
  }

  return (
    <Fragment>
      <div className={
        cx(
          'constructor-elemmenu__label',
          `constructor-elemmenu__label_${item.itemType}`
        )
      }>{NAMES_MAP[item.itemType]}</div>
      <div className="constructor-elemmenu__action" key={item.id} >
        <div className="constructor-elemmenu__operations">
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_delete"
            onClick={() => onRemoveItem(item.id)}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_up"
            onClick={() => onMoveItem(item.id, 'up')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_down"
            onClick={() => onMoveItem(item.id, 'down')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_copy"
            onClick={() => onCopyItem(item.id, 0)}
          />
        </div>
        <OutsideClickHandler
          onOutsideClick={() => {
            toggleFieldList(false)
          }}
        >
          <div className="constructor-elemmenu__choice-select">
            <input
              type="text"
              placeholder='Введите название метки'
              className={
                cx(
                  'constructor-elemmenu__input',
                  { 'constructor-elemmenu__input_active': fieldList && searchValue.trim().length }
                )
              }
              onFocus={() => toggleFieldList(true)}
              value={searchValue}
              onChange={(e) => setSearchValue(e.target.value)}
              onKeyPress={(e) => {
                if (e.key === 'Enter') {
                  onAddTag(item.id, searchValue)
                  setSearchValue('')
                  toggleFieldList(false)
                  e.target.blur()
                }
              }}
            />
            {fieldList && searchValue.trim().length
              ? <div className="constructor-elemmenu__choice-list">
                {filteredOptions.length ? filteredOptions.map((field, i) => {
                  return (
                    <div key={i} className="constructor-elemmenu__choice-item"
                      onClick={(e) => {
                        onAddTag(item.id, field.label)
                        toggleFieldList(!fieldList)
                      }}
                    >{field.label}</div>
                  )
                }) : showCreateMenu()
                }
              </div>
              : null}
          </div>
        </OutsideClickHandler>

        {
          item.tags ? <div className='constructor-elemmenu__tags'>
            {item.tags.map((tag) => {
              return (
                <div className={
                  cx(
                    'constructor-elemmenu__tag',
                    `constructor-elemmenu__tag_${item.itemType}`
                  )
                } key={tag.id}>
                  #<span className='constructor-elemmenu__tag-txt'>{tag.text}</span>
                  <span className='constructor-elemmenu__tag-remove'
                    onClick={(e) => {
                      onRemoveTag(tag.id)
                    }}
                  >×</span>
                </div>
              )
            })}
          </div> : null
        }
      </div>
    </Fragment>
  )

};

export default ElemMenuTagItem;