import { handleActions, combineActions } from 'redux-actions';
import produce from 'immer'

import {
  constructorBotActions,
} from '../actions'

// console.log(JSON.parse(JSON.stringify(draft.elems)))

const log = (obj) => {
  console.log(JSON.parse(JSON.stringify(obj)))
}

const {
  elems,
  url,
  fields,
  button,
  answer,
  file,
  image,
  title,
  message,
  question,
  item,
  act,
  condition,
  delay,
  template,
  floatmenu,
  savebot,
} = constructorBotActions

const initialState = {
  elems: [],
  tags: [],
  fields: [
    { value: 'name', label: 'Имя', type: 'string' },
    { value: 'email', label: 'Email', type: 'email' },
    { value: 'phone', label: 'Телефон', type: 'phone' },
  ],
  elements: [], // send to backend
  currentElem: {
    type: null,
    data: {
      text: '',
      items: [],
      nextStep: null
    }
  },
  currentBtn: {
    data: {
      text: '',
      nextStep: null
    }
  },
  currentItem: null,
  floatMenu: {
    type: '',
    top: 0,
    bottom: 0,
    left: 0
  },
  removeElemWithoutConfirm: false,
  loading: false,
  saving: false,
  error: null,
}

const saveData = produce((draft) => {
  draft.elems.forEach((item, i) => {
    if (item.id === draft.currentElem.id) {
      draft.elems[i] = draft.currentElem
    }
  })

  draft.elements = draft.elems.map(elem => {
    const element = {
      id: elem.id,
    }

    if (elem.data) {
      element.type = elem.type
      element.position = elem.position
      element.sourcePosition = elem.sourcePosition
      element.data = elem.data

      if (elem.data.nextStep) {
        element.data.nextStepId = elem.data.nextStep.id
        delete element.data.nextStep
      }
      if (elem.data.nextIfStep) {
        element.data.nextIfStepId = elem.data.nextIfStep.id
        delete element.data.nextIfStep
      }
      if (elem.data.nextElseStep) {
        element.data.nextElseStepId = elem.data.nextElseStep.id
        delete element.data.nextElseStep
      }

      if (elem.data.items) {
        elem.data.items.forEach((item, i) => {
          if (item.buttons) {
            item.buttons.forEach((btn, b) => {
              if (btn.data.nextStep) {
                element.data.items[i].buttons[b].data.nextStepId = btn.data.nextStep.id
                if (btn.data.nextStep.url) {
                  element.data.items[i].buttons[b].data.nextStepUrl = btn.data.nextStep.url
                }
                delete element.data.items[i].buttons[b].data.nextStep
              }
            })
          }
        })
      }
    } else if (elem.type === 'input') {
      element.type = elem.type
      element.position = elem.position
    } else if (elem.type.includes('linkNode')) {
      element.source = elem.source
      element.target = elem.target
      element.type = 'linkNode'
    }

    element.timeAdd = Date.now()

    return element
  })

  draft.saving = true
})

const constructorReducer = handleActions(
  {
    [combineActions(
      elems.fetch.request.toString(),
    )]: produce((draft, action) => {
      draft.loading = true
    }),

    [elems.fetch.success.toString()]: produce((draft, action) => {
      draft.elems = action.payload
      draft.loading = false
    }),

    [elems.addlink.toString()]: produce((draft, action) => {
      const { id, msgId, linkId, type } = action.payload

      switch (type) {
        case 'message':
          draft.elems.push({
            id: id,
            type: 'messageNode',
            position: { x: draft.currentElem.position.x + 50, y: draft.currentElem.position.y + 50 },
            data: {
              text: `Сообщение ${draft.elems.filter(el => el.type === 'messageNode').length + 1}`,
              items: [
                {
                  id: msgId,
                  itemType: 'message',
                  text: ``,
                },
              ],
            },
          })
          break

        case 'action':
          draft.elems.push({
            id: id,
            type: 'actionNode',
            position: { x: draft.currentElem.position.x + 50, y: draft.currentElem.position.y + 50 },
            data: {
              text: `Событие ${draft.elems.filter(el => el.type === 'actionNode').length + 1}`,
              items: []
            },
          })
          break

        case 'condition':
          draft.elems.push({
            id: id,
            type: 'conditionNode',
            position: { x: draft.currentElem.position.x + 50, y: draft.currentElem.position.y + 50 },
            data: {
              text: `Условие ${draft.elems.filter(el => el.type === 'conditionNode').length + 1}`,
              items: []
            },
          })
          break

        case 'delay':
          draft.elems.push({
            id: id,
            type: 'delayNode',
            position: { x: draft.currentElem.position.x + 50, y: draft.currentElem.position.y + 50 },
            data: {
              text: `Задержка ${draft.elems.filter(el => el.type === 'delayNode').length + 1}`,
              value: '1',
              period: 'day',
              fromTo: {},
              exact: false,
              schedule: {}
            },
          })
          break

        default:
          break
      }

      draft.elems.push({
        id: linkId,
        type: `linkNode ${linkId}`,
        source: `${draft.currentElem.id}__${draft.currentBtn.id}`,
        target: id,
        arrowHeadType: 'arrow',
        label: '×',
        labelStyle: { fill: '#5A696F', fontWeight: 100 },
        labelBgPadding: [6, 0],
        labelBgBorderRadius: 5,
        labelBgStyle: {
          fillOpacity: 1, fill: '#ffffff',
          stroke: '#ebeef1', strokeWidth: '2px', strokeDasharray: '0 22 45 60'
        },
      })

      const nextStep = draft.elems.find(item => item.id === id)

      draft.currentElem.data.items.forEach((msg, m) => {
        if (msg.buttons && msg.buttons.length) {
          msg.buttons.forEach((btn, b) => {
            if (draft.currentBtn.id === btn.id) {
              draft.currentElem.data.items[m].buttons[b].data.nextStep = nextStep
              draft.currentBtn.data.nextStep = nextStep
            }
          })
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [url.add.toString()]: produce((draft, action) => {
      const { id, url } = action.payload
      draft.currentBtn.data.nextStep = { id, url }

      draft.currentElem.data.items.forEach((msg, m) => {
        if (msg.buttons && msg.buttons.length) {
          msg.buttons.forEach((btn, b) => {
            if (draft.currentBtn.id === btn.id) {
              draft.currentElem.data.items[m].buttons[b].data.nextStep = { id, url }
              draft.currentBtn.data.nextStep = { id, url }
            }
          })
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [url.remove.toString()]: produce((draft, action) => {
      draft.currentElem.data.items.forEach((msg, m) => {
        if (msg.buttons && msg.buttons.length) {
          msg.buttons.forEach((btn, b) => {
            if (draft.currentBtn.id === btn.id) {
              draft.currentElem.data.items[m].buttons[b].data.nextStep = null
              draft.currentElem.data.items[m].buttons[b].data.nextStepId = null
              draft.currentBtn.data.nextStep = null
              draft.currentBtn.data.nextStepId = null
            }
          })
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [elems.add.toString()]: produce((draft, action) => {
      const { msgId, actionId, conditionId, delayId, elemType, position } = action.payload

      switch (elemType) {
        case 'message':
          draft.elems.push({
            id: msgId,
            position,
            type: 'messageNode',
            data: {
              text: `Сообщение ${draft.elems.filter(el => el.type === 'messageNode').length + 1}`,
              items: [],
            },
          })
          break

        case 'action':
          draft.elems.push({
            id: actionId,
            position,
            type: 'actionNode',
            data: {
              text: `Событие ${draft.elems.filter(el => el.type === 'actionNode').length + 1}`,
              items: [],
            },
          })
          break

        case 'condition':
          draft.elems.push({
            id: conditionId,
            position,
            type: 'conditionNode',
            form: 'all',
            data: {
              text: `Условие ${draft.elems.filter(el => el.type === 'conditionNode').length + 1}`,
              items: [],
            },
          })
          break

        case 'delay':
          draft.elems.push({
            id: delayId,
            position,
            type: 'delayNode',
            data: {
              text: `Задержка ${draft.elems.filter(el => el.type === 'delayNode').length + 1}`,
              value: '1',
              period: { value: 'day', label: 'Дней' },
              fromTo: {
                fromHour: '00',
                toHour: '00',
                fromMinute: '00',
                toMinute: '00'
              },
              exact: false,
              schedule: {
                mon: false,
                tue: false,
                wed: false,
                thu: false,
                fri: false,
                sat: false,
                sun: false,
              }
            },
          })
          break

        default:
          break
      }

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [elems.remove.toString()]: produce((draft, action) => {
      let elemIds = draft.currentElem.id

      if (action.payload) {
        elemIds = action.payload.map(item => item.id)
      }

      draft.elems = draft.elems.filter(item => {
        const sourceId = item.source ? item.source.slice(0, 41) : null
        const isBtnLink = item.id.includes('btn_link')

        if (isBtnLink) {
          const btnId = item.source.slice(43)
          draft.elems.forEach((elem, i) => {
            if (elem.data && elem.data.items && elem.data.items.length) {
              elem.data.items.forEach((msg, m) => {
                if (msg.buttons && msg.buttons.length) {
                  msg.buttons.forEach((btn, b) => {
                    if (btn.id === btnId) {
                      draft.elems[i].data.items[m].buttons[b].data.nextStep = null
                      draft.elems[i].data.items[m].buttons[b].data.nextStepId = null
                    }
                  })
                }
              })
            }
          })
        }

        return (
          elemIds && !elemIds.includes(item.id)
          && !elemIds.includes(item.target)
          && !elemIds.includes(sourceId)
        )
      });

      draft.currentElem = {
        data: {}
      }

      draft.elems.forEach((item, i) => {
        if (item.id === draft.currentElem.id) {
          draft.elems[i] = draft.currentElem
        }
        if (item.data && item.data.nextStep && (elemIds.includes(item.data.nextStep.id) || elemIds === item.data.nextStep.id)) {
          draft.elems[i].data.nextStep = null
          draft.elems[i].data.nextStepId = null
        }
        if (item.data && item.data.nextStepId && (elemIds.includes(item.data.nextStepId) || elemIds === item.data.nextStepId)) {
          draft.elems[i].data.nextStep = null
          draft.elems[i].data.nextStepId = null
        }
        if (item.data && item.data.nextIfStep && (elemIds.includes(item.data.nextIfStep.id) || elemIds === item.data.nextIfStep.id)) {
          draft.elems[i].data.nextIfStep = null
          draft.elems[i].data.nextIfStepId = null
        }
        if (item.data && item.data.nextIfStepId && (elemIds.includes(item.data.nextIfStepId) || elemIds === item.data.nextIfStepId)) {
          draft.elems[i].data.nextIfStep = null
          draft.elems[i].data.nextIfStepId = null
        }
        if (item.data && item.data.nextElseStep && (elemIds.includes(item.data.nextElseStep.id) || elemIds === item.data.nextElseStep.id)) {
          draft.elems[i].data.nextElseStep = null
          draft.elems[i].data.nextElseStepId = null
        }
        if (item.data && item.data.nextElseStepId && (elemIds.includes(item.data.nextElseStepId) || elemIds === item.data.nextElseStepId)) {
          draft.elems[i].data.nextElseStep = null
          draft.elems[i].data.nextElseStepId = null
        }
        if (item.data && item.data.items && item.data.items.length) {
          item.data.items.forEach((msg, m) => {
            if (msg.buttons && msg.buttons.length) {
              msg.buttons.forEach((btn, b) => {
                if (btn.data.nextStep && (elemIds.includes(btn.data.nextStep.id) || elemIds === btn.data.nextStep.id)) {
                  draft.elems[i].data.items[m].buttons[b].data.nextStep = null
                  draft.elems[i].data.items[m].buttons[b].data.nextStepId = null
                }
                if (btn.data.nextStepId && (elemIds.includes(btn.data.nextStepId) === elemIds === btn.data.nextStepId)) {
                  draft.elems[i].data.items[m].buttons[b].data.nextStep = null
                  draft.elems[i].data.items[m].buttons[b].data.nextStepId = null
                }
              })
            }
          })
        }
      })

      draft.elements = draft.elems.map(elem => {
        elem.timeAdd = Date.now()

        return elem
      })
      draft.saving = true
    }),

    [elems.removeconfirm.toString()]: produce((draft, action) => {
      draft.removeElemWithoutConfirm = action.payload
    }),

    [elems.select.toString()]: produce((draft, action) => {
      draft.currentElem = action.payload
      if (!action.payload.type) {
        draft.floatMenu = {
          type: '',
          top: 0,
          bottom: 0,
          left: 0
        }
      }
    }),

    [elems.copy.toString()]: produce((draft, action) => {
      const { id, elemId } = action.payload
      const elem = draft.elems.find(item => item.id === elemId)

      const newElem = produce(elem, newElem => {
        newElem.id = id
        newElem.position.x = draft.currentElem.position.x + 50
        newElem.position.y = draft.currentElem.position.y + 50
      })

      draft.elems.push(newElem)
    }),

    [elems.link.toString()]: produce((draft, action) => {
      const { id, sourceId, targetId } = action.payload
      draft.elems = draft.elems.filter(item => item.source !== sourceId)

      if (targetId) {
        draft.elems.push({
          id: id,
          type: `linkNode ${id}`,
          source: sourceId,
          target: targetId,
          arrowHeadType: 'arrow',
          label: '×',
          labelStyle: { fill: '#5A696F', fontWeight: 100 },
          labelBgPadding: [6, 0],
          labelBgBorderRadius: 5,
          labelBgStyle: {
            fillOpacity: 1, fill: '#ffffff',
            stroke: '#ebeef1', strokeWidth: '2px', strokeDasharray: '0 22 45 60'
          },
        })
      }

      const nextStep = draft.elems.find(item => item.id === targetId)

      if (sourceId.includes('__btn') && draft.currentElem.data?.items) {
        draft.currentElem.data.items.forEach((msg, m) => {
          if (msg.buttons && msg.buttons.length) {
            msg.buttons.forEach((btn, b) => {
              if (sourceId.includes(btn.id)) {
                draft.currentElem.data.items[m].buttons[b].data.nextStep = nextStep
                draft.currentElem.data.items[m].buttons[b].data.nextStepId = nextStep.id

                if (draft.currentBtn.data) {
                  draft.currentBtn.data.nextStep = nextStep
                  draft.currentBtn.data.nextStepId = nextStep.id
                }
              }
            })
          }
        })
      } else {
        if (sourceId.includes('__output')) {
          if (draft.elems.find(item => item.id === sourceId.slice(0, -8)).data) {
            draft.elems.find(item => item.id === sourceId.slice(0, -8)).data.nextStep = nextStep

            if (draft.currentElem.id === sourceId.slice(0, -8)) {
              draft.currentElem.data.nextStep = nextStep
            }
          }
        }

        if (sourceId.includes('__if')) {
          draft.elems.find(item => item.id === sourceId.slice(0, -4)).data.nextIfStep = nextStep

          if (draft.currentElem.id === sourceId.slice(0, -4)) {
            draft.currentElem.data.nextIfStep = nextStep
          }
        }

        if (sourceId.includes('__else')) {
          draft.elems.find(item => item.id === sourceId.slice(0, -6)).data.nextElseStep = nextStep

          if (draft.currentElem.id === sourceId.slice(0, -6)) {
            draft.currentElem.data.nextElseStep = nextStep
          }
        }
      }

      draft.elements = draft.elems.map(elem => {
        const element = {
          id: elem.id,
        }
        if (elem.type.includes('linkNode')) {
          element.source = elem.source
          element.target = elem.target
          element.type = 'linkNode'
          element.timeAdd = Date.now()

          return element
        } else {
          elem.timeAdd = Date.now()
          return elem
        }
      })
      draft.saving = true
    }),

    [elems.unlink.toString()]: produce((draft, action) => {
      const { sourceId, targetId } = action.payload

      draft.elems = draft.elems.filter(item => !(item.source === sourceId && item.target === targetId))

      if (sourceId.includes('__btn') && draft.currentElem.data?.items) {
        if (draft.currentElem.data.items) {
          draft.currentElem.data.items.forEach((msg, m) => {
            if (msg.buttons && msg.buttons.length) {
              msg.buttons.forEach((btn, b) => {
                if (draft.currentBtn.id === btn.id) {
                  draft.currentElem.data.items[m].buttons[b].data.nextStep = null
                  draft.currentElem.data.items[m].buttons[b].data.nextStepId = null
                  draft.currentBtn.data.nextStep = null
                  draft.currentBtn.data.nextStepId = null
                }
              })
            }
          })
        }
      } else {
        if (sourceId.includes('__output')) {
          if (draft.elems.find(item => item.id === sourceId.slice(0, -8)).data) {
            draft.elems.find(item => item.id === sourceId.slice(0, -8)).data.nextStep = null

            if (draft.currentElem.id === sourceId.slice(0, -8)) {
              draft.currentElem.data.nextStep = null
            }
          }
        }

        if (sourceId.includes('__if')) {
          draft.elems.find(item => item.id === sourceId.slice(0, -4)).data.nextIfStep = null

          if (draft.currentElem.id === sourceId.slice(0, -4)) {
            draft.currentElem.data.nextIfStep = null
          }
        }

        if (sourceId.includes('__else')) {
          draft.elems.find(item => item.id === sourceId.slice(0, -6)).data.nextElseStep = null

          if (draft.currentElem.id === sourceId.slice(0, -6)) {
            draft.currentElem.data.nextElseStep = null
          }
        }
      }

      draft.elements = draft.elems.map(elem => {
        elem.timeAdd = Date.now()

        return elem
      })

      draft.saving = true
    }),

    [elems.coords.toString()]: produce((draft, action) => {
      // draft.currentElem = action.payload
      draft.elems.forEach((item, i) => {
        if (item.id === action.payload.id) {
          draft.elems[i] = action.payload
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [fields.create.toString()]: produce((draft, action) => {
      draft.fields.push(action.payload)
    }),

    [button.select.toString()]: produce((draft, action) => {
      draft.currentBtn = action.payload
    }),

    [button.add.toString()]: produce((draft, action) => {
      const { msgId, id } = action.payload

      draft.currentElem.data.items.forEach((item, i) => {
        if (item.id === msgId) {
          if (draft.currentElem.data.items[i].buttons) {
            draft.currentElem.data.items[i].buttons.push({
              id,
              data: {
                text: `Кнопка ${draft.currentElem.data.items[i].buttons.length + 1}`
              }
            })
          } else {
            draft.currentElem.data.items[i].buttons = [{
              id,
              data: {
                text: `Кнопка ${draft.currentElem.data.items[i].buttons.length + 1}`
              }
            }]
          }
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [button.remove.toString()]: produce((draft, action) => {
      draft.currentElem.data.items.forEach((item, i) => {
        if (item.id === action.payload) {
          draft.currentElem.data.items[i].buttons =
            draft.currentElem.data.items[i].buttons.filter(btn => btn.id !== draft.currentBtn.id)
          draft.currentBtn = { text: '' }
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [button.rename.toString()]: produce((draft, action) => {
      draft.currentElem.data.items.forEach((msg, m) => {
        if (msg.buttons && msg.buttons.length) {
          msg.buttons.forEach((btn, b) => {
            if (draft.currentBtn.id === btn.id) {
              draft.currentElem.data.items[m].buttons[b].data.text = action.payload
              draft.currentBtn.data.text = action.payload
            }
          })
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [answer.add.toString()]: produce((draft, action) => {
      draft.currentElem.data.items.forEach((quest, q) => {
        if (draft.currentItem === quest.id) {
          draft.currentElem.data.items[q].data = action.payload
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [answer.remove.toString()]: produce((draft, action) => {
      draft.currentElem.data.items.forEach((quest, q) => {
        if (quest.id === action.payload) {
          draft.currentElem.data.items[q].data = {}
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [title.edit.toString()]: produce((draft, action) => {
      draft.currentElem.data.edit = action.payload
    }),

    [title.change.toString()]: produce((draft, action) => {
      draft.currentElem.data.text = action.payload

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [message.add.toString()]: produce((draft, action) => {
      const { id } = action.payload

      draft.currentElem.data.items.push({
        id,
        itemType: 'message',
        text: ``,
        buttons: []
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [question.add.toString()]: produce((draft, action) => {
      const { id } = action.payload

      draft.currentElem.data.items.push({
        id,
        itemType: 'question',
        text: ``,
        data: {}
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [item.select.toString()]: produce((draft, action) => {
      draft.currentItem = action.payload

      draft.elems.forEach((item, i) => {
        if (item.id === draft.currentElem.id) {
          draft.currentElem.data.items = draft.elems[i].data.items
        }
      })
    }),

    [item.change.toString()]: produce((draft, action) => {
      const { itemId, txt } = action.payload
      draft.currentElem.data.items.forEach((item, i) => {
        if (item.id === itemId) {
          draft.currentElem.data.items[i].text = txt
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [file.upload.success.toString()]: produce((draft, action) => {
      const { id, file } = action.payload

      draft.currentElem.data.items.push({
        id,
        itemType: 'file',
        ...file
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [file.remove.toString()]: produce((draft, action) => {
      draft.currentElem.data.files = draft.currentElem.data.files.filter((item, i) => i !== action.payload)

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [image.upload.success.toString()]: produce((draft, action) => {
      const { id, img } = action.payload

      draft.currentElem.data.items.push({
        id,
        itemType: 'image',
        ...img
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [image.remove.toString()]: produce((draft, action) => {
      draft.currentElem.data.items = draft.currentElem.data.items.filter((img, i) => img.id !== action.payload)

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [item.copy.toString()]: produce((draft, action) => {
      const { id, itemId, buttons } = action.payload
      const item = draft.currentElem.data.items.find((item, i) => item.id === itemId)

      let itemButtons = []

      if (item.buttons) {
        itemButtons = item.buttons.map((btn, i) => {
          return {
            ...btn,
            id: buttons[i]
          }
        })
      }

      draft.currentElem.data.items.push({ ...item, id, buttons: itemButtons })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [item.remove.toString()]: produce((draft, action) => {
      draft.currentElem.data.items = draft.currentElem.data.items.filter((item, i) => item.id !== action.payload)

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [item.move.toString()]: produce((draft, action) => {
      const { itemId, direction } = action.payload
      const { items } = draft.currentElem.data
      const shift = direction === 'up' ? -1 : 1
      const indices = items.map(item => item.id)
      const index = indices.indexOf(itemId);
      const newIndex = index + shift;

      if (newIndex < 0 || newIndex === indices.length) {
        return
      }

      const sorted = [index, newIndex].sort((a, b) => a - b)

      items.splice(sorted[0], 2, items[sorted[1]], items[sorted[0]])

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [act.add.toString()]: produce((draft, action) => {
      const { id, actionType } = action.payload

      switch (actionType) {
        case 'addTag':
          draft.currentElem.data.items.push({
            id,
            itemType: actionType,
            text: 'Добавить метки',
            tags: []
          })
          break

        case 'removeTag':
          draft.currentElem.data.items.push({
            id,
            itemType: actionType,
            text: 'Удалить метки',
            tags: []
          })
          break

        case 'addField':
          draft.currentElem.data.items.push({
            id,
            itemType: actionType,
            text: 'Установить пользовательское поле',
            data: {
              field: '',
              value: ''
            }
          })
          break

        case 'removeField':
          draft.currentElem.data.items.push({
            id,
            itemType: actionType,
            text: 'Очистить пользовательское поле',
            data: {
              field: '',
              value: ''
            }
          })
          break

        case 'increaseField':
          draft.currentElem.data.items.push({
            id,
            itemType: actionType,
            text: 'Увеличить числовое значение пользовательского поля',
            data: {
              field: '',
              value: '',
              type: 'increase'
            }
          })
          break

        case 'decreaseField':
          draft.currentElem.data.items.push({
            id,
            itemType: actionType,
            text: 'Уменьшить числовое значение пользовательского поля',
            data: {
              field: '',
              value: '',
              type: 'decrease'
            }
          })
          break

        case 'sendEmail':
          draft.currentElem.data.items.push({
            id,
            itemType: actionType,
            text: 'Отправить данные пользователя на email',
            emails: []
          })
          break

        case 'subscribe':
        case 'unsubscribe':
          draft.currentElem.data.items.push({
            id,
            itemType: actionType,
          })
          break

        default:
          break
      }

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [act.tag.add.toString()]: produce((draft, action) => {
      const { id, itemId, text } = action.payload

      if (!draft.tags.find(tag => tag.label === text)) {
        draft.tags.push({ label: text, value: id })
      }


      draft.currentElem.data.items.forEach((item, i) => {
        if (item.id === itemId) {
          draft.currentElem.data.items[i].tags.push({
            id,
            text
          })
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [act.tag.remove.toString()]: produce((draft, action) => {
      draft.currentElem.data.items.forEach((item, i) => {
        if (item.tags) {
          draft.currentElem.data.items[i].tags = item.tags.filter(tag => tag.id !== action.payload)
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [act.field.add.toString()]: produce((draft, action) => {
      const { itemId, field } = action.payload

      draft.currentElem.data.items.forEach((item, i) => {
        if (item.id === itemId) {
          draft.currentElem.data.items[i].data = {
            field,
            value: ''
          }
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [act.field.change.toString()]: produce((draft, action) => {
      const { itemId, fieldValue, type } = action.payload

      draft.currentElem.data.items.forEach((item, i) => {
        if (item.id === itemId) {
          draft.currentElem.data.items[i].data.value = fieldValue

          if (type) {
            draft.currentElem.data.items[i].data.type = type
          }
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [act.email.add.toString()]: produce((draft, action) => {
      const { id, itemId, text } = action.payload

      draft.currentElem.data.items.forEach((item, i) => {
        if (item.id === itemId) {
          draft.currentElem.data.items[i].emails.push({
            id,
            text
          })
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [act.email.remove.toString()]: produce((draft, action) => {
      draft.currentElem.data.items.forEach((item, i) => {
        if (item.emails) {
          draft.currentElem.data.items[i].emails = item.emails.filter(email => email.id !== action.payload)
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [template.set.toString()]: produce((draft, action) => {
      draft.currentElem.data.items.forEach((item, i) => {
        if (draft.currentItem === item.id) {
          draft.currentElem.data.items[i].template = action.payload
        }
      })

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [condition.form.toString()]: produce((draft, action) => {
      draft.currentElem.form = action.payload

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [condition.item.add.toString()]: produce((draft, action) => {
      const { id, conditionType, conditionOptions } = action.payload

      let condition = null
      let value = null

      switch (conditionOptions.condition) {
        case 'is':
          condition = '='
          break

        case 'is_not':
          condition = '≠'
          break

        case 'contains':
          condition = 'содержит'
          break

        case 'is_unknown':
          condition = 'содержит'
          value = 'неизвестное значение'
          break

        case 'has_any_value':
          condition = 'содержит'
          value = 'любое значение'
          break

        case 'does_not_contain':
          condition = 'не содержит'
          break

        case 'step':
          condition = ''
          break

        default:
          break
      }

      if (conditionOptions.condition === 'is' ||
        conditionOptions.condition === 'is_not' ||
        conditionOptions.condition === 'contains' ||
        conditionOptions.condition === 'does_not_contain' ||
        conditionOptions.condition === 'is_unknown' ||
        conditionOptions.condition === 'has_any_value'
      ) {
        draft.currentElem.data.items.push({
          id,
          itemType: conditionType,
          condition,
          key: conditionOptions.key,
          value: value ? value : conditionOptions.value
        })
      }

      if (conditionOptions.condition === 'before') {
        const { beforeDate, beforeValue, fromTo: { beforeHour, beforeMinute } } = conditionOptions

        if (beforeValue === 'date') {
          draft.currentElem.data.items.push({
            id,
            itemType: conditionType,
            condition: '=',
            key: 'Дата',
            fromTo: conditionOptions.fromTo,
            value: `до ${beforeDate}`
          })
        }
        if (beforeValue === 'time') {
          draft.currentElem.data.items.push({
            id,
            itemType: conditionType,
            condition: '=',
            key: 'Время',
            fromTo: conditionOptions.fromTo,
            value: `до ${beforeHour}:${beforeMinute}`
          })
        }

      }

      if (conditionOptions.condition === 'after') {
        const { afterDate, afterValue, fromTo: { afterHour, afterMinute } } = conditionOptions

        if (afterValue === 'date') {
          draft.currentElem.data.items.push({
            id,
            itemType: conditionType,
            condition: '=',
            key: 'Дата',
            fromTo: conditionOptions.fromTo,
            value: `после ${afterDate}`
          })
        }
        if (afterValue === 'time') {
          draft.currentElem.data.items.push({
            id,
            itemType: conditionType,
            condition: '=',
            key: 'Время',
            fromTo: conditionOptions.fromTo,
            value: `после ${afterHour}:${afterMinute}`
          })
        }
      }

      if (conditionOptions.condition === 'exact') {
        const { exactDate } = conditionOptions
        draft.currentElem.data.items.push({
          id,
          itemType: conditionType,
          condition: '=',
          key: 'Точная дата',
          value: `${exactDate}`
        })
      }

      if (conditionOptions.condition === 'schedule') {
        const { schedule } = conditionOptions
        const days = {
          mon: 'Пн.',
          tue: 'Вт.',
          wed: 'Ср.',
          thu: 'Чт.',
          fri: 'Пт.',
          sat: 'Сб.',
          sun: 'Вс.',
        };

        const activeSchedule = Object.keys(schedule).map(day => {
          if (schedule[day]) {
            return `${days[day]}`
          }
          return null
        }).filter(item => !!item)

        draft.currentElem.data.items.push({
          id,
          itemType: conditionType,
          condition: ':',
          key: 'Дни',
          value: `${activeSchedule}`
        })
      }

      if (conditionOptions.condition === 'step') {
        const { key, value: { label, value } } = conditionOptions
        draft.currentElem.data.items.push({
          id,
          itemType: conditionType,
          condition: '',
          step: value,
          key,
          value: label
        })
      }

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [condition.item.remove.toString()]: produce((draft, action) => {
      draft.currentElem.data.items = draft.currentElem.data.items.filter(item => item.id !== action.payload)

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [delay.value.toString()]: produce((draft, action) => {

      if (!isNaN(action.payload)) {
        draft.currentElem.data.value = action.payload

        const { elems, elements, saving } = saveData(draft)

        draft.elems = elems
        draft.elements = elements
        draft.saving = saving
      }
    }),

    [delay.period.toString()]: produce((draft, action) => {
      draft.currentElem.data.period = action.payload

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [delay.exact.toString()]: produce((draft, action) => {
      draft.currentElem.data.exact = !draft.currentElem.data.exact

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [delay.fromto.toString()]: produce((draft, action) => {
      const { field, value } = action.payload

      if (!isNaN(value)) {
        draft.currentElem.data.fromTo[field] = value

        const { elems, elements, saving } = saveData(draft)

        draft.elems = elems
        draft.elements = elements
        draft.saving = saving
      }
    }),

    [delay.schedule.toString()]: produce((draft, action) => {
      draft.currentElem.data.schedule[action.payload] = !draft.currentElem.data.schedule[action.payload]

      const { elems, elements, saving } = saveData(draft)

      draft.elems = elems
      draft.elements = elements
      draft.saving = saving
    }),

    [floatmenu.open.toString()]: produce((draft, action) => {
      draft.floatMenu = action.payload
    }),

    [floatmenu.close.toString()]: produce((draft, action) => {
      draft.floatMenu = {
        type: '',
        top: 0,
        bottom: 0,
        left: 0
      }
    }),

    [savebot.success.toString()]: produce((draft, action) => {
      draft.saving = false
    }),
  },
  initialState
);

export default constructorReducer