import React, { useState } from 'react';

import Modal from '../Modal'

import Button from '../../common/Button'

import './AddAutomailingModal.scss';

const AddAutomailingModal = ({ modal, onAddAutomailing, onCloseModal }) => {
  const [title, setTitle] = useState('');

  return (
    <Modal
      title='Создание авторассылки'
      onClose={onCloseModal}
    >
      <div className="modal-content">
        <input
          className='modal-input'
          placeholder="Введите название авторассылки"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
      </div>

      <div className="modal-footer" >
        <div className="modal-buttons">
          <Button modifier='cancel' onClick={onCloseModal}>Отмена</Button>
          <Button onClick={() => {
            onCloseModal()
            onAddAutomailing(title)
          }}>Создать</Button>
        </div>
      </div>
    </Modal>
  );
};

export default AddAutomailingModal;