import React, { Fragment } from 'react';

import ElemMenuTagItem from './ElemMenuTagItem'
import ElemMenuFieldItem from './ElemMenuFieldItem'
import ElemMenuEmailItem from './ElemMenuEmailItem'
import ElemMenuSubscribeItem from './ElemMenuSubscribeItem'

const ElemMenuActions = ({
  flowType,
  tags,
  fields,
  currentElem,
  onCopyItem,
  onRemoveItem,
  onMoveItem,
  onAddTag,
  onRemoveTag,
  onAddField,
  onChangeField,
  onCreateField,
  onAddEmail,
  onRemoveEmail,
  handleFloatMenu
}) => {

  const showContent = () => {
    const { items } = currentElem.data
    if (!items.length) {
      return null
    }

    return items.map((item, i) => {
      switch (item.itemType) {
        case 'addTag':
        case 'removeTag':
          return (
            <ElemMenuTagItem
              key={i}
              item={item}
              options={tags}
              currentElem={currentElem}
              onCopyItem={onCopyItem}
              onRemoveItem={onRemoveItem}
              onMoveItem={onMoveItem}
              onAddTag={onAddTag}
              onRemoveTag={onRemoveTag}
            />
          )

        case 'addField':
        case 'removeField':
        case 'increaseField':
        case 'decreaseField':
          return <ElemMenuFieldItem
            key={i}
            item={item}
            fields={fields}
            currentElem={currentElem}
            onCopyItem={onCopyItem}
            onRemoveItem={onRemoveItem}
            onMoveItem={onMoveItem}
            onAddField={onAddField}
            onChangeField={onChangeField}
            onCreateField={onCreateField}
          />

        case 'sendEmail':
          return <ElemMenuEmailItem
            key={i}
            item={item}
            currentElem={currentElem}
            onCopyItem={onCopyItem}
            onRemoveItem={onRemoveItem}
            onMoveItem={onMoveItem}
            onAddEmail={onAddEmail}
            onRemoveEmail={onRemoveEmail}
          />

        case 'subscribe':
        case 'unsubscribe':
          return <ElemMenuSubscribeItem
            key={i}
            item={item}
            currentElem={currentElem}
            onCopyItem={onCopyItem}
            onRemoveItem={onRemoveItem}
            onMoveItem={onMoveItem}
          />

        default:
          return null
      }
    })
  }

  return (
    <Fragment>
      {showContent()}
      <div
        className="constructor-elemmenu__add constructor-elemmenu__add_action"
        onClick={(e) => handleFloatMenu(e, 'addAction')}
      >+&nbsp;{'Добавить событие'}</div>
      {flowType === 'bot' ? 'Следующий шаг после того, как произойдет событие:' : null}
    </Fragment>
  )

};

export default ElemMenuActions;