import React, { Component } from 'react'
import { connect } from 'react-redux';

import { constructorBotActions } from '../../actions/index'

import ElemMenuActions from '../../components/constructor/ElemMenu/action/ElemMenuActions'
import Loader from '../../components/common/Loader'
class ElemMenuBotActionsController extends Component {

  handleFloatMenu = (e, floatMenuType) => {
    const { openFloatMenu } = this.props
    openFloatMenu(
      e.target.getBoundingClientRect().top,
      e.target.closest('.constructor-elemmenu').getBoundingClientRect().left,
      'auto',
      floatMenuType
    )
  }

  render() {
    const {
      constructorBot: {
        tags,
        fields,
        currentElem,
      },
      settings,
      changeItem,
      copyItem,
      removeItem,
      moveItem,
      addTag,
      removeTag,
      addField,
      changeField,
      createField,
      addEmail,
      removeEmail,
    } = this.props

    if (!currentElem.id || currentElem.id.startsWith('link_')) {
      return null
    }

    if (settings.loading.length) {
      return <Loader />
    }

    return (
      <ElemMenuActions
        flowType='bot'
        tags={tags}
        fields={fields}
        currentElem={currentElem}
        onChangeItem={changeItem}
        onCopyItem={copyItem}
        onRemoveItem={removeItem}
        onMoveItem={moveItem}
        onAddTag={addTag}
        onRemoveTag={removeTag}
        onAddField={addField}
        onChangeField={changeField}
        onCreateField={createField}
        onAddEmail={addEmail}
        onRemoveEmail={removeEmail}
        handleFloatMenu={this.handleFloatMenu}
      />
    )
  }
}

const mapStateToProps = (state) => {
  const { constructorBot, settings, error } = state

  return {
    constructorBot,
    settings,
    error
  };
};

const mapDispatchToProps = {
  openFloatMenu: constructorBotActions.floatmenu.open,
  copyItem: constructorBotActions.item.copy,
  removeItem: constructorBotActions.item.remove,
  moveItem: constructorBotActions.item.move,
  addTag: constructorBotActions.act.tag.add,
  removeTag: constructorBotActions.act.tag.remove,
  addField: constructorBotActions.act.field.add,
  changeField: constructorBotActions.act.field.change,
  addEmail: constructorBotActions.act.email.add,
  removeEmail: constructorBotActions.act.email.remove,
  createField: constructorBotActions.fields.create,
};

export default connect(mapStateToProps, mapDispatchToProps)(ElemMenuBotActionsController)