import React, { Component } from 'react'
import { connect } from 'react-redux';
import { authActions } from '../actions/index'

import { domainPattern } from '../const/validate-patterns'
import { getDomain } from '../const/urlHelper'

import {
  Link, Redirect
} from 'react-router-dom'

class LoginController extends Component {
  state = {
    domain: '',
    password: '',
    domainError: '',
    passwordError: '',
  }

  componentWillUnmount() {
    const { authDrop } = this.props
    authDrop()
  }

  handleSubmit = () => {
    const { authLogin } = this.props
    const { domain, password } = this.state

    if (!domainPattern.test(domain)) {
      this.setState({ domainError: 'Укажите корректный домен. Разрешены только символы (a-z) и цифры (0-9)' })
      return
    }

    if (!password.length) {
      this.setState({ passwordError: 'пароль не может быть пустым' })
      return
    }

    authLogin(domain, password)
  }


  render() {
    const { auth } = this.props
    const { domain, password, domainError, passwordError } = this.state

    if (auth.loading) {
      return <h2>Loading...</h2>
    }

    if (auth.sessionId) {
      document.cookie = `sessionId=${auth.sessionId}; domain=${getDomain()}`
      document.cookie = `subdomain=${auth.domain}; domain=${getDomain()}`
      document.cookie = `sessionId=${auth.sessionId}; domain=localhost`
      document.cookie = `subdomain=${auth.domain}; domain=localhost`
      window.location = `http://${auth.domain}.${getDomain()}/settings?sessionId=${auth.sessionId}`
      // window.location = `/settings?sessionId=${auth.sessionId}`
    }

    return (
      <div className="auth__block">
        <Link className="auth__back" to={`/`}>×</Link>
        <div className="auth__title">Вход</div>
        <div className="auth__field auth__field_domain">
          <div className="auth__label">Поддомен</div>
          <input
            value={domain}
            onChange={(e) => this.setState({ domain: e.target.value, domainError: '' })}
            type="text"
            className="auth__input"
            placeholder='укажите домен'
          />
          <div className="auth__error">{auth.error ? auth.error.domain : domainError}</div>
        </div>
        <div className="auth__field">
          <div className="auth__label">Пароль</div>
          <input
            value={password}
            onChange={(e) => this.setState({ password: e.target.value, passwordError: '' })}
            type="password"
            className="auth__input"
            placeholder='укажите пароль'
          />
          <div className="auth__error">{auth.error ? auth.error.password : passwordError}</div>
          <div className="auth__error">{auth.error ? auth.error.common : null}</div>
        </div>

        <div className="auth__button"
          onClick={this.handleSubmit}
        >Войти</div>
        <div className="auth__info">
          <Link to={`/forgot-password`}>Забыли пароль?</Link>
        </div>
        <div className="auth__footer">
          <div className="auth__text">Еще нет аккаунта?</div>
          <a href={`http://auth.${getDomain()}/signup`}>Зарегистрироваться</a>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  const { auth } = state

  return {
    auth,
  };
};

const mapDispatchToProps = {
  authLogin: authActions.auth.login.request,
  authDrop: authActions.auth.drop,
};

export default connect(mapStateToProps, mapDispatchToProps)(LoginController)