import React from 'react';
import cx from 'classnames';

export default function DataTableRow({
  isHead = false,
  isDisabled = false,
  children,
  className,
  ...rest
}) {

  const componentClasses = cx({
    'table-row': !isHead,
    'table-head-row': isHead,
    'is-disabled': isDisabled,
    [className]: !!className,
  });

  return (
    <div className={componentClasses} {...rest}>
      { children}
    </div>
  );
}
