import React, { useState, Fragment } from 'react';
import OutsideClickHandler from 'react-outside-click-handler';
import cx from 'classnames'
import FloatMenu from '../FloatMenu'

const ElemMenuButtonsNextStep = ({
  flowType,
  elems,
  currentElem,
  currentBtn,
  floatMenu,
  top,
  left,
  bottom,
  onAddLinkElem,
  onLinkElem,
  onUnlinkElem,
  onRenameBtn,
  onAddUrl,
  onRemoveUrl,
  onClose
}) => {
  const [type, setType] = useState('');
  const [url, setUrl] = useState('');
  const [urlError, setUrlError] = useState('');

  const TYPES = {
    message: 'Сообщение',
    action: 'Событие',
    condition: 'Условие',
    delay: 'Задержка',
  }

  let nodes = elems.filter(item => {
    return item.data && item.type !== 'input' && item.id !== currentElem.id
  })

  if (floatMenu !== 'buttonNextStep') {
    return null
  }

  const showButtonMenuHeader = () => {
    if (type === 'nextstep') {
      return null
    }

    return (
      <Fragment>
        <div className='constructor-elemmenu__block constructor-elemmenu__block_title'>
          <div className="constructor-elemmenu__label constructor-elemmenu__label_title">Название кнопки</div>
          <input type="text"
            className="constructor-elemmenu__input"
            value={currentBtn.data.text}
            onChange={(e) => onRenameBtn(e.target.value)}
          />
        </div>
        <div className='constructor-elemmenu__block'>
          <div className="constructor-elemmenu__label constructor-elemmenu__label_choice">После нажатия на кнопку</div>
        </div>
      </Fragment>
    )

  }

  const showButtonMenuContent = () => {
    let activeBtn = null
    currentElem.data.items.forEach(item => {
      if (item.buttons) {
        item.buttons.forEach(btn => {
          if (btn.id === currentBtn.id) {
            activeBtn = btn
          }
        })
      }
    })

    if (type && type === 'nextstep') {
      return (
        <div className="constructor-elemmenu__nextstep-menu">
          {nodes.map(item => {
            return (
              <div key={item.id}
                className={
                  cx(
                    'constructor-elemmenu__nextstep-item',
                    `constructor-elemmenu__nextstep-item_${item.type.slice(0, -4)}`,
                  )
                }
                onClick={() => {
                  onLinkElem(`${currentElem.id}__${currentBtn.id}`, item.id, true)
                  setType(null)
                  onClose()
                }}
              >{item.data.text}</div>
            )
          })}
        </div>
      )
    }

    if (type && type === 'url') {
      return (
        <Fragment>
          <div className={
            cx(
              'constructor-elemmenu__nextelem',
              'constructor-elemmenu__nextelem_button',
              `constructor-elemmenu__nextelem_${type}`,
            )
          }>
            <div className="constructor-elemmenu__nextelem-close"
              onClick={() => setType(null)}
            />
            <div className="constructor-elemmenu__nextelem-text">Открыть url</div>
          </div>
          <div className="constructor-elemmenu__url">
            <div className="constructor-elemmenu__label">Ссылка (url)</div>
            <input
              type="text"
              placeholder='Введите URL'
              className="constructor-elemmenu__input"
              value={url}
              onChange={(e => {
                setUrl(e.target.value)
                setUrlError('')
              })}
            />
            {urlError ? <div className="constructor-elemmenu__url-error">{urlError}</div> : null}
          </div>
        </Fragment>
      )
    }

    if (type) {
      return (
        <div className={
          cx(
            'constructor-elemmenu__nextelem',
            'constructor-elemmenu__nextelem_button',
            `constructor-elemmenu__nextelem_${type}`,
          )
        }>
          <div className="constructor-elemmenu__nextelem-close"
            onClick={() => setType(null)}
          />
          <div className="constructor-elemmenu__nextelem-text">
            {TYPES[type]} {elems.filter(el => el.type === `${type}Node`).length + 1}
          </div>
        </div>
      )
    }

    let nextStep

    if (currentBtn.data.nextStep) {
      nextStep = currentBtn.data.nextStep
    }

    if (currentBtn.data.nextStepId) {
      nextStep = elems.find(el => el.id === currentBtn.data.nextStepId)
    }

    if (nextStep && activeBtn) {
      if (nextStep.url) {
        return (
          <div className={
            cx(
              'constructor-elemmenu__nextelem',
              'constructor-elemmenu__nextelem_button',
              `constructor-elemmenu__nextelem_url`,
            )
          }>
            <div className="constructor-elemmenu__nextelem-close"
              onClick={() => {
                onRemoveUrl()
                setUrl('')
              }}
            />
            <div className="constructor-elemmenu__nextelem-text">{nextStep.url}</div>
          </div>
        )
      }
      return (
        <div className={
          cx(
            'constructor-elemmenu__nextelem',
            'constructor-elemmenu__nextelem_button',
            `constructor-elemmenu__nextelem_${nextStep.type ? nextStep.type.slice(0, -4) : ''}`,
          )
        }>
          <div className="constructor-elemmenu__nextelem-close"
            onClick={() => {
              onUnlinkElem(`${currentElem.id}__${currentBtn.id}`, nextStep.id)
            }}
          />
          <div className="constructor-elemmenu__nextelem-text">{nextStep.data.text}</div>
        </div>
      )
    }

    const nextElems = elems.filter(el => el.type !== 'input' && el.id !== currentElem.id)

    return (
      <div className='constructor-elemmenu__block constructor-elemmenu__block_choice'>
        <div className="constructor-elemmenu__button-menu">
          {flowType === 'bot' ? <div className="constructor-elemmenu__button-item constructor-elemmenu__button-item_message"
            onClick={() => setType('message')}
          >Отправить сообщение</div> : null}
          <div className="constructor-elemmenu__button-item constructor-elemmenu__button-item_action"
            onClick={() => setType('action')}
          >Запустить событие</div>
          {flowType === 'bot' ? <div className="constructor-elemmenu__button-item constructor-elemmenu__button-item_condition"
            onClick={() => setType('condition')}
          >Запустить условие</div> : null}
          {flowType === 'bot' ? <div className="constructor-elemmenu__button-item constructor-elemmenu__button-item_delay"
            onClick={() => setType('delay')}
          >Запустить задержку</div> : null}
          {flowType === 'bot' && nextElems.length ? <div className="constructor-elemmenu__button-item constructor-elemmenu__button-item_nextstep"
            onClick={() => setType('nextstep')}
          >Выбрать существующий шаг</div> : null}
          <div className="constructor-elemmenu__button-item constructor-elemmenu__button-item_url"
            onClick={() => setType('url')}
          >Открыть url</div>
        </div>
      </div>
    )
  }

  const showButtonMenuFooter = () => {
    if (!type || type === 'nextstep') {
      return null
    }

    return (
      <div className="constructor-elemmenu__button-footer">
        <div className="constructor-elemmenu__button-cancel"
          onClick={() => {
            setType(null)
            onClose()
          }}
        >Отмена</div>
        <div className="constructor-elemmenu__button-submit"
          onClick={() => {
            if (type === 'url') {
              if (url.length) {
                onAddUrl(url)
                setUrl('')
              } else {
                setUrlError('url не может быть пустым')
                return
              }
            } else {
              onAddLinkElem(type, true)
            }
            setType(null)
            onClose()
          }}
        >Подтвердить</div>
      </div>
    )
  }

  return (
    <OutsideClickHandler
      onOutsideClick={() => {
        onClose()
        setType(null)
      }}
    >
      <FloatMenu
        title={type === 'nextstep' ? 'Выберите существующий шаг' : 'Настройка кнопки'}
        coords={{ top, left, bottom }}
        onClose={() => {
          onClose()
          setType(null)
        }}
        modifier='buttonNextStep'
      >
        {showButtonMenuHeader()}
        {showButtonMenuContent()}
        {showButtonMenuFooter()}
      </FloatMenu>
    </OutsideClickHandler>
  )

};

export default ElemMenuButtonsNextStep;