import React from 'react';
import OutsideClickHandler from 'react-outside-click-handler';
import cx from 'classnames'
import FloatMenu from '../FloatMenu'

const ElemMenuNextStep = ({
  elems,
  currentElem,
  floatMenu,
  top,
  left,
  bottom,
  onLinkElem,
  onClose
}) => {
  let nodes = elems.filter(item => {
    return item.data && item.type !== 'input' && item.id !== currentElem.id
  })

  if (floatMenu !== 'nextStep' && floatMenu !== 'nextIfStep' && floatMenu !== 'nextElseStep') {
    return null
  }

  const getNextStepId = () => {
    if (floatMenu === 'nextStep') return `${currentElem.id}__output`
    if (floatMenu === 'nextIfStep') return `${currentElem.id}__if`
    if (floatMenu === 'nextElseStep') return `${currentElem.id}__else`
  }

  return (
    <OutsideClickHandler
      onOutsideClick={() => {
        onClose()
      }}
    >
      <FloatMenu
        title='Следующий шаг'
        coords={{ top, left, bottom }}
        onClose={onClose}
        modifier='nextStep'
      >
        <div className="constructor-elemmenu__nextstep-menu">
          {nodes.map(item => {
            return (
              <div key={item.id}
                className={
                  cx(
                    'constructor-elemmenu__nextstep-item',
                    `constructor-elemmenu__nextstep-item_${item.type.slice(0, -4)}`,
                  )
                }
                onClick={() => {
                  onLinkElem(getNextStepId(), item.id)
                  onClose()
                }}
              >{item.data.text}</div>
            )
          })}
        </div>
      </FloatMenu>
    </OutsideClickHandler>
  )

};

export default ElemMenuNextStep;