import React from 'react';

import DataTable from '../DataTable';
import ReadableDate from '../ReadableDate';

import ProjectInfo from '../ProjectInfo';

export default function AccountsTable({ items, onOpenModal, onSetCurrentElem }) {
  return (
    <DataTable>
      <DataTable.HeadRow>
        <DataTable.HeadCell className='name-column'>
          Аккаунт
        </DataTable.HeadCell>
        <DataTable.HeadCell className='date-column'>
          Дата добавления
        </DataTable.HeadCell>
        <DataTable.HeadCell className='count-column'>
          Ботов
        </DataTable.HeadCell>
        <DataTable.HeadCell className='count-column'>
          Ключевых слов
        </DataTable.HeadCell>
        {/*<DataTable.HeadCell className='count-column'>
          Авторассылок
        </DataTable.HeadCell>
        <DataTable.HeadCell className='count-column'>
          Чатов
        </DataTable.HeadCell>*/}
        <DataTable.HeadCell type="actions" />
      </DataTable.HeadRow>

      <DataTable.Body>
        {items.map((item, index) => (
          <DataTable.Row
            className='table-row'
            key={item.id}
          >
            <DataTable.Cell className='name-column'>
              <ProjectInfo item={item} />
            </DataTable.Cell>
            <DataTable.Cell className='date-column'>
              <ReadableDate date={''} />
            </DataTable.Cell>
            <DataTable.Cell className='count-column'>
              {item.bots}
            </DataTable.Cell>
            <DataTable.Cell className='count-column'>
              {item.keywords}
            </DataTable.Cell>
            {/*<DataTable.Cell className='count-column'>
              {item.mailings}
            </DataTable.Cell>
            <DataTable.Cell className='count-column'>
              {item.chats}
            </DataTable.Cell>*/}
            <DataTable.Cell type="actions">
              <DataTable.Actions
                actions={[
                  {
                    type: 'remove',
                    handler: () => {
                      onSetCurrentElem({ connectorId: item.adapterConnectorId, type: item.adapterType })
                      onOpenModal('removeAccount')
                    }
                  },
                ]}
              />
            </DataTable.Cell>
          </DataTable.Row>
        ))}
      </DataTable.Body>
    </DataTable>
  );
}
