import React from 'react';

import Button from '../Button'

import './TariffInfo.scss';

import moment from 'moment'

moment.locale('ru')

const TariffInfo = ({ account }) => {
  if (!account) {
    return null
  }

  const { tariffEndAt, limits } = account.account

  return (
    <section className='tariff-container'>
      <div className='tariff-item'>
        {limits.tariff.isTrial ? <div className='tariff-title'>Пробный период:</div> : null}
        До: {moment.unix(tariffEndAt).format('DD.MM.YYYY')}
      </div>

      <div className='tariff-item'>
        <div className='tariff-title'>Доступно:</div>
        {limits.showed.maxContacts.limit - limits.showed.maxContacts.value} /
        {limits.showed.maxContacts.limit} подписчиков
      </div>

      <Button
        href="/payment"
        modifier='payment'>
        Оплатить
      </Button>
    </section>
  );
}

export default TariffInfo
