import React, { Fragment, Component } from 'react'
import { connect } from 'react-redux';

import TariffInfo from '../components/common/TariffInfo'

class TariffController extends Component {

  render() {
    const { settings: { basic: account } } = this.props
    return (
      <Fragment>
        <TariffInfo account={account} />
      </Fragment>
    )
  }
}

const mapStateToProps = (state) => {
  const { settings, loading, error } = state

  return {
    settings,
    loading,
    error,
  };
};

export default connect(mapStateToProps, null)(TariffController)