class AutoMailingsController extends Component {
  state = {
    action: '+ Добавить авторассылку'
  }

  componentDidMount() {
    const { getBasicData } = this.props
    // getBasicData()
  }

  showAutomailingDetail = (id) => {
    this.props.history.push(`/automailing/${id}`);
  }

  showMailingFlow = (id) => {
    this.props.history.push(`/mailing/${id}`);
  }

  showAutomailings = () => {
    const { settings: { loading, data }, match, openModal } = this.props

    if (loading.length) {
      return <h2>Loading...</h2>
    }

    if (!DATA) {
      return (
        <Placeholder
          name="mailings"
          width={380}
        >
          Авторассылки позволяют делать серию рассылок сообщений пользователям,
          где каждая рассылка имеет свое заранее запланированное время отправки.

          <br />
          <Button
            onClick={() => openModal('addAutomailing')}
          >
            Создать авторассылку
        </Button>
        </Placeholder>
      )
    }

    if (match.params.id) {
      return (
        <AutoMailingsTableDetail
          items={DATADETAIL}
          onOpenModal={openModal}
          onEdit={this.showMailingFlow}
          onDelete={() => openModal('removeMailing')}
        />
      )
    }

    return (
      <AutoMailingsTable
        items={DATA}
        onOpenModal={openModal}
        onEdit={this.showAutomailingDetail}
        onDelete={() => openModal('removeAutomailing')}
      />
    )
  }

  render() {
    const { match, openModal } = this.props
    const { action } = this.state

    return (
      <Fragment>
        <PageLayout
          title={`${match.params.id ? `Авторассылка → ${match.params.id}` : 'Авторассылки'}`}
          action={action}
          onClick={() => openModal('addAutomailing')}
        >
          {this.showAutomailings()}
        </PageLayout>
        <ModalController />
      </Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  const { settings, loading, error } = state

  return {
    settings,
    loading,
    error,
  };
};

const mapDispatchToProps = {
  getBasicData: settingsActions.basic.request,
  openModal: modalActions.modal.open,
  closeModal: modalActions.modal.close,
};

export default connect(mapStateToProps, mapDispatchToProps)(AutoMailingsController)