import React from 'react';
import OutsideClickHandler from 'react-outside-click-handler';

import FloatMenu from '../FloatMenu'

const ElemMenuTemplate = ({
  floatMenu,
  top,
  left,
  onSetTemplate,
  onClose
}) => {

  if (floatMenu === 'showTemplate') {
    return (
      <OutsideClickHandler
        onOutsideClick={() => {
          onClose()
        }}
      >
        <FloatMenu
          coords={{ top, left }}
          onClose={onClose}
          modifier='showTemplate'
        >
          <div className="constructor-elemmenu__add-menu">
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onSetTemplate('name')
                onClose()
              }}
            >Имя</div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onSetTemplate('email')
                onClose()
              }}
            >Email</div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onSetTemplate('phone')
                onClose()
              }}
            >Телефон</div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onSetTemplate('userid')
                onClose()
              }}
            >User Id</div>
          </div>
        </FloatMenu>
      </OutsideClickHandler>
    )
  }

  return null
}



export default ElemMenuTemplate;