import React, { useState, Fragment } from 'react';
import cx from 'classnames'

import { emailPattern } from '../../../../const/validate-patterns'

const ElemMenuEmailItem = ({
  item,
  onCopyItem,
  onRemoveItem,
  onMoveItem,
  onAddEmail,
  onRemoveEmail,
}) => {

  const [searchValue, setSearchValue] = useState('');
  const [error, setError] = useState(false);

  return (
    <Fragment>
      <div className={
        cx(
          'constructor-elemmenu__label',
          `constructor-elemmenu__label_${item.itemType}`
        )
      }>{item.text}</div>
      <div className="constructor-elemmenu__action">
        <div className="constructor-elemmenu__operations">
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_delete"
            onClick={() => onRemoveItem(item.id)}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_up"
            onClick={() => onMoveItem(item.id, 'up')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_down"
            onClick={() => onMoveItem(item.id, 'down')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_copy"
            onClick={() => onCopyItem(item.id, 0)}
          />
        </div>
        <input
          type="text"
          placeholder='Введите email'
          className={
            cx(
              'constructor-elemmenu__input',
              {
                'constructor-elemmenu__input_active': searchValue,
                'constructor-elemmenu__input_error': error,
              }
            )
          }
          value={searchValue}
          onChange={(e) => {
            setError(false)
            setSearchValue(e.target.value)
          }}
          onKeyPress={(e) => {
            if (e.key === 'Enter') {
              if (emailPattern.test(searchValue)) {
                onAddEmail(item.id, searchValue)
                setSearchValue('')
              } else {
                setError(true)
              }
            }
          }}
        />
        {
          searchValue ? <div
            className="constructor-elemmenu__choice-create constructor-elemmenu__choice-create_border"
            onClick={() => {
              if (emailPattern.test(searchValue)) {
                onAddEmail(item.id, searchValue)
                setSearchValue('')
              } else {
                setError(true)
              }
            }}
          >+&nbsp;Создать новый email <span className='constructor-elemmenu__create'>"{searchValue}"</span></div>
            : null
        }
        {
          item.emails ? <div className='constructor-elemmenu__emails'>
            {item.emails.map((email) => {
              return (
                <div className={
                  cx(
                    'constructor-elemmenu__email',
                    `constructor-elemmenu__email_${item.itemType}`
                  )
                } key={email.id}>
                  @<span className='constructor-elemmenu__email-txt'>{email.text}</span>
                  <span className='constructor-elemmenu__email-remove'
                    onClick={(e) => {
                      onRemoveEmail(email.id)
                    }}
                  >×</span>
                </div>
              )
            })}
          </div> : null
        }
      </div>
    </Fragment>
  )

};

export default ElemMenuEmailItem;