import React, { Component } from 'react'
import { connect } from 'react-redux';
import { authActions } from '../actions/index'

import {
  Link, Redirect
} from 'react-router-dom'

class RegCheckController extends Component {

  componentWillUnmount() {
    const { authDrop } = this.props
    authDrop()
  }

  componentDidMount() {
    const { auth, regCheck } = this.props
    regCheck(auth.token)
  }

  render() {
    const { auth } = this.props

    if (auth.loading) {
      return <h2>Завершение регистрации домена</h2>
    }

    if (auth.location) {
      window.location = auth.location
    }
    return null
  }
}

const mapStateToProps = (state) => {
  const { auth } = state

  return {
    auth
  };
};

const mapDispatchToProps = {
  regCheck: authActions.auth.check.request,
  authDrop: authActions.auth.drop,
};

export default connect(mapStateToProps, mapDispatchToProps)(RegCheckController)