import React, { Component } from 'react'
import { connect } from 'react-redux';

import { constructorBotActions } from '../../actions/index'

import ElemMenuDelay from '../../components/constructor/ElemMenu/delay/ElemMenuDelay'

class ElemMenuBotDelaysController extends Component {
  render() {
    const {
      constructorBot,
      changeDelayValue,
      changeDelayPeriod,
      changeExact,
      changeFromTo,
      changeSchedule
    } = this.props

    const { currentElem } = constructorBot

    if (!currentElem.id || currentElem.id.startsWith('link_')) {
      return null
    }

    return (
      <ElemMenuDelay
        currentElem={currentElem}
        onChangeExact={changeExact}
        onChangeFromTo={changeFromTo} å
        onChangeSchedule={changeSchedule}
        onChangeDelayValue={changeDelayValue}
        onChangeDelayPeriod={changeDelayPeriod}
      />
    )
  }
}

const mapStateToProps = (state) => {
  const { constructorBot, loading, error } = state

  return {
    constructorBot,
    loading,
    error
  };
};

const mapDispatchToProps = {
  changeTitle: constructorBotActions.title.change,
  changeDelayValue: constructorBotActions.delay.value,
  changeDelayPeriod: constructorBotActions.delay.period,
  changeExact: constructorBotActions.delay.exact,
  changeFromTo: constructorBotActions.delay.fromto,
  changeSchedule: constructorBotActions.delay.schedule,
};

export default connect(mapStateToProps, mapDispatchToProps)(ElemMenuBotDelaysController)