import React, { useState, useRef } from 'react'
import cx from 'classnames'
import AutosizeInput from 'react-input-autosize';

import {
  Link,
} from 'react-router-dom'

import ElemMenuBotController from '../controllers/constructor/ElemMenuBotController'
import CanvasBotController from '../controllers/constructor/CanvasBotController'

import LeftMenu from '../components/constructor/LeftMenu'
import Loader from '../components/common/Loader'

import URLS from '../const/urls'

import './ConstructorBotPage.scss';

const DEFAULT_TITLE = 'Новый бот Telegram от "02.02.2021"'

const ConstructorBotPage = ({
  elems,
  error,
  saving,
  onAddElem,
}) => {


  console.log('elems')
  console.log(elems)

  const [name, changeName] = useState(DEFAULT_TITLE);
  const [focus, changeFocus] = useState(false);

  const inputRef = useRef(null);

  if (error) {
    return <h1>{error}</h1>
  }

  const handleKeyDown = (e) => {
    if (e.key === 'Enter' || e.key === 'Escape') {
      inputRef.current.blur()
      changeFocus(false)
    }
  };

  const showProjectSave = () => {
    if (saving) {
      return (
        <div className="constructor-status constructor-status_saving">сохранение...</div>
      )
    }
    return (
      <div className="constructor-status">сохранено</div>
    )
  }

  return (
    <div className="constructor-page">
      <div className="constructor-header">
        <div className='constructor-header__left'>
          <Link className='constructor-back' to={URLS.main}></Link>
          <div className={
            cx(
              'constructor-title',
              {
                'constructor-title_focus': focus
              }
            )
          }>
            <AutosizeInput
              ref={inputRef}
              type="text"
              className="constructor-title__input"
              value={name}
              onChange={(e) => {
                if (e.target.value.length < 35) {
                  changeName(e.target.value)
                }
              }}
              onFocus={(e) => {
                changeFocus(true)
                e.target.select()
              }}
              onBlur={(e) => {
                if (e.target.value.length === 0) {
                  changeName(DEFAULT_TITLE)
                }
                changeFocus(false)
              }}
              onKeyDown={handleKeyDown}
            />
            <div className="constructor-title__tooltip">Переименовать</div>
          </div>
          <Link className='constructor-settings' to={URLS.settings}>Настройки</Link>
        </div>
        <div className='constructor-header__right'>
          {showProjectSave()}
          <div className="constructor-preview">
            <div className="constructor-preview__button">Тестировать</div>
          </div>
          <div className="constructor-publish">
            <div className="constructor-publish__button">Опубликовать</div>
          </div>
        </div>
      </div>
      <div className="constructor-content">
        <div className="constructor-work">
        <LeftMenu
          onAddElem={onAddElem}
        /> 
          <CanvasBotController />
        </div>
        <ElemMenuBotController />
      </div>
    </div>
  )
}

export default ConstructorBotPage