import { createActions } from 'redux-actions';

export default createActions({
  AUTH: {
    LOGIN: {
      REQUEST: (domain, password) => ({ domain, password }),
      SUCCESS: null,
      FAILURE: null
    },
    REG: {
      REQUEST: (domain, email, password) => ({ domain, email, password }),
      SUCCESS: null,
      FAILURE: null
    },
    CHECK: {
      REQUEST: (token) => (token),
      SUCCESS: null,
      FAILURE: null
    },
    FORGET: {
      REQUEST: (email) => ({ email }),
      SUCCESS: null,
      FAILURE: null
    },
    DROP: null
  },
});