import React, { Fragment, Component } from 'react'
import { connect } from 'react-redux';

import AddAccountModal from '../components/modals/AddAccountModal'
import AddTelegramAccountModal from '../components/modals/AddTelegramAccountModal'
import AddViberAccountModal from '../components/modals/AddViberAccountModal'
import AddVkontakteAccountModal from '../components/modals/AddVkontakteAccountModal'
import AddBotModal from '../components/modals/AddBotModal'
import AddTelegramBotModal from '../components/modals/AddTelegramBotModal'
import AddViberBotModal from '../components/modals/AddViberBotModal'
import AddKeywordsModal from '../components/modals/AddKeywordsModal'
import EditKeywordsModal from '../components/modals/EditKeywordsModal'
import RemoveAccountModal from '../components/modals/RemoveAccountModal'
import RemoveBotModal from '../components/modals/RemoveBotModal'
import RemoveKeywordsModal from '../components/modals/RemoveKeywordsModal'
import RemoveSubscribersModal from '../components/modals/RemoveSubscribersModal'
import CancelSubscribeModal from '../components/modals/CancelSubscribeModal'
import AddAutomailingModal from '../components/modals/AddAutomailingModal'
// import RemoveAutomailingModal from '../components/modals/RemoveAutomailingModal'
import RemoveElemModal from '../components/modals/RemoveElemModal'
import CopyBotLinkModal from '../components/modals/CopyBotLinkModal'

import { settingsActions, modalActions, constructorBotActions } from '../actions/index'

class ModalController extends Component {
  render() {
    const {
      modal,
      basic,
      data,
      currentElem,
      openModal,
      closeModal,
      addChannel,
      removeChannel,
      addBot,
      removeBot,
      addKeywords,
      removeKeywords,
      addAutomailing,
      removeAutomailing,
      removeSubscribers,
      setCurrentElem,
      removeElem,
      removeElemWithoutConfirm,
      removeConfirm
    } = this.props

    if (!modal) {
      return null
    }

    switch (modal) {
      case 'addAccount':
        return (
          <AddAccountModal
            modal={modal}
            basic={basic}
            onOpenModal={openModal}
            onCloseModal={closeModal}
            onAddChannel={addChannel}
            onSetCurrentElem={setCurrentElem}
          />
        )

      case 'addTelegramAccount':
        return (
          <AddTelegramAccountModal
            modal={modal}
            onOpenModal={openModal}
            onCloseModal={closeModal}
            onAddChannel={addChannel}
          />
        )

      case 'addViberAccount':
        return (
          <AddViberAccountModal
            modal={modal}
            onOpenModal={openModal}
            onCloseModal={closeModal}
            onAddChannel={addChannel}
          />
        )

      case 'addVkontakteAccount':
        return (
          <AddVkontakteAccountModal
            modal={modal}
            basic={basic}
            currentElem={currentElem}
            onCloseModal={closeModal}
            onAddChannel={addChannel}
            onSetCurrentElem={setCurrentElem}
          />
        )

      case 'removeAccount':
        return (
          <RemoveAccountModal
            modal={modal}
            onOpenModal={openModal}
            onCloseModal={closeModal}
            onRemoveChannel={removeChannel}
            onSetCurrentElem={setCurrentElem}
          />
        )

      case 'removeBot':
        return (
          <RemoveBotModal
            onOpenModal={openModal}
            onCloseModal={closeModal}
            onRemoveBot={removeBot}
          />
        )

      case 'removeKeywords':
        return (
          <RemoveKeywordsModal
            onRemoveKeywords={removeKeywords}
            onOpenModal={openModal}
            onCloseModal={closeModal}
            onSetCurrentElem={setCurrentElem}
          />
        )

      case 'removeSubscribers':
        return (
          <RemoveSubscribersModal
            onRemoveSubscribers={removeSubscribers}
            onOpenModal={openModal}
            onCloseModal={closeModal}
            onSetCurrentElem={setCurrentElem}
          />
        )

      case 'addBot':
        return (
          <AddBotModal
            onOpenModal={openModal}
            onCloseModal={closeModal}
          />
        )

      case 'addTelegramBot':
        return (
          <AddTelegramBotModal
            basic={basic}
            onOpenModal={openModal}
            onCloseModal={closeModal}
            onAddBot={addBot}
            onSetCurrentElem={setCurrentElem}
          />
        )

      case 'addViberBot':
        return (
          <AddViberBotModal
            basic={basic}
            onOpenModal={openModal}
            onCloseModal={closeModal}
            onAddBot={addBot}
            onSetCurrentElem={setCurrentElem}
          />
        )

      case 'addKeywords':
        return (
          <AddKeywordsModal
            bots={data.bots}
            onAddKeywords={addKeywords}
            onOpenModal={openModal}
            onCloseModal={closeModal}
          />
        )

      case 'editKeywords':
        return (
          <EditKeywordsModal
            bots={data.bots}
            dataKeywords={data.keywords}
            currentElem={currentElem}
            onAddKeywords={addKeywords}
            onOpenModal={openModal}
            onCloseModal={closeModal}
          />
        )

      case 'removeElem':
        return (
          <RemoveElemModal
            modal={modal}
            removeElemWithoutConfirm={removeElemWithoutConfirm}
            onRemoveConfirm={removeConfirm}
            onCloseModal={closeModal}
            onRemoveElem={removeElem}
          />
        )

      case 'addAutomailing':
        return (
          <AddAutomailingModal
            modal={modal}
            onCloseModal={closeModal}
            onAddAutomailing={addAutomailing}
          />
        )


      case 'cancelSubscribe':
        return (
          <CancelSubscribeModal
            modal={modal}
            onCancelSubscribe={() => console.log('onCancelSubscribe')}
            onCloseModal={closeModal}
            onRemoveElem={removeElem}
          />
        )

      case 'copyBotLink':
        return (
          <CopyBotLinkModal
            onOpenModal={openModal}
            onCloseModal={closeModal}
          />
        )

      default:
        return null
    }
  }
}

const mapStateToProps = (state) => {
  const { modal, currentItem } = state.modal
  const { basic, data, currentElem } = state.settings
  const { removeElemWithoutConfirm } = state.constructorBot

  return {
    basic,
    data,
    modal,
    currentElem,
    currentItem,
    removeElemWithoutConfirm
  };
};

const mapDispatchToProps = {
  addChannel: settingsActions.channel.add,
  removeChannel: settingsActions.channel.remove,
  addBot: settingsActions.bot.add,
  removeBot: settingsActions.bot.remove,
  addKeywords: settingsActions.keywords.add,
  removeKeywords: settingsActions.keywords.remove,
  addAutomailing: settingsActions.automailing.add,
  removeAutomailing: settingsActions.automailing.remove,
  removeSubscribers: settingsActions.audience.remove,
  openModal: modalActions.modal.open,
  closeModal: modalActions.modal.close,
  setCurrentElem: settingsActions.currentelem.set,
  removeElem: constructorBotActions.elems.remove,
  removeConfirm: constructorBotActions.elems.removeconfirm,
};

export default connect(mapStateToProps, mapDispatchToProps)(ModalController)