import React, { useState, Fragment } from 'react';
import OutsideClickHandler from 'react-outside-click-handler';
import cx from 'classnames'

import Icon from '../Icon'

import './BotSelect.scss'

const Menu = ({
  placeholder,
  options,
  activeOption,
  onChange,
}) => {
  const [open, toggleOpen] = useState(false);
  const [title, setTitle] = useState(placeholder);

  return (
    <OutsideClickHandler
      onOutsideClick={() => {
        toggleOpen(false)
      }}
    >
      <div className='bot-select'>
        <div className={
          cx(
            'bot-select-title',
            { 'bot-select-title_open': open }
          )
        }
          onClick={() => {
            toggleOpen(!open)
          }}
        >{activeOption ? activeOption.label : title}</div>
        {open ? <div className='bot-select-list'>
          {options.map((opt, i) => {
            return (
              <div
                key={i}
                className='bot-select-item'
                onClick={() => {
                  toggleOpen(false)
                  onChange(opt)
                  setTitle(opt.label)
                }}
              ><Icon name={opt.type} width={20} />&nbsp;{opt.label}</div>
            )
          })}
        </div> : null}
      </div>
    </OutsideClickHandler>
  )

};

export default Menu;


