import React, { Fragment, Component } from 'react'
import { connect } from 'react-redux';
import classnames from 'classnames';
import { Redirect } from 'react-router-dom'

import { settingsActions } from '../actions/index'

import PageLayout from '../components/common/PageLayout'

import DialogsArea from '../components/common/DialogsArea'
import ThreadArea from '../components/common/ThreadArea'
import UserinfoArea from '../components/common/UserinfoArea'

import getCookie from '../const/getCookie'

class ChatController extends Component {
  state = {
    infoOpened: false
  }

  onInfoToggle = () => {
    const { infoOpened } = this.state
    this.setState({ infoOpened: !infoOpened })
  }

  componentDidMount() {
    const { getBasicData } = this.props
    // getBasicData()
  }

  render() {
    const { settings } = this.props
    const { infoOpened } = this.state

    return (
      <PageLayout
        title="Онлайн-чат"
        type="chat"
      >
        <div style={{ display: 'flex', height: '100%' }}>
          <DialogsArea />

          <ThreadArea
            isInfoOpened={infoOpened}
            onInfoToggle={this.onInfoToggle}
          />

          {infoOpened ? <UserinfoArea /> : null}
        </div>
      </PageLayout>
    )
  }
}

const mapStateToProps = (state) => {
  const { settings, loading, error } = state

  return {
    settings,
    loading,
    error,
  };
};

const mapDispatchToProps = {
  getBasicData: settingsActions.basic.request,
};

export default connect(mapStateToProps, mapDispatchToProps)(ChatController)