import React from 'react';
import classnames from 'classnames';
import ExternalSwitch from 'react-switch';

import './Switch.scss';

export default function Switch({ label, enabled, onChange }) {
  const componentClasses = classnames({
    'switch': true,
    'switch-disabled': !enabled,
  });

  return (
    <label className={componentClasses}>
      { label && <span className='switch-caption'>{label}</span>}
      <ExternalSwitch
        onChange={onChange}
        checked={enabled}
        uncheckedIcon={false}
        checkedIcon={false}
        className='switch-control'
      />
    </label>

  );
}
