import React from 'react';

import Button from '../../common/Button'
import Modal from '../Modal'

const RemoveBot = ({ onCloseModal, onRemoveBot }) => {
  return (
    <Modal
      title='Вы уверены что хотите удалить бота?'
      onClose={onCloseModal}
    >
      <div className="modal-content">
        Бот будет полностью удален из системы, включая все шаги и настройки.
      </div>
      <div className="modal-footer" >
        <div className="modal-buttons">
          <Button modifier='cancel' onClick={() => {
            onCloseModal()
          }}>Отмена</Button>
          <Button modifier='remove' onClick={() => {
            onRemoveBot()
            onCloseModal()
          }}>Удалить бот</Button>
        </div>
      </div>
    </Modal>
  );
};

export default RemoveBot;