import React, { useState, Fragment } from 'react';
import cx from 'classnames'
import DatePicker, { registerLocale } from 'react-datepicker'
import MaskedTextInput from 'react-text-mask'
import 'react-datepicker/dist/react-datepicker.css'
import ru from 'date-fns/locale/ru'
import ElemMenuEditor from '../ElemMenu/ElemMenuEditor'
import Select from '../../common/Select'

import moment from 'moment'
// import 'moment/locale/ru'

moment.locale('ru')
registerLocale('ru', ru)

const ElemMenuAddDetail = ({
  elems,
  tags,
  menuDetail,
  onSubmit,
  onCancel,
  onOpenFloatMenu,
  onClose
}) => {

  const [activeMenuItem, setActiveMenuItem] = useState('');
  const [text, setText] = useState('');
  const [beforeDate, setBeforeDate] = useState(null);
  const [afterDate, setAfterDate] = useState(null);
  const [exactDate, setExactDate] = useState(null);
  const [fromTo, setFromTo] = useState({
    fromHour: '00',
    toHour: '00',
    fromMinute: '00',
    toMinute: '00',
    beforeHour: '00',
    beforeMinute: '00',
    afterHour: '00',
    afterMinute: '00',
    exactHour: '00',
    exactMinute: '00',
  });
  const [stepLink, setStepLink] = useState(null);
  const [error, setError] = useState({});

  const [schedule, setSchedule] = useState({
    mon: false,
    tue: false,
    wed: false,
    thu: false,
    fri: false,
    sat: false,
    sun: false,
  });

  const beforeOptions = [
    { label: 'Дата', value: 'date' },
    { label: 'Время', value: 'time' }
  ]

  const afterOptions = [
    { label: 'Дата', value: 'date' },
    { label: 'Время', value: 'time' }
  ]

  const [beforeOption, setBeforeOption] = useState(beforeOptions[0]);
  const [afterOption, setAfterOption] = useState(afterOptions[0]);

  const TITLES = {
    tag: 'Метка',
    name: 'Имя',
    email: 'Email',
    phone: 'Телефон',
    dateTime: 'Текущая дата/время',
    link: 'Переход по ссылке в шаге',
  }

  const showDateTime = () => {
    switch (activeMenuItem) {
      case 'before':
        return (
          <Fragment>
            <Select
              options={beforeOptions}
              activeOption={beforeOption}
              update={true}
              modifier='datetime'
              onChange={(field) => {
                setBeforeOption(field)
              }}
            />
            {beforeOption.value === 'date' &&
              <DatePicker
                placeholder='date'
                selected={beforeDate}
                className={
                  cx(
                    'constructor-elemmenu__datepicker',
                    {
                      'constructor-elemmenu__datepicker_error': error.beforeDate
                    })
                }
                locale='ru'
                dateFormat='dd.MM.yyyy'
                placeholderText='ДД.ММ.ГГГГ'
                // minDate={modifier === 'superform_modifier' ? null : moment().toDate()}
                // maxDate={modifier === 'superform_modifier' ? moment().toDate() : null}
                onChange={(date) => {
                  setError({ ...error, beforeDate: false })
                  setBeforeDate(date)
                }}
                onSelect={(date) => {
                  setError({ ...error, beforeDate: false })
                  setBeforeDate(date)
                }}
                customInput={
                  <MaskedTextInput
                    type='text'
                    mask={[/\d/, /\d/, '.', /\d/, /\d/, '.', /\d/, /\d/, /\d/, /\d/]}
                  />
                }
              />}
            {beforeOption.value === 'time' && <Fragment>
              <input
                type="text"
                maxLength={2}
                value={fromTo.beforeHour}
                className={
                  cx(
                    'constructor-elemmenu__input',
                    'constructor-elemmenu__input_small',
                    {
                      'constructor-elemmenu__input_error': error.beforeHour
                    })
                }
                onChange={(e) => {
                  setError({ ...error, beforeHour: false })
                  if (e.target.value > 23) {
                    setFromTo({ ...fromTo, beforeHour: '00' })
                    setError({ ...error, beforeHour: true })
                  } else {
                    setFromTo({ ...fromTo, beforeHour: e.target.value })
                  }
                }}
                onBlur={(e) => {
                  if (e.target.value < 10) {
                    setFromTo({ ...fromTo, beforeHour: `0${+e.target.value}` })
                  }
                }}
              />
            &nbsp;:&nbsp;
            <input
                type="text"
                maxLength={2}
                value={fromTo.beforeMinute}
                className={
                  cx(
                    'constructor-elemmenu__input',
                    'constructor-elemmenu__input_small',
                    {
                      'constructor-elemmenu__input_error': error.beforeMinute
                    })
                }
                onChange={(e) => {
                  setError({ ...error, beforeMinute: false })
                  if (e.target.value > 59) {
                    setFromTo({ ...fromTo, beforeMinute: '00' })
                    setError({ ...error, beforeMinute: true })
                  } else {
                    setFromTo({ ...fromTo, beforeMinute: e.target.value })
                  }
                }}
                onBlur={(e) => {
                  if (e.target.value < 10) {
                    setFromTo({ ...fromTo, beforeMinute: `0${+e.target.value}` })
                  }
                }}
              />
            </Fragment>
            }

          </Fragment>
        )

      case 'after':
        return (
          <Fragment>
            <Select
              options={afterOptions}
              activeOption={afterOption}
              update={true}
              modifier='datetime'
              onChange={(field) => {
                setAfterOption(field)
              }}
            />
            {afterOption.value === 'date' &&
              <DatePicker
                selected={afterDate}
                className={
                  cx(
                    'constructor-elemmenu__datepicker',
                    {
                      'constructor-elemmenu__datepicker_error': error.afterDate
                    })
                }
                locale='ru'
                dateFormat='dd.MM.yyyy'
                placeholderText='ДД.ММ.ГГГГ'
                // minDate={modifier === 'superform_modifier' ? null : moment().toDate()}
                // maxDate={modifier === 'superform_modifier' ? moment().toDate() : null}
                onChange={(date) => {
                  setError({ ...error, afterDate: false })
                  setAfterDate(date)
                }}
                onSelect={(date) => {
                  setError({ ...error, afterDate: false })
                  setAfterDate(date)
                }}
                customInput={
                  <MaskedTextInput
                    type='text'
                    mask={[/\d/, /\d/, '.', /\d/, /\d/, '.', /\d/, /\d/, /\d/, /\d/]}
                  />
                }
              />}
            {afterOption.value === 'time' && <Fragment>
              <input
                type="text"
                maxLength={2}
                value={fromTo.afterHour}
                className={
                  cx(
                    'constructor-elemmenu__input',
                    'constructor-elemmenu__input_small',
                    {
                      'constructor-elemmenu__input_error': error.afterHour
                    })
                }
                onChange={(e) => {
                  setError({ ...error, afterHour: false })
                  if (e.target.value > 23) {
                    setFromTo({ ...fromTo, afterHour: '00' })
                    setError({ ...error, afterHour: true })
                  } else {
                    setFromTo({ ...fromTo, afterHour: e.target.value })
                  }
                }}
                onBlur={(e) => {
                  if (e.target.value < 10) {
                    setFromTo({ ...fromTo, afterHour: `0${+e.target.value}` })
                  }
                }}
              />
              &nbsp;:&nbsp;
              <input
                type="text"
                maxLength={2}
                value={fromTo.afterMinute}
                className={
                  cx(
                    'constructor-elemmenu__input',
                    'constructor-elemmenu__input_small',
                    {
                      'constructor-elemmenu__input_error': error.afterMinute
                    })
                }
                onChange={(e) => {
                  setError({ ...error, afterMinute: false })
                  if (e.target.value > 59) {
                    setFromTo({ ...fromTo, afterMinute: '00' })
                    setError({ ...error, afterMinute: true })
                  } else {
                    setFromTo({ ...fromTo, afterMinute: e.target.value })
                  }
                }}
                onBlur={(e) => {
                  if (e.target.value < 10) {
                    setFromTo({ ...fromTo, afterMinute: `0${+e.target.value}` })
                  }
                }}
              />
            </Fragment>
            }

          </Fragment>
        )

      case 'exact':
        return (
          <Fragment>
            <div className="constructor-elemmenu__subtitle">Дата</div>
            <DatePicker
              selected={exactDate}
              className={
                cx(
                  'constructor-elemmenu__datepicker',
                  {
                    'constructor-elemmenu__datepicker_error': error.exactDate
                  })
              }
              locale='ru'
              dateFormat='dd.MM.yyyy'
              placeholderText='ДД.ММ.ГГГГ'
              onChange={(date) => {
                setError({ ...error, exactDate: false })
                setExactDate(date)
              }}
              onSelect={(date) => {
                setError({ ...error, exactDate: false })
                setExactDate(date)
              }}
              customInput={
                <MaskedTextInput
                  type='text'
                  mask={[/\d/, /\d/, '.', /\d/, /\d/, '.', /\d/, /\d/, /\d/, /\d/]}
                />
              }
            />
          </Fragment>
        )

      case 'schedule':
        return (
          <div className="constructor-elemmenu__row constructor-elemmenu__row_schedule">
            <div className="constructor-elemmenu__col">
              <div className={
                cx('constructor-elemmenu__schedule-item',
                  {
                    'constructor-elemmenu__schedule-item_blue constructor-elemmenu__schedule-item_active': schedule.mon
                  })
              }
                onClick={(e) => {
                  setSchedule({ ...schedule, mon: !schedule.mon })
                }}
              >Пн.</div>
              <div className={
                cx('constructor-elemmenu__schedule-item',
                  {
                    'constructor-elemmenu__schedule-item_blue constructor-elemmenu__schedule-item_active': schedule.wed
                  })
              }
                onClick={(e) => {
                  setSchedule({ ...schedule, wed: !schedule.wed })
                }}
              >Ср.</div>
              <div className={
                cx('constructor-elemmenu__schedule-item',
                  {
                    'constructor-elemmenu__schedule-item_blue constructor-elemmenu__schedule-item_active': schedule.fri
                  })
              }
                onClick={(e) => {
                  setSchedule({ ...schedule, fri: !schedule.fri })
                }}
              >Пт.</div>
              <div className={
                cx('constructor-elemmenu__schedule-item',
                  {
                    'constructor-elemmenu__schedule-item_blue constructor-elemmenu__schedule-item_active': schedule.sun
                  })
              }
                onClick={(e) => {
                  setSchedule({ ...schedule, sun: !schedule.sun })
                }}
              >Вс.</div>
            </div>
            <div className="constructor-elemmenu__col">
              <div className={
                cx('constructor-elemmenu__schedule-item',
                  {
                    'constructor-elemmenu__schedule-item_blue constructor-elemmenu__schedule-item_active': schedule.tue
                  })
              }
                onClick={(e) => {
                  setSchedule({ ...schedule, tue: !schedule.tue })
                }}
              >Вт.</div>
              <div className={
                cx('constructor-elemmenu__schedule-item',
                  {
                    'constructor-elemmenu__schedule-item_blue constructor-elemmenu__schedule-item_active': schedule.thu
                  })
              }
                onClick={(e) => {
                  setSchedule({ ...schedule, thu: !schedule.thu })
                }}
              >Чт.</div>
              <div className={
                cx('constructor-elemmenu__schedule-item',
                  {
                    'constructor-elemmenu__schedule-item_blue constructor-elemmenu__schedule-item_active': schedule.sat
                  })
              }
                onClick={(e) => {
                  setSchedule({ ...schedule, sat: !schedule.sat })
                }}
              >Сб.</div>
            </div>
          </div>
        )

      default:
        return null
    }
  }

  const showLeftCol = () => {
    switch (menuDetail) {
      case 'tag':
        if (!activeMenuItem) {
          setActiveMenuItem('is')
        }

        return (
          <div className="constructor-elemmenu__detail-col">
            <div className="constructor-elemmenu__detail-list">
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'is' }
                )
              }
                onClick={() => setActiveMenuItem('is')}>Соответствует</div>
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'is_not' }
                )
              }
                onClick={() => setActiveMenuItem('is_not')}>Не соответствует</div>
            </div>
          </div>
        )

      case 'name':
      case 'email':
      case 'phone':
      case 'has_any_value':
      case 'is_unknown':
        if (!activeMenuItem) {
          setActiveMenuItem('is')
        }

        return (
          <div className="constructor-elemmenu__detail-col">
            <div className="constructor-elemmenu__detail-list">
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'is' }
                )
              }
                onClick={() => setActiveMenuItem('is')}>Соответствует</div>
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'is_not' }
                )
              }
                onClick={() => setActiveMenuItem('is_not')}>Не соответствует</div>
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'contains' }
                )
              }
                onClick={() => setActiveMenuItem('contains')}>Содержит</div>
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'does_not_contain' }
                )
              }
                onClick={() => setActiveMenuItem('does_not_contain')}>Не содержит</div>
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'has_any_value' }
                )
              }
                onClick={() => setActiveMenuItem('has_any_value')}>Любое значение</div>
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'is_unknown' }
                )
              }
                onClick={() => setActiveMenuItem('is_unknown')}>Неизвестно</div>
            </div>
          </div>
        )

      case 'dateTime':
        if (!activeMenuItem) {
          setActiveMenuItem('before')
        }
        return (
          <div className="constructor-elemmenu__detail-col">
            <div className="constructor-elemmenu__detail-list">
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'before' }
                )
              }
                onClick={() => setActiveMenuItem('before')}
              >До</div>
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'after' }
                )
              }
                onClick={() => setActiveMenuItem('after')}>После</div>
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'exact' }
                )
              }
                onClick={() => setActiveMenuItem('exact')}>Точное значение</div>
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'schedule' }
                )
              }
                onClick={() => setActiveMenuItem('schedule')}>Дни недели</div>
            </div>
          </div>
        )

      case 'link':
        const options = elems.filter(item => item.type === 'messageNode').map(item => {
          return ({
            label: item.data.text,
            value: item.id
          })
        })

        let modifier = 'step'

        if (error.stepLink) {
          modifier = 'step_error'
        }

        return (
          <Select
            placeholder='Выберите шаг'
            options={options}
            modifier={modifier}
            onChange={(field) => {
              setError({ ...error, stepLink: false })
              setStepLink(field)
            }}
          />
        )

      default:
        return (
          <div className="constructor-elemmenu__detail-col">
            <div className="constructor-elemmenu__detail-list">
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'is' }
                )
              }
                onClick={() => setActiveMenuItem('is')}>Соответствует</div>
              <div className={
                cx(
                  'constructor-elemmenu__detail-item',
                  { 'constructor-elemmenu__detail-item_active': activeMenuItem === 'is_not' }
                )
              }
                onClick={() => setActiveMenuItem('is_not')}>Не соответствует</div>
            </div>
          </div>
        )
    }
  }

  const showRightCol = () => {
    switch (menuDetail) {
      case 'tag':
        return (
          tags.length ? <Select
            placeholder='Выберите метку'
            options={tags}
            modifier={'tags'}
            onChange={(field) => {
              setText(field.label)
            }}
          /> : <div className='constructor-elemmenu__empty'>Вы еще не добавили ни <br />одной метки</div>
        )
      case 'name':
      case 'email':
      case 'phone':
        return (
          <div className={
            cx(
              'constructor-elemmenu__detail-col',
              'constructor-elemmenu__detail-col_padding',
            )
          }
            onClick={(e) => e.stopPropagation()}
          >
            {activeMenuItem === 'is_unknown' || activeMenuItem === 'has_any_value'
              ? null :
              <ElemMenuEditor
                placeholder='Введите значение'
                text={text}
                modifier={error.text ? 'error' : null}
                onChangeText={(text) => {
                  setError({ ...error, text: null })
                  setText(text)
                }}
                onOpenFloatMenu={onOpenFloatMenu}
              />
            }
          </div>
        )

      case 'dateTime':
        return (
          <div className={
            cx(
              'constructor-elemmenu__detail-col',
              'constructor-elemmenu__detail-col_padding',
            )
          }>
            {showDateTime()}
          </div>
        )

      default:
        return (
          <div className={
            cx(
              'constructor-elemmenu__detail-col',
              'constructor-elemmenu__detail-col_padding',
            )
          }
            onClick={(e) => e.stopPropagation()}
          >
            <ElemMenuEditor
              placeholder='Введите значение'
              text={text}
              modifier={error.text ? 'error' : null}
              onChangeText={(text) => {
                setError({ ...error, text: null })
                setText(text)
              }}
              onOpenFloatMenu={onOpenFloatMenu}
            />
          </div>
        )
    }
  }

  const showFooter = () => {
    return (
      <div className="constructor-elemmenu__button-footer">
        <div className="constructor-elemmenu__button-cancel"
          onClick={() => {
            onCancel()
          }}
        >Отмена</div>
        <div className="constructor-elemmenu__button-submit"
          onClick={() => {
            let options = {}

            if (['tag', 'name', 'email', 'phone'].includes(menuDetail)) {
              if (!text.trim().length && activeMenuItem !== 'is_unknown' && activeMenuItem !== 'has_any_value') {
                setError({ ...error, text: true })
                return null
              }
              options.condition = activeMenuItem
              options.key = TITLES[menuDetail]
              options.value = text
            }
            if (menuDetail === 'dateTime') {
              if (activeMenuItem === 'before' && beforeOption.value === 'date' && !beforeDate) {
                setError({ ...error, beforeDate: true })
                return null
              }
              if (activeMenuItem === 'after' && afterOption.value === 'date' && !afterDate) {
                setError({ ...error, afterDate: true })
                return null
              }
              if (activeMenuItem === 'exact' && !exactDate) {
                setError({ ...error, exactDate: true })
                return null
              }
              options = {
                condition: activeMenuItem,
                beforeDate: beforeDate ? moment(beforeDate).format('DD.MM.YYYY') : 'Любая дата',
                afterDate: afterDate ? moment(afterDate).format('DD.MM.YYYY') : 'Любая дата',
                exactDate: exactDate ? moment(exactDate).format('DD.MM.YYYY') : 'Любая дата',
                beforeValue: beforeOption.value,
                afterValue: afterOption.value,
                fromTo,
                schedule,
                stepLink
              }
            }
            if (menuDetail === 'link') {
              if (!stepLink) {
                setError({ ...error, stepLink: true })
                return null
              }
              options.condition = 'step'
              options.key = TITLES[menuDetail]
              options.value = stepLink
            }
            if (menuDetail) {
              options.condition = activeMenuItem
              options.key = options.key ? options.key : menuDetail
              options.value = options.value ? options.value : text
            }
            onSubmit(menuDetail, options)
            onCancel()
            onClose()
          }}
        >Подтвердить</div>
      </div>
    )
  }

  return (
    <div className="constructor-elemmenu__detail">
      <div className="constructor-elemmenu__detail-back" onClick={onCancel}>‹</div>
      <div className="constructor-elemmenu__detail-title">{TITLES[menuDetail] ? TITLES[menuDetail] : menuDetail}</div>
      <div className="constructor-elemmenu__detail-content">
        {showLeftCol()}
        {showRightCol()}
      </div>
      {showFooter()}
    </div>
  )
}



export default ElemMenuAddDetail;