import React from 'react';

import DataTable from '../DataTable';
import Icon from '../Icon';
import ReadableDate from '../ReadableDate';

import './UsersTable.scss';

export default function UsersTable({
  items,
  selected,
  onSelect,
  onSelectAll,
  onOpenModal
}) {
  return (
    <DataTable>
      <DataTable.HeadRow>
        <DataTable.HeadCell type="check">
          <DataTable.Check
            onChange={onSelectAll}
            isChecked={!!selected.length && selected.length === items.length}
          />
        </DataTable.HeadCell>
        <DataTable.HeadCell className='name-column'>
          Подписчик
        </DataTable.HeadCell>
        {/*<DataTable.HeadCell className='chats-column'>
          Онлайн чатов
        </DataTable.HeadCell>*/}
        <DataTable.HeadCell className='subscribe-column'>
          Подписался
        </DataTable.HeadCell>
      </DataTable.HeadRow>

      <DataTable.Body>
        {(() => {
          if (!items.length) {
            return (
              <DataTable.Placeholder>
                <Icon
                  className='users-table-placeholder-icon'
                  name="people"
                  width={25}
                />
                Не найдено ни одного подписчика
              </DataTable.Placeholder>
            )
          }

          return items.map((item, index) => {
            return <DataTable.Row
              className='table-row'
              key={index}
            >
              <DataTable.Cell type="check">
                <DataTable.Check
                  name={item.name}
                  value={item.id}
                  isChecked={selected.includes(item.id)}
                  onChange={onSelect}
                />
              </DataTable.Cell>
              <DataTable.Cell className='name-column'>
                {item.name}
              </DataTable.Cell>
              {/*<DataTable.Cell className='chats-column'>
                {item.chats}
              </DataTable.Cell>*/}
              <DataTable.Cell className='subscribe-column'>
                <ReadableDate date={item.createdAt} />
              </DataTable.Cell>
              <DataTable.Cell type="actions">
                <DataTable.Actions
                  actions={[
                    {
                      type: 'remove',
                      handler: () => {
                        if (selected.length) {
                          onOpenModal('removeSubscribers')
                        }
                      }
                    },
                  ]}
                />
              </DataTable.Cell>
            </DataTable.Row>
          })
        })()}
      </DataTable.Body>
    </DataTable>
  );
}
