import React, { Fragment } from 'react';
import cx from 'classnames'

const ElemMenuEmailItem = ({
  item,
  onCopyItem,
  onRemoveItem,
  onMoveItem,
}) => {
  const TITLES = {
    subscribe: 'Подписать на бота',
    unsubscribe: 'Отписать от бота'
  }

  return (
    <Fragment>
      <div className={
        cx(
          'constructor-elemmenu__label',
          `constructor-elemmenu__label_${item.itemType}`
        )
      }>{TITLES[item.itemType]}</div>
      <div className="constructor-elemmenu__action constructor-elemmenu__action_subscribe">
        <div className="constructor-elemmenu__operations">
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_delete"
            onClick={() => onRemoveItem(item.id)}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_up"
            onClick={() => onMoveItem(item.id, 'up')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_down"
            onClick={() => onMoveItem(item.id, 'down')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_copy"
            onClick={() => onCopyItem(item.id, 0)}
          />
        </div>
      </div>
    </Fragment>
  )

};

export default ElemMenuEmailItem;