import React, { Fragment, Component } from 'react'
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom'

import ModalController from './ModalController'

import PageLayout from '../components/common/PageLayout'
import Placeholder from '../components/common/Placeholder'
import Loader from '../components/common/Loader'
import Button from '../components/common/Button'
import BotsTable from '../components/common/BotsTable'

import { settingsActions, modalActions } from '../actions/index'

import getCookie from '../const/getCookie'

class BotsController extends Component {
  state = {
    action: '+ Добавить бота'
  }

  componentDidMount() {
    const { basic, getBasicData } = this.props
    if (!basic) {
      getBasicData()
    }
  }

  showBots = () => {
    const { settings: { loading, data }, openModal, setCurrentElem, toggleBotState } = this.props

    if (loading.length) {
      return <Loader />
    }

    if (!data.bots.length) {
      return (
        <Placeholder
          name="bots"
          width={114}
        >
          <div>Создайте свой первый бот в telegram, facebook, viber или vkontakte</div>
          <Button
            onClick={() => openModal('addBot')}
          >
            Создать бота
          </Button>
        </Placeholder>
      )
    }

    return (
      <BotsTable
        items={data.bots}
        onOpenModal={openModal}
        onSetCurrentElem={setCurrentElem}
        onToggleState={toggleBotState}
      />
    )
  }

  render() {
    const { openModal } = this.props
    const { action } = this.state

    return (
      <Fragment>
        <PageLayout
          title="Боты"
          action={action}
          onClick={() => openModal('addBot')}
        >
          {this.showBots()}
        </PageLayout>
        <ModalController />
      </Fragment>

    );
  }
}

const mapStateToProps = (state) => {
  const { basic, settings, loading, error } = state

  return {
    basic,
    settings,
    loading,
    error,
  };
};

const mapDispatchToProps = {
  getBasicData: settingsActions.basic.request,
  toggleBotState: settingsActions.bot.toggle,
  setCurrentElem: settingsActions.currentelem.set,
  openModal: modalActions.modal.open,
  closeModal: modalActions.modal.close,
};

export default connect(mapStateToProps, mapDispatchToProps)(BotsController)