function getCookie(name) {
  const value = `; ${document.cookie}`;
  const parts = value.split(`; ${name}=`);
  if (parts.length === 2) return parts.pop().split(';').shift();
}

function heartbeat() {
  if (!ws) return;
  if (ws.readyState !== 1) return;
  ws.send(`'primus::ping::${Date.now()}'`);
  setTimeout(heartbeat, 60000);
}

function* getBasicData(action) {
  const basicData = yield select(getBasic)

  if (basicData) {
    return null
  }

  try {
    let payload = yield call(getData,
      `http://${getCookie('subdomain')}.${getDomain()}/basic-data?sessionId=${getCookie('sessionId')}`);

    yield put(basic.success(payload))
    yield call(initializeWebSocketsChannel, payload.wsUrl);

  } catch (e) {
    yield put(basic.failure())
  }
}

