function* getFlowInfo() {
  const evGetComplexBotsId = uuidv4()

  ws.send(JSON.stringify({
    data: window.location.pathname.slice(6),
    method: 'evGetComplexBot',
    uid: evGetComplexBotsId
  }));
  yield put(socket.loading.add([evGetComplexBotsId]));
}

function* getInfo() {
  yield call(getCountersInfo);
  const { pathname } = window.location
  switch (true) {
    case pathname.includes('/accounts'):
      yield call(getAccountsInfo);
      break

    case pathname.includes('/bots'):
      yield call(getBotsInfo);
      break

    case pathname.includes('/keywords'):
      yield call(getBotsInfo);
      yield call(getKeywordsInfo);
      break

    case pathname.includes('/audience'):
      yield call(getAudienceInfo);
      break

    case pathname.includes('/flow'):
      yield call(getFlowInfo);
      break

    default:
    // console.log('default message');
    // console.log(message);
  }
}

function* sendToWs(method, data, callback) {
  let uuid = uuidv4();
  yield ws.send(JSON.stringify({
    method: method,
    data: data,
    uid: uuid,
  }));
  yield put(savebot.success());
  if (callback) {
    addAwaitingReplyCallback(uuid, callback);
  }
}

function* saveBot(action) {
  let [sendMethod, data, callback] = action.payload;
  yield sendToWs(sendMethod, data, callback ?? null);
}

