// eslint-disable-next-line
function* createEventChannel() {
  return eventChannel(emit => {
    ws.onopen = () => {
      heartbeat();
      ws.send(JSON.stringify({ data: { sessionId: getCookie('sessionId') }, method: 'authHandshake', uid: uuidv4() }));
    };
    ws.onmessage = (message) => {
      emit(message.data);
      wsMessageListener(JSON.parse(message.data));
    };
    return () => {
      ws.close();
    };
  });
}

function* initializeWebSocketsChannel(wsUrl) {
  const { proto, host, pathname } = wsUrl
  ws = new WebSocket(`${proto}://${getCookie('subdomain')}.${host}${pathname}`);
  const channel = yield call(createEventChannel, ws);

  while (true) {
    const msg = yield take(channel);

    if (!msg.includes('primus::pong')) {
      const message = JSON.parse(msg)
      const { data } = message

      yield put(socket.loading.remove(message));

      if (data && data.sessionId || data === true || data.success === true
        || data.contentTag === 'eventUpdate' || data.keywords) {
        yield call(getInfo);
      }

      if (data && data.elements) {
        yield put(elems.fetch.success(data.elements))
      }

      yield put(socket.receive(message));
    }
  }
}

const awaitingReply = [];
function addAwaitingReplyCallback(uid, callback) {
  awaitingReply.push({
    uid: uid,
    callback: callback,
  })
}

function wsMessageListener(message) {
  let key = null;
  awaitingReply.find((element, index) => {
    if (message.event && message.event === element.uid) {
      key = index;
      return true;
    }
  });
  if (key !== null) {
    awaitingReply[key].callback(message.data ?? {});
    // delete only uuidV4 listeners
    if (uuidValidate(message.event)) {
      awaitingReply.splice(key, 1);
    }
  }
}
