import React from 'react';
import { Link } from 'react-router-dom';

import './Button.scss';

const Button = ({ href, onClick, modifier, children }) => {
  if (href) {
    return (
      <Link className={`button ${modifier ? `button_${modifier}` : ''}`} to={href} >
        { children}
      </Link>
    )
  }

  return (
    <div className={`button ${modifier ? `button_${modifier}` : ''}`} onClick={onClick}>
      {children}
    </div>
  )
}

export default Button
