import React from 'react';

import Button from '../../common/Button'
import Modal from '../Modal'

const RemoveKeywords = ({ onCloseModal, onRemoveKeywords, onSetCurrentElem }) => {
  return (
    <Modal
      title='Вы уверены что хотите удалить ключевое слово?'
      onClose={onCloseModal}
    >
      <div className="modal-content">
        Ключевое слово будет полностью удалено из системы.
      </div>
      <div className="modal-footer" >
        <div className="modal-buttons">
          <Button modifier='cancel' onClick={() => {
            onSetCurrentElem({})
            onCloseModal()
          }}>Отмена</Button>
          <Button modifier='remove' onClick={() => {
            onRemoveKeywords()
            onSetCurrentElem({})
            onCloseModal()
          }}>Удалить ключевое слово</Button>
        </div>
      </div>
    </Modal>
  );
};

export default RemoveKeywords;