import React, { Component, Fragment } from 'react';
import cx from 'classnames'

import ElemMenuBotMessagesController from '../../../controllers/constructor/ElemMenuBotMessagesController';
import ElemMenuBotActionsController from '../../../controllers/constructor/ElemMenuBotActionsController';
import ElemMenuBotConditionsController from '../../../controllers/constructor/ElemMenuBotConditionsController';
import ElemMenuBotDelaysController from '../../../controllers/constructor/ElemMenuBotDelaysController';

import ElemMenuTitle from './ElemMenuTitle'
import ElemMenuNextStep from './ElemMenuNextStep'
import ElemMenuAdd from './ElemMenuAdd'
import ElemMenuTemplate from './ElemMenuTemplate'
import ElemMenuButtonsNextStep from './ElemMenuButtonsNextStep'
import ElemMenuAnswer from './ElemMenuAnswer'

import './ElemMenu.scss';

class ElemMenu extends Component {
  render() {
    const { flowType, elems, tags, fields, currentElem, currentBtn, currentItem,
      floatMenu, top, left, bottom,
      onEditTitle, onChangeTitle, onOpenFloatMenu, onCloseFloatMenu, onSetTemplate,
      onRenameBtn, onLinkElem, onAddLinkElem, onUnlinkElem, onAddAnswer, onAddQuestion, onAddUrl, onRemoveUrl,
      onAddMessage, onChangeImg, onChangeFile, onAddAction, onAddCondition, onCreateField
    } = this.props

    const showContent = () => {
      switch (currentElem.type) {
        case 'messageNode':
          if (flowType === 'bot') {
            return <ElemMenuBotMessagesController />
          }

          break

        case 'actionNode':
          if (flowType === 'bot') {
            return <ElemMenuBotActionsController />
          }

          break

        case 'conditionNode':
          return <ElemMenuBotConditionsController />

        case 'delayNode':
          return <ElemMenuBotDelaysController />

        default:
          return null
      }
    }

    const showNextStep = () => {
      if (currentElem.type === 'conditionNode' || flowType !== 'bot') {
        return null
      }

      if (currentElem.data.nextStep) {
        return (
          <div className={
            cx(
              'constructor-elemmenu__nextelem',
              `constructor-elemmenu__nextelem_${currentElem.data.nextStep.type.slice(0, -4)}`,
            )
          }>
            <div className="constructor-elemmenu__nextelem-close"
              onClick={() => {
                onUnlinkElem(`${currentElem.id}__output`, currentElem.data.nextStep.id)
              }}
            />
            <div className="constructor-elemmenu__nextelem-text">{currentElem.data.nextStep.data.text}</div>
          </div>
        )
      }

      return (
        <div
          className="constructor-elemmenu__nextstep"
          onClick={(e) => onOpenFloatMenu(e, 'nextStep')}
        >Указать следующий шаг</div>
      )
    }

    if (currentElem.type === 'input') {
      return null
    }

    return (
      <Fragment>
        <div className={
          cx(
            'constructor-elemmenu',
            { 'constructor-elemmenu_hidden': floatMenu }
          )
        } >
          <ElemMenuTitle
            currentElem={currentElem}
            onEditTitle={onEditTitle}
            onChangeTitle={onChangeTitle}
          />
          <div className="constructor-titlebg"></div>
          {showContent()}
          {showNextStep()}
        </div>
        <ElemMenuAdd
          elems={elems}
          tags={tags}
          fields={fields}
          currentElem={currentElem}
          floatMenu={floatMenu}
          top={top}
          left={left}
          bottom={bottom}
          onChangeFile={onChangeFile}
          onChangeImg={onChangeImg}
          onAddMessage={onAddMessage}
          onAddQuestion={onAddQuestion}
          onAddAction={onAddAction}
          onAddCondition={onAddCondition}
          onOpenFloatMenu={onOpenFloatMenu}
          onClose={onCloseFloatMenu}
        />
        <ElemMenuTemplate
          elems={elems}
          currentElem={currentElem}
          floatMenu={floatMenu}
          top={top}
          left={left}
          onSetTemplate={onSetTemplate}
          onClose={onCloseFloatMenu}
        />
        <ElemMenuButtonsNextStep
          flowType={flowType}
          elems={elems}
          currentElem={currentElem}
          currentBtn={currentBtn}
          floatMenu={floatMenu}
          top={top}
          left={left}
          bottom={bottom}
          onRenameBtn={onRenameBtn}
          onLinkElem={onLinkElem}
          onAddLinkElem={onAddLinkElem}
          onUnlinkElem={onUnlinkElem}
          onAddUrl={onAddUrl}
          onRemoveUrl={onRemoveUrl}
          onClose={onCloseFloatMenu}
        />
        <ElemMenuAnswer
          fields={fields}
          currentElem={currentElem}
          currentItem={currentItem}
          floatMenu={floatMenu}
          top={top}
          left={left}
          bottom={bottom}
          onSubmit={onAddAnswer}
          onClose={onCloseFloatMenu}
          onCreateField={onCreateField}
          onOpenFloatMenu={onOpenFloatMenu}
        />
        <ElemMenuNextStep
          elems={elems}
          currentElem={currentElem}
          floatMenu={floatMenu}
          top={top}
          left={left}
          bottom={bottom}
          onLinkElem={onLinkElem}
          onClose={onCloseFloatMenu}
        />
      </Fragment>
    )
  }
};

export default ElemMenu;