import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux';

import ModalController from './ModalController'

import PageLayout from '../components/common/PageLayout'
import Placeholder from '../components/common/Placeholder'
import Loader from '../components/common/Loader'
import Button from '../components/common/Button'
import AccountsTable from '../components/common/AccountsTable'

import { settingsActions, modalActions } from '../actions/index'

class AccountsController extends Component {
  state = {
    action: 'Добавить аккаунт'
  }

  showAccounts = () => {
    const { settings: { loading, data }, openModal, setCurrentElem } = this.props

    if (loading.length) {
      return <Loader />
    }

    if (!data.adapters.length) {
      return (
        <Placeholder
          name="accounts"
          width={216}
        >
          <div>Добавьте существующий аккаунт telegram, whats’up, facebook, viber или <br />
           vkontakte, к которому в дальнейшем можно будет привязать бот.</div>
          <Button
            onClick={() => openModal('addAccount')}
            modifier='placeholder'
          >
            Добавить аккаунт
          </Button>
        </Placeholder>
      )
    }

    return (
      <AccountsTable
        items={data.adapters}
        onOpenModal={openModal}
        onSetCurrentElem={setCurrentElem}
      />
    )
  }

  render() {
    const { settings: { data }, openModal } = this.props
    const { action } = this.state

    return (
      <Fragment>
        <PageLayout
          title="Аккаунты"
          action={data.adapters.length ? action : null}
          onClick={() => openModal('addAccount')}
        >
          {this.showAccounts()}
        </PageLayout>
        <ModalController />
      </Fragment>

    );
  }
}

const mapStateToProps = (state) => {
  const { basic, settings, loading, error } = state

  return {
    basic,
    settings,
    loading,
    error,
  };
};

const mapDispatchToProps = {
  addChannel: settingsActions.channel.add,
  removeChannel: settingsActions.channel.remove,
  setCurrentElem: settingsActions.currentelem.set,
  openModal: modalActions.modal.open,
  closeModal: modalActions.modal.close,
};

export default connect(mapStateToProps, mapDispatchToProps)(AccountsController)