import React, { useState } from 'react';

import DataTable from '../DataTable';
import Percentage from '../Percentage';
import Switch from '../Switch';

import './AutoMailingsTable.scss';

export default function AutoMailingsTable({ items = [], onEdit, onDelete, ...rest }) {
  const onSwitch = (state, value) => {
    console.log('onSwitch');
    console.log(state, value);
  }

  return (
    <DataTable
      {...rest}
    >
      <DataTable.HeadRow>
        <DataTable.HeadCell width={400}>
          Название
        </DataTable.HeadCell>

        <DataTable.HeadGroup>
          <DataTable.HeadCell width={125}>
            Дата создания
        </DataTable.HeadCell>
          <DataTable.HeadCell width={125}>
            Добавлено
        </DataTable.HeadCell>
          <DataTable.HeadCell width={125}>
            В пути
        </DataTable.HeadCell>
          <DataTable.HeadCell width={125}>
            Дошли до конца
        </DataTable.HeadCell>
          <DataTable.HeadCell width={125}>
            Прервали
          </DataTable.HeadCell>
          <DataTable.HeadCell type="actions" />
        </DataTable.HeadGroup>
      </DataTable.HeadRow>

      <DataTable.Body className='table-body'>
        {
          items.map((item, index) => {
            return (
              <DataTable.Row
                key={index}
                isDisabled={!item.enabled}
                className='table-row'
              >
                <Switch
                  enabled={item.enabled}
                  onChange={onSwitch}
                />

                <DataTable.Cell width={325}>
                  {item.name}
                </DataTable.Cell>

                <DataTable.Group className='table-group'>
                  <DataTable.Cell width={125}>
                    {item.createdAt}
                  </DataTable.Cell>
                  <DataTable.Cell width={120}>
                    {item.added}
                  </DataTable.Cell>
                  <DataTable.Cell width={120}>
                    <Percentage
                      value={item.pending}
                      from={item.added}
                    />
                  </DataTable.Cell>
                  <DataTable.Cell width={120}>
                    <Percentage
                      value={item.finished}
                      from={item.added}
                    />
                  </DataTable.Cell>
                  <DataTable.Cell width={120}>
                    <Percentage
                      value={item.interrupted}
                      from={item.added}
                    />
                  </DataTable.Cell>
                  <DataTable.Cell type="actions">
                    <DataTable.Actions
                      actions={[
                        {
                          type: 'edit',
                          handler: () => onEdit(item.id)
                        },
                        {
                          type: 'copy',
                          handler: () => console.log('copy')
                        },
                        {
                          type: 'remove',
                          handler: onDelete
                        },
                      ]}
                    />
                  </DataTable.Cell>
                </DataTable.Group>
              </DataTable.Row>
            )
          })
        }
      </DataTable.Body>
    </DataTable>
  )
}
