import React, { Fragment, useState } from 'react';
import cx from 'classnames'

import Select from '../../../common/Select'

const ElemMenuDelay = ({
  currentElem,
  onChangeDelayValue,
  onChangeDelayPeriod,
  onChangeExact,
  onChangeFromTo,
  onChangeSchedule
}) => {
  const [error, setError] = useState({
    fromHour: false,
    fromMinute: false,
    toHour: false,
    toMinute: false,
  });

  const periodOptions = [
    { value: 'minute', label: 'Минут' },
    { value: 'hour', label: 'Часов' },
    { value: 'day', label: 'Дней' },
  ];

  const { value, period, fromTo, exact, schedule } = currentElem.data

  const activeOption = periodOptions.find(item => item.value === period.value)

  return <Fragment>
    <div className="constructor-elemmenu__label">Длительность задержки</div>
    <div className="constructor-elemmenu__row">
      <input
        type="text"
        maxLength={2}
        value={value}
        className="constructor-elemmenu__input constructor-elemmenu__input_small"
        onChange={(e) => {
          onChangeDelayValue(e.target.value)
        }}
      />
      <Select
        placeholder='Выберите поле'
        options={periodOptions}
        activeOption={activeOption}
        modifier='delay'
        onChange={onChangeDelayPeriod}
      />
    </div>

    <div className={
      cx('constructor-elemmenu__exact',
        {
          'constructor-elemmenu__exact_active': exact
        })
    } onClick={onChangeExact}>
      Указать время, когда продолжить следующий шаг
    </div>

    {exact ? <Fragment>
      <div className="constructor-elemmenu__label">Продолжить между</div>
      <div className="constructor-elemmenu__row">
        <div className="constructor-elemmenu__label constructor-elemmenu__label_col">C:</div>
        <div className="constructor-elemmenu__label constructor-elemmenu__label_col">До:</div>
      </div>
      <div className="constructor-elemmenu__row">
        <div className="constructor-elemmenu__row">
          <input
            type="text"
            maxLength={2}
            value={fromTo.fromHour}
            className={
              cx(
                'constructor-elemmenu__input',
                'constructor-elemmenu__input_small',
                {
                  'constructor-elemmenu__input_error': error.fromHour
                })
            }
            onChange={(e) => {
              setError({ ...error, fromHour: false })
              if (e.target.value > 23) {
                onChangeFromTo('fromHour', '00')
                setError({ ...error, fromHour: true })
              } else {
                onChangeFromTo('fromHour', e.target.value)
              }
            }}
            onBlur={(e) => {
              if (e.target.value < 10) {
                onChangeFromTo('fromHour', `0${+e.target.value}`)
              }
            }}
          />
        &nbsp;:&nbsp;
        <input
            type="text"
            maxLength={2}
            value={fromTo.fromMinute}
            className={
              cx(
                'constructor-elemmenu__input',
                'constructor-elemmenu__input_small',
                {
                  'constructor-elemmenu__input_error': error.fromMinute
                })
            }
            onChange={(e) => {
              setError({ ...error, fromMinute: false })
              if (e.target.value > 59) {
                onChangeFromTo('fromMinute', '00')
                setError({ ...error, fromMinute: true })
              } else {
                onChangeFromTo('fromMinute', e.target.value)
              }
            }}
            onBlur={(e) => {
              if (e.target.value < 10) {
                onChangeFromTo('fromMinute', `0${+e.target.value}`)
              }
            }}
          />
        </div>
        <div className="constructor-elemmenu__row">
          <input
            type="text"
            maxLength={2}
            value={fromTo.toHour}
            className={
              cx(
                'constructor-elemmenu__input',
                'constructor-elemmenu__input_small',
                {
                  'constructor-elemmenu__input_error': error.toHour
                })
            }
            onChange={(e) => {
              setError({ ...error, toHour: false })
              if (e.target.value > 23) {
                onChangeFromTo('toHour', '00')
                setError({ ...error, toHour: true })
              } else {
                onChangeFromTo('toHour', e.target.value)
              }
            }}
            onBlur={(e) => {
              if (e.target.value < 10) {
                onChangeFromTo('toHour', `0${+e.target.value}`)
              }
            }}
          />
        &nbsp;:&nbsp;
        <input
            type="text"
            maxLength={2}
            value={fromTo.toMinute}
            className={
              cx(
                'constructor-elemmenu__input',
                'constructor-elemmenu__input_small',
                {
                  'constructor-elemmenu__input_error': error.toMinute
                })
            }
            onChange={(e) => {
              setError({ ...error, toMinute: false })
              if (e.target.value > 59) {
                onChangeFromTo('toMinute', '00')
                setError({ ...error, toMinute: true })
              } else {
                onChangeFromTo('toMinute', e.target.value)
              }
            }}
            onBlur={(e) => {
              if (e.target.value < 10) {
                onChangeFromTo('toMinute', `0${+e.target.value}`)
              }
            }}
          />
        </div>
      </div>
      <div className="constructor-elemmenu__row constructor-elemmenu__row_schedule">
        <div className="constructor-elemmenu__col">
          <div className={
            cx('constructor-elemmenu__schedule-item',
              {
                'constructor-elemmenu__schedule-item_active constructor-elemmenu__schedule-item_violet': schedule.mon
              })
          }
            onClick={(e) => {
              onChangeSchedule('mon')
            }}
          >Понедельник</div>
          <div className={
            cx('constructor-elemmenu__schedule-item',
              {
                'constructor-elemmenu__schedule-item_active constructor-elemmenu__schedule-item_violet': schedule.wed
              })
          }
            onClick={(e) => {
              onChangeSchedule('wed')
            }}
          >Среда</div>
          <div className={
            cx('constructor-elemmenu__schedule-item',
              {
                'constructor-elemmenu__schedule-item_active constructor-elemmenu__schedule-item_violet': schedule.fri
              })
          }
            onClick={(e) => {
              onChangeSchedule('fri')
            }}
          >Пятница</div>
          <div className={
            cx('constructor-elemmenu__schedule-item',
              {
                'constructor-elemmenu__schedule-item_active constructor-elemmenu__schedule-item_violet': schedule.sun
              })
          }
            onClick={(e) => {
              onChangeSchedule('sun')
            }}
          >Воскресенье</div>
        </div>
        <div className="constructor-elemmenu__col">
          <div className={
            cx('constructor-elemmenu__schedule-item',
              {
                'constructor-elemmenu__schedule-item_active constructor-elemmenu__schedule-item_violet': schedule.tue
              })
          }
            onClick={(e) => {
              onChangeSchedule('tue')
            }}
          >Вторник</div>
          <div className={
            cx('constructor-elemmenu__schedule-item',
              {
                'constructor-elemmenu__schedule-item_active constructor-elemmenu__schedule-item_violet': schedule.thu
              })
          }
            onClick={(e) => {
              onChangeSchedule('thu')
            }}
          >Четверг</div>
          <div className={
            cx('constructor-elemmenu__schedule-item',
              {
                'constructor-elemmenu__schedule-item_active constructor-elemmenu__schedule-item_violet': schedule.sat
              })
          }
            onClick={(e) => {
              onChangeSchedule('sat')
            }}
          >Суббота</div>
        </div>
      </div>
    </Fragment> : null}
  </Fragment>
};

export default ElemMenuDelay;