import React, { Fragment, useState } from 'react';

import Icon from '../../common/Icon';
import Button from '../../common/Button';

import Modal from '../Modal'

import './AddTelegramAccountModal.scss';

const AddTelegramAccountModal = ({ onOpenModal, onCloseModal, onAddChannel }) => {
  const [areRulesOpened, setRulesState] = useState(false);
  const [value, setValue] = useState('');

  return (
    <Modal
      title='Добавить аккаунт Telegram'
      onBack={() => onOpenModal('addAccount')}
      onClose={onCloseModal}
    >
      <div className="modal-content modal-content_left">
        <section className='account-addition'>
          <div className='account-addition-disclaimer'>
            <Icon
              name="telegram"
              width={30}
              className='account-addition-type'
            />
            <span>Для добавления аккаунта Telegram необходимо создать отдельного бота Telegram и ввести его токен в поле ниже:</span>
          </div>
          <form className='telegram-bot-add-form'>
            <input
              type="text"
              value={value}
              onChange={(e => setValue(e.target.value))}
              placeholder="Введите сюда Токен-ключ"
              className='modal-input'
            />

            <footer className='telegram-bot-add-form-footer'>
              <Button
                onClick={() => {
                  onAddChannel(value, 'telegram')
                  onCloseModal()
                }}
              >
                Добавить бота Telegram
              </Button>

              <span className='telegram-bot-add-more'>Подробнее о добавлении бота Telegram</span>
            </footer>
          </form>
          <section
            className='telegram-bot-add-instruction'
          >
            <h3 className='telegram-bot-add-instruction-header'>Как создать бота и где взять токен ключ?</h3>
            <p>1. Зайдите в приложение Telegram c существующего аккаунта (владельцем будущего бота будет аккаунт, с которого был создан бот).</p>
            <p>2. В поиске Telegram найдите аккаунт @BotFather (справа от названия аккаунта будет синяя галочка, а на аватарке крестный отец с пачкой денег).</p>
            <p>3. Напишите следующее сообщение этому аккаунту: “/newbot”.</p>
            {areRulesOpened ?
              <Fragment>
                <p>4. Telegram попросит вас написать имя вашего будущего бота (оно будет отображаться в списке контактов пользователей и в диалогах с ними).</p>
                <p>5. Telegram попросит вас написать ник вашего будущего бота (должен обязательно заканчиваться на “bot”, например, ServiceBot или Service_Bot).</p>
                <p>6. Telegram отправит вам такое сообщение:</p>
                <blockquote className='telegram-bot-add-example'>
                  <p>Done! Congratulations on your new bot. You will find it at t.me/ServiceBot.
                  You can now add a description, about section and profile picture for your bot, see /help for a list of commands.
                  By the way, when you've finished creating your cool bot, ping our Bot Support if you want a better
                  username for it. Just make sure the bot is fully operational before you do this.</p>
                  <p>Use this token to access the HTTP API:
      <br />
                    <code className='telegram-bot-add-token-code'>105859328409:AADa5Hg-rH5QSV0BkJCeei0wRiKvG6lVDMo</code>
                    <br />
    Keep your token secure and store it safely, it can be used by anyone to control your bot.</p>

    For a description of the Bot API, see this page: https://core.telegram.org/bots/api</blockquote>
                <p>7. Текст <code className='telegram-bot-add-token'>105859328409:AADa5Hg-rH5QSV0BkJCeei0wRiKvG6lVDMo</code>
                и есть необходимый для создания вашего бота токен. Скопируйте и вставьте его в поле выше в данном окне.</p>
              </Fragment>
              : null
            }
          </section>
        </section>

      </div>
      <div className="modal-footer" >
        <span
          className="modal-footer-title"
          onClick={() => setRulesState(!areRulesOpened)}>
          {areRulesOpened ? 'свернуть' : 'читать далее'}
        </span>
      </div>
    </Modal>
  )
}

export default AddTelegramAccountModal
