import React, { memo, Fragment } from 'react';
import cx from 'classnames'
import { Handle } from 'react-flow-renderer';

export default memo(({
  id,
  data,
  isActive,
  onCopyElem,
  onUnlinkElem,
  onRemoveElem
}) => {
  const { items, text } = data

  return (
    <div className={cx(
      'flow-block',
      'flow-block_condition',
      {
        'flow-block_active': isActive
      }
    )}>
      <div className='flow-block__copy' onClick={() => onCopyElem(id)} />
      <div className='flow-block__delete' onClick={() => onRemoveElem(id)} />
      <div className='flow-block__title flow-block__title_condition'>{text}</div>
      <Handle
        type="target"
        position="left"
        style={{ background: '#fff', top: 20, border: '1px solid #59D965' }}
        onConnect={({ source, target }) => onUnlinkElem(source, target)}
      />
      <div className='flow-block__conditions'>
        {items && items.length ? items.map((item, i) => {
          return (
            <div key={item.id} className='flow-block__conditions-item'>
              {item.key}
              <span className='flow-block__conditions-label'>{item.condition}</span>
              {item.value}
            </div>
          )
        }) : <div className="flow-block__placeholder flow-block__placeholder_condition">Добавьте сюда условие</div>
        }
      </div>
      <Handle type="source" position="right" id="if"
        style={{ bottom: 80, top: 'auto' }} />
      <hr />
      <div className="flow-block__nextstep">
        Если подписчик не соответствует ни<br />одному из этих условий
      <Handle type="source" position="right" id="else"
          style={{ bottom: 7, top: 'auto' }} />
      </div>
    </div>
  );
});
