import { takeLatest, put, call } from "redux-saga/effects";
import {
  authActions,
} from '../actions'

import API, { getData, postData } from '../api'
import { getDomain } from '../const/urlHelper'

const { auth } = authActions

export function* watchAuth() {
  yield takeLatest(auth.login.request.toString(), authLogin);
  yield takeLatest(auth.reg.request.toString(), authReg);
  yield takeLatest(auth.check.request.toString(), regCheck);
  yield takeLatest(auth.forget.request.toString(), authForget);
}

function* authLogin(action) {
  const { domain, password } = action.payload

  try {
    let payload = yield call(postData, `http://auth.${getDomain()}/login`, { login: domain, password });

    console.log('auth payload')
    console.log(payload)

    if (payload.sessionId) {
      yield put(auth.login.success(payload))
    } else {
      yield put(auth.login.failure(payload.error))
    }

  } catch (err) {
    console.log('auth err')
    console.log(err)
    yield put(auth.login.failure(err))
  }
}

function* authReg(action) {
  const { domain, email, password } = action.payload

  try {
    let payload = yield call(postData, `http://auth.${getDomain()}/signup`, { login: domain, email, password });

    console.log('signup payload')
    console.log(payload)

    if (payload.location) {
      yield put(auth.reg.success(payload.token))

    } else {
      yield put(auth.reg.failure('Ошибка регистрации'))
    }

  } catch (e) {
    yield put(auth.reg.failure('Ошибка регистрации'))
  }
}

function* regCheck(action) {
  console.log('regCheck');
  console.log(action.payload);
  let check = yield call(getData, `http://auth.${getDomain()}/signup/check?token=${action.payload}`);

  console.log('check')
  console.log(check)

  if (check.location && check.status === 'done') {
    yield put(auth.check.success(check.location))
  }
}

function* authForget(action) {
  const { email } = action.payload

  try {
    let payload = yield call(postData, `http://auth.${getDomain()}/recover/request-email`, { email });

    console.log('forget payload')
    console.log(payload)

    if (payload) {
      yield put(auth.forget.success(payload))
    } else {
      yield put(auth.forget.failure('Ошибка сброса пароля'))
    }

  } catch (e) {
    yield put(auth.forget.failure('Ошибка сброса пароля'))
  }
}
