import React from 'react';

const ElemMenuConditionItem = ({
  item,
  equal,
  onOpenFloatMenu,
  onRemoveConditionItem,
}) => {
  return (
    <div className='constructor-elemmenu__condition-item'
      onClick={(e) => onOpenFloatMenu(e, 'editCondition')}
    >
      <span className="constructor-elemmenu__condition-text">{item.key}</span>
      &nbsp;{item.condition}&nbsp;
      <span className="constructor-elemmenu__condition-text">{item.value}</span>
      <div className="constructor-elemmenu__condition-remove"
        onClick={(e) => {
          e.stopPropagation()
          onRemoveConditionItem(item.id)
        }}
      >×</div>
    </div>
  )
};

export default ElemMenuConditionItem;