import React, { useState, Fragment } from 'react';
import cx from 'classnames'
import Button from '../../common/Button'
import Icon from '../../common/Icon'

import Modal from '../Modal'

const AddViberBotModal = ({ basic, onOpenModal, onCloseModal, onSetCurrentElem, onAddBot }) => {
  const [activeChannel, setActiveChannel] = useState(false);
  const [value, setValue] = useState('');

  if (!basic) {
    return null
  }

  const channels = basic.account.adapters.filter(item => item.enabled && item.adapterType === 'viber')

  return (
    <Modal
      title='Создание нового бота Viber'
      onBack={() => onOpenModal('addBot')}
      onClose={onCloseModal}
    >
      <Fragment>
        Название бота:
      <input
          type="text"
          className='modal-input'
          placeholder="Введите название бота"
          value={value}
          onChange={(e) => setValue(e.target.value)}
        />

        <section className='account-creation'>
          <h3 className='account-creation-title'>К какому аккаунту привязать бот?</h3>

          <div>
            <p>Каждый бот должен быть привязан к существующему аккаунту в соответствующем мессенджере.</p>
            <p>Для создания бота вам сначала необходимо  добавить существующий аккаунт Viber в наш сервис.</p>
          </div>

          <section className='accounts-list'>
            {channels.map(item => {
              return (
                <article
                  key={item.adapterConnectorId}
                  className={
                    cx(
                      'accounts-list-item',
                      {
                        'accounts-list-item_active': activeChannel === item.adapterConnectorId
                      })
                  }
                  onClick={() => {
                    setActiveChannel(item.adapterConnectorId)
                    onSetCurrentElem({ connectorId: item.adapterConnectorId, type: item.adapterType })
                  }}
                >
                  <div className='accounts-list-item-info'>{item.nick}</div>

                  <footer className='accounts-list-item-footer'>
                    <Icon name="viber" width={10} className='accounts-list-item-type' />
                    аккаунт Viber
                  </footer>
                </article>
              )
            })}

            <div
              className='accounts-list-item-new'
              onClick={() => {
                onSetCurrentElem({})
                onOpenModal('addAccount')
              }}>
              <Icon name="plus" width={15} className='accounts-list-item-new-icon' />
              Добавить аккаунт
            </div>
          </section>
        </section>
      </Fragment>
      <div className="modal-footer" >
        <div className="modal-buttons">
          <Button modifier='cancel' onClick={onCloseModal}>Отмена</Button>
          {value && activeChannel ? <Button
            onClick={() => {
              onAddBot(value, 'viber')
              onCloseModal()
            }}
          >
            Создать Viber бот
        </Button> : null}
        </div>
      </div>
    </Modal>
  );
}

export default AddViberBotModal
