import React, { Fragment, useState } from 'react';
import cx from 'classnames'

import Select from '../../../common/Select'

const NAMES_MAP = {
  addField: 'Установить значение для польз. поля',
  removeField: 'Очистить пользовательское поле',
  increaseField: 'Увеличить числовое значение \n пользовательского поля на',
  decreaseField: 'Уменьшить числовое значение \n пользовательского поля на',
}

const ElemMenuFieldItem = ({
  item,
  fields,
  onCopyItem,
  onRemoveItem,
  onMoveItem,
  onAddField,
  onChangeField,
  onCreateField
}) => {
  const [fieldName, setFieldName] = useState('');

  const showFieldValue = () => {
    if (fieldName && item.itemType === 'addField') {
      return (
        <Fragment>
          <div className="constructor-elemmenu__label">Значение</div>
          <input
            className='constructor-elemmenu__input'
            placeholder='Введите значение'
            type='text'
            value={item.fieldValue}
            onChange={(e) => {
              onChangeField(item.id, e.target.value)
            }}
          />
        </Fragment>
      )
    }
    if (fieldName && item.itemType === 'increaseField'
      && fields.find(f => f.label === item.data.field)
      && fields.find(f => f.label === item.data.field).type === 'number') {
      return (
        <Fragment>
          <div className="constructor-elemmenu__label">Значение</div>
          <input
            className='constructor-elemmenu__input'
            placeholder='Введите значение'
            type='text'
            value={item.fieldValue}
            onChange={(e) => {
              onChangeField(item.id, e.target.value, 'increase')
            }}
          />
        </Fragment>
      )
    }
    if (fieldName && item.itemType === 'decreaseField'
      && fields.find(f => f.label === item.data.field)
      && fields.find(f => f.label === item.data.field).type === 'number') {
      return (
        <Fragment>
          <div className="constructor-elemmenu__label">Значение</div>
          <input
            className='constructor-elemmenu__input'
            placeholder='Введите значение'
            type='text'
            value={item.fieldValue}
            onChange={(e) => {
              onChangeField(item.id, e.target.value, 'decrease')
            }}
          />
        </Fragment>
      )
    }
  }

  return (
    <Fragment>
      <div className={
        cx(
          'constructor-elemmenu__label',
          `constructor-elemmenu__label_${item.itemType}`
        )
      }>{NAMES_MAP[item.itemType]}</div>
      <div className="constructor-elemmenu__action" key={item.id} >
        <div className="constructor-elemmenu__operations">
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_delete"
            onClick={() => onRemoveItem(item.id)}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_up"
            onClick={() => onMoveItem(item.id, 'up')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_down"
            onClick={() => onMoveItem(item.id, 'down')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_copy"
            onClick={() => onCopyItem(item.id, 0)}
          />
        </div>
        <div className="constructor-elemmenu__label">Поле</div>
        <Select
          placeholder='Выберите поле'
          options={fields}
          isNewField={true}
          modifier='fields'
          onCreateField={onCreateField}
          onChange={(field) => {
            setFieldName(field.value)
            onAddField(item.id, field.label)
          }}
        />
        <br />
        {showFieldValue()}
      </div>
    </Fragment>
  )

};

export default ElemMenuFieldItem;