import React from 'react';
import cx from 'classnames'

import Button from '../../common/Button'
import Modal from '../Modal'

const RemoveElemModal = ({ onCloseModal, onRemoveElem, removeElemWithoutConfirm, onRemoveConfirm }) => {
  return (
    <Modal
      title='Вы уверены что хотите удалить данный блок?'
      onClose={onCloseModal}
    >
      <div className="modal-content">
        Обратите внимание, что после удаления
        все данные блока будут удалены без возможности восстановления.
      </div>
      <div className="modal-footer" >
        <div className="modal-settings">
          <div className={
            cx(
              'modal-confirm',
              { 'modal-confirm_active': removeElemWithoutConfirm }
            )
          }
            onClick={() => onRemoveConfirm(!removeElemWithoutConfirm)}
          >Больше не спрашивать</div>
          <div className="modal-buttons">
            <Button modifier='cancel' onClick={() => {
              onCloseModal()
            }}>Отмена</Button>
            <Button modifier='remove' onClick={() => {
              onRemoveElem()
              onCloseModal()
            }}>Удалить блок</Button>
          </div>
        </div>
      </div>
    </Modal>
  );
};

export default RemoveElemModal;