import React, { useState, Fragment } from 'react';
import OutsideClickHandler from 'react-outside-click-handler';
import ImageUploading from "react-images-uploading";

import ElemMenuAddDetail from './ElemMenuAddDetail'

import FloatMenu from '../FloatMenu'

const ElemMenuAdd = ({
  elems,
  tags,
  fields,
  floatMenu,
  top,
  left,
  bottom,
  onChangeFile,
  onChangeImg,
  onAddMessage,
  onAddQuestion,
  onAddAction,
  onAddCondition,
  onOpenFloatMenu,
  onClose
}) => {
  const [menuDetail, toggleMenuDetail] = useState('');

  const customFields = fields.map(field => {
    if (field.value === 'name' || field.value === 'email' || field.value === 'phone') {
      return null
    }
    return field
  }).filter(f => !!f)

  if (floatMenu === 'addMessage') {
    return (
      <OutsideClickHandler
        onOutsideClick={() => {
          onClose()
        }}
      >
        <FloatMenu
          coords={{ top, left, bottom }}
          onClose={onClose}
          modifier={floatMenu}
        >
          <div className="constructor-elemmenu__add-menu">
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onAddMessage()
                onClose()
              }}
            >
              Текст + кнопки</div>
            <ImageUploading
              onChange={onChangeImg}
            >
              {({
                onImageUpload,
              }) => (
                <div
                  className="constructor-elemmenu__add-item"
                  onClick={(e) => {
                    onImageUpload()
                  }}>
                  Изображение
                </div>
              )}
            </ImageUploading>
            <div className="constructor-elemmenu__add-item">
              <label className='constructor-elemmenu__add-label'>
                <input className='constructor-elemmenu__add-file' type="file" multiple onChange={onChangeFile} />Файл
              </label>
            </div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onAddQuestion()
                onClose()
              }}
            >Вопрос</div>
          </div>
        </FloatMenu>
      </OutsideClickHandler>
    )
  }
  if (floatMenu === 'addAction') {
    return (
      <OutsideClickHandler
        onOutsideClick={() => {
          onClose()
        }}
      >
        <FloatMenu
          coords={{ top, left }}
          onClose={onClose}
          modifier={floatMenu}
        >
          <div className="constructor-elemmenu__add-menu">
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onAddAction('addTag')
                onClose()
              }}
            >Добавить метку</div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onAddAction('removeTag')
                onClose()
              }}
            >Удалить метку</div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onAddAction('addField')
                onClose()
              }}
            >Установить значение для польз. поля</div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onAddAction('removeField')
                onClose()
              }}
            >Очистить пользовательское поле</div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onAddAction('increaseField')
                onClose()
              }}
            >Увеличить числовое значение польз. поля на</div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onAddAction('decreaseField')
                onClose()
              }}
            >Уменьшить числовое значение польз. поля на</div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onAddAction('sendEmail')
                onClose()
              }}
            >Отправить данные подписчика на эл. почту</div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onAddAction('subscribe')
                onClose()
              }}
            >Подписать на бота</div>
            <div className="constructor-elemmenu__add-item"
              onClick={() => {
                onAddAction('unsubscribe')
                onClose()
              }}
            >Отписать от бота</div>
          </div>
        </FloatMenu>
      </OutsideClickHandler>
    )
  }
  if (floatMenu === 'addCondition' || floatMenu === 'editCondition') {
    return (
      <OutsideClickHandler
        onOutsideClick={() => {
          toggleMenuDetail('')
          onClose()
        }}
      >
        <FloatMenu
          coords={{ top, left }}
          modifier={floatMenu}
          onClose={() => {
            toggleMenuDetail('')
            onClose()
          }}
        >
          <div className="constructor-elemmenu__add-menu">
            {menuDetail
              ? <ElemMenuAddDetail
                elems={elems}
                tags={tags}
                menuDetail={menuDetail}
                onSubmit={onAddCondition}
                onCancel={() => toggleMenuDetail('')}
                onOpenFloatMenu={onOpenFloatMenu}
                onClose={onClose}
              />
              : <Fragment>
                <div className="constructor-elemmenu__add-item"
                  onClick={() => toggleMenuDetail('tag')}
                >Метка</div>
                <div className="constructor-elemmenu__add-item"
                  onClick={() => toggleMenuDetail('dateTime')}
                >Текущая дата/время</div>
                <div className="constructor-elemmenu__add-item"
                  onClick={() => toggleMenuDetail('link')}
                >Переход по ссылке в шаге</div>
                <div className="constructor-elemmenu__add-title">Системные поля</div>
                <div className="constructor-elemmenu__add-item"
                  onClick={() => toggleMenuDetail('name')}
                >Имя</div>
                <div className="constructor-elemmenu__add-item"
                  onClick={() => toggleMenuDetail('email')}
                >Email</div>
                <div className="constructor-elemmenu__add-item"
                  onClick={() => toggleMenuDetail('phone')}
                >Телефон</div>
                {customFields.length ? <Fragment>
                  <div className="constructor-elemmenu__add-title">Пользовательские поля</div>
                  {customFields.map(field => {
                    return (
                      <div className="constructor-elemmenu__add-item"
                        onClick={() => toggleMenuDetail(field.value)}
                      >{field.label}</div>
                    )
                  })}
                </Fragment> : null}
              </Fragment>
            }
          </div>
        </FloatMenu>
      </OutsideClickHandler>
    )

  };

  return null
}



export default ElemMenuAdd;