import React, { Component } from 'react'
import { connect } from 'react-redux';
import { authActions } from '../actions/index'

import { domainPattern, emailPattern } from '../const/validate-patterns'

import {
  Link, Redirect
} from 'react-router-dom'

class RegController extends Component {
  state = {
    domain: '',
    email: '',
    password: '',
    domainError: '',
    emailError: '',
    passwordError: '',
  }

  handleSubmit = () => {
    const { authReg } = this.props
    const { domain, email, password } = this.state

    if (!domainPattern.test(domain)) {
      this.setState({ domainError: 'Укажите корректный домен. Разрешены только символы (a-z) и цифры (0-9)' })
      return
    }

    if (!emailPattern.test(email)) {
      this.setState({ emailError: 'Укажите корректный email' })
      return
    }

    if (!password.length) {
      this.setState({ passwordError: 'пароль не может быть пустым' })
      return
    }

    authReg(domain, email, password)
  }

  render() {
    const { auth } = this.props
    const { domain, email, password, domainError, emailError, passwordError } = this.state

    if (auth.loading) {
      return <h2>Loading...</h2>
    }

    if (auth.token) {
      return <Redirect to={'/signup/wait'} />
    }

    return (
      <div className="auth__block">
        <Link className="auth__back" to={`/`}>×</Link>
        <div className="auth__title">Регистрация</div>
        <div className="auth__field auth__field_domain">
          <input
            value={domain}
            onChange={(e) => this.setState({ domain: e.target.value, domainError: '' })}
            type="text"
            className="auth__input"
            placeholder='укажите домен'
          />
          <div className="auth__error">{domainError}</div>
        </div>
        <div className="auth__field">
          <input
            value={email}
            onChange={(e) => this.setState({ email: e.target.value, emailError: '' })}
            type="text"
            className="auth__input"
            placeholder='укажите email'
          />
          <div className="auth__error">{emailError}</div>
        </div>
        <div className="auth__field">
          <input
            value={password}
            onChange={(e) => this.setState({ password: e.target.value, passwordError: '' })}
            type="password"
            className="auth__input"
            placeholder='укажите пароль'
          />
          <div className="auth__error">{passwordError}</div>
          <div className="auth__error">{auth.error}</div>
        </div>

        <div className="auth__button"
          onClick={this.handleSubmit}
        >Зарегистрироваться</div>
        <div className="auth__info">
          Нажимая на кнопку «зарегистрироваться», вы принимаете условия <Link to={`/`}>договора</Link>
        </div>
        <div className="auth__footer">
          <div className="auth__text">Уже есть аккаунт?</div>
          <Link to={`/auth`}>Войти</Link>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  const { auth } = state

  return {
    auth
  };
};

const mapDispatchToProps = {
  authReg: authActions.auth.reg.request,
};

export default connect(mapStateToProps, mapDispatchToProps)(RegController)