import React from 'react';
import classnames from 'classnames';

import Icon from '../Icon';

import styles from './ProjectInfo.module.scss';

export default function ProjectInfo({ className, item }) {
  if (!item) {
    return null
  }

  const { type, picture, nick, systemIdentifier } = item;

  const infoClasses = classnames({
    [styles['project-info']]: true,
    [className]: !!className,
  });

  return (
    <article className={infoClasses}>
      <Icon name={type} width={30} />
      <div className={styles['project-info-meta']}>
        {picture ? <img src={picture} width={30} alt={systemIdentifier} /> : null}
        <span className={styles['project-info-project-name']}>
          {nick}
        </span>
      </div>
    </article>
  );
}
