import React, { Fragment, useState, useEffect } from 'react';
import OutsideClickHandler from 'react-outside-click-handler';
import cx from 'classnames'

import ElemMenuEditor from './ElemMenuEditor'
import Select from '../../common/Select'
import FloatMenu from '../FloatMenu'

const ElemMenuAnswer = ({
  fields,
  currentElem,
  currentItem,
  floatMenu,
  top,
  left,
  bottom,
  onSubmit,
  onCreateField,
  onOpenFloatMenu,
  onClose
}) => {
  const [answerField, setAnswerField] = useState(false);
  const [answerCorrect, setAnswerCorrect] = useState(false);
  const [answerRepeat, setAnswerRepeat] = useState(false);
  const [answerFormat, setAnswerFormat] = useState('');
  const [wrongAnswerText, setWrongAnswerText] = useState('');
  const [repeatAnswerText, setRepeatAnswerText] = useState('');
  const [readyAnswer, setReadyAnswer] = useState('');
  const [readyAnswers, setReadyAnswers] = useState('');
  const [repeatHours, setRepeatHours] = useState(0);
  const [repeatMinutes, setRepeatMinutes] = useState(0);
  const [error, setError] = useState({});

  useEffect(() => {
    const item = currentElem.data && currentElem.data.items ? currentElem.data.items.find(item => item.id === currentItem) : null

    if (item && item.data && Object.keys(item.data).length) {
      const {
        answerField,
        answerCorrect,
        answerRepeat,
        answerFormat,
        wrongAnswerText,
        repeatAnswerText,
        readyAnswers,
        repeatHours,
        repeatMinutes
      } = item.data

      setAnswerField(answerField)
      setAnswerCorrect(answerCorrect)
      setAnswerRepeat(answerRepeat)
      setAnswerFormat(answerFormat)
      setWrongAnswerText(wrongAnswerText)
      setRepeatAnswerText(repeatAnswerText)
      setReadyAnswers(readyAnswers)
      setRepeatHours(repeatHours)
      setRepeatMinutes(repeatMinutes)
    }

    return () => {
      setAnswerField(false)
      setAnswerCorrect(false)
      setAnswerRepeat(false)
      setAnswerFormat('')
      setWrongAnswerText('')
      setRepeatAnswerText('')
      setReadyAnswers('')
      setRepeatHours(0)
      setRepeatMinutes(0)
    };

  }, [currentElem, currentItem])

  if (floatMenu !== 'answerCreate') {
    return null
  }

  const formatOptions = [
    { value: 'email', label: 'Email', type: 'email' },
    { value: 'phone', label: 'Телефон', type: 'phone' },
    { value: 'number', label: 'Число', type: 'number' },
  ]

  const activeAnswerField = currentElem.data.items.find(item => item.id === currentItem).data.answerField
  const activeFieldOption = fields.find(opt => opt.value === activeAnswerField)
  const activeFormatOption = formatOptions.find(opt => opt.value === answerFormat)

  const showReadyAnswers = () => {
    if (!readyAnswers.trim().length) {
      return null
    }

    const answers = readyAnswers.split(',').filter(item => !!item.trim())

    return (
      <Fragment>
        {answers.map((item, i) => {
          return (
            <div className="constructor-elemmenu__answers-item" key={i}>{item}
              <span className='constructor-elemmenu__answers-item-close'
                onClick={() => {
                  const txt = answers.filter(answer => answer !== item).join(',').trim()
                  setReadyAnswers(txt)
                }}
              >×</span>
            </div>
          )
        })}
      </Fragment>
    )
  }

  return (
    <OutsideClickHandler
      onOutsideClick={() => {
        onClose()
      }}
    >
      <FloatMenu
        title='Настройка ответа'
        coords={{ top, left, bottom }}
        onClose={onClose}
        modifier='answerCreate'
      >
        <div className="constructor-elemmenu__answer">
          <div className="constructor-elemmenu__label">Сохранять ответ в пользовательское поле</div>
          <div className="constructor-elemmenu__choice">
            <Select
              placeholder='Выберите поле'
              options={fields}
              activeOption={activeFieldOption}
              isNewField={true}
              modifier='answer'
              onCreateField={onCreateField}
              onChange={(item) => {
                setAnswerField(item.value)
              }}
            />
            <div className="constructor-elemmenu__answer-block">
              <div className={
                cx(
                  "constructor-elemmenu__answer-checkbox",
                  { "constructor-elemmenu__answer-checkbox_active": answerCorrect }
                )
              }
                onClick={() => setAnswerCorrect(!answerCorrect)}
              >Проверять корректность ответа</div>
              {answerCorrect
                ? <Fragment>
                  <Select
                    placeholder='Выберите формат ответа'
                    options={formatOptions}
                    activeOption={activeFormatOption}
                    modifier='answer'
                    onChange={(item) => {
                      setAnswerFormat(item.value)
                    }}
                  />
                  <br />
                  <div className="constructor-elemmenu__label">Сообщение, если ответ не прошел проверку</div>
                  <ElemMenuEditor
                    placeholder='Введите сюда текст сообщения'
                    text={wrongAnswerText}
                    onChangeText={setWrongAnswerText}
                    onOpenFloatMenu={onOpenFloatMenu}
                  />
                </Fragment>
                : null}
            </div>
            <div className="constructor-elemmenu__answer-block">
              <div className={
                cx(
                  "constructor-elemmenu__answer-checkbox",
                  { "constructor-elemmenu__answer-checkbox_active": answerRepeat }
                )
              }
                onClick={() => setAnswerRepeat(!answerRepeat)}
              >Повторить вопрос, если ответ не получен</div>

              {answerRepeat ?
                <Fragment>
                  <div className="constructor-elemmenu__row constructor-elemmenu__row_bottom">
                    <div className="constructor-elemmenu__answer-text">Через какое время повторить вопрос?</div>
                    <div className="constructor-elemmenu__row">
                      <input
                        type="text"
                        maxLength={2}
                        placeholder='часы'
                        value={repeatHours}
                        className={
                          cx(
                            'constructor-elemmenu__input',
                            'constructor-elemmenu__input_small',
                            {
                              'constructor-elemmenu__input_error': error.repeatHours
                            })
                        }
                        onChange={(e) => {
                          setError({ ...error, repeatHours: false })
                          if (e.target.value > 23) {
                            setRepeatHours('00')
                            setError({ ...error, repeatHours: true })
                          } else {
                            if (!isNaN(e.target.value)) {
                              setRepeatHours(e.target.value)
                            }
                          }
                        }}
                        onBlur={(e) => {
                          if (e.target.value < 10) {
                            setRepeatHours(`0${+e.target.value}`)
                          }
                        }}
                      />&nbsp;:&nbsp;
                    <input
                        type="text"
                        maxLength={2}
                        placeholder='мин'
                        value={repeatMinutes}
                        className={
                          cx(
                            'constructor-elemmenu__input',
                            'constructor-elemmenu__input_small',
                            {
                              'constructor-elemmenu__input_error': error.repeatMinutes
                            })
                        }
                        onChange={(e) => {
                          setError({ ...error, repeatMinutes: false })
                          if (e.target.value > 59) {
                            setRepeatMinutes('00')
                            setError({ ...error, repeatMinutes: true })
                          } else {
                            if (!isNaN(e.target.value)) {
                              setRepeatMinutes(e.target.value)
                            }
                          }
                        }}
                        onBlur={(e) => {
                          if (e.target.value < 10) {
                            setRepeatMinutes(`0${+e.target.value}`)
                          }
                        }}
                      />
                    </div>
                  </div>
                  <div className="constructor-elemmenu__label">Сообщение в случае повторной отправки</div>
                  <ElemMenuEditor
                    placeholder='Введите сюда текст сообщения'
                    text={repeatAnswerText}
                    onChangeText={setRepeatAnswerText}
                  />
                </Fragment>
                : null
              }
            </div>
            <div className="constructor-elemmenu__answer-block">
              <div className="constructor-elemmenu__answer-ready">
                <div className="constructor-elemmenu__label">Добавить готовый ответ</div>
                <input
                  type="text"
                  className="constructor-elemmenu__input"
                  placeholder='Введите готовые ответы через запятую'
                  value={readyAnswer}
                  onChange={(e) => setReadyAnswer(e.target.value)}
                  onKeyPress={(e) => {
                    if (e.key === 'Enter') {
                      setReadyAnswer('')
                      setReadyAnswers(`${readyAnswers}, ${e.target.value}`)
                    }
                  }}
                />
                <div className="constructor-elemmenu__answers">
                  {showReadyAnswers()}
                </div>
              </div>
            </div>
          </div>
          <div className="constructor-elemmenu__button-footer">
            <div className="constructor-elemmenu__button-cancel"
              onClick={onClose}
            >Отмена</div>
            <div className="constructor-elemmenu__button-submit"
              onClick={() => {
                let answerFieldLabel = null
                if (fields.find(field => field.value === answerField)) {
                  answerFieldLabel = fields.find(field => field.value === answerField).label
                }
                onSubmit({
                  answerField,
                  answerFieldLabel,
                  answerCorrect,
                  wrongAnswerText,
                  answerRepeat,
                  answerFormat,
                  repeatHours,
                  repeatMinutes,
                  repeatAnswerText,
                  readyAnswers
                })

                onClose()
              }}>Подтвердить</div>
          </div>
        </div>
      </FloatMenu>
    </OutsideClickHandler>
  )
}

export default ElemMenuAnswer;