import { handleActions, combineActions } from 'redux-actions';
import produce from 'immer'

import {
  authActions,
} from '../actions'

const { auth } = authActions

const initialState = {
  token: null,
  location: null,
  sessionId: null,
  domain: null,
  forgetsuccess: false,
  loading: false,
  error: null,
}

export default handleActions(
  {
    [combineActions(
      auth.login.request.toString(),
      auth.reg.request.toString(),
      auth.check.request.toString(),
      auth.forget.request.toString(),
    )]: produce((draft, action) => {
      draft.loading = true
    }),

    [auth.login.success.toString()]: produce((draft, action) => {
      console.log('action');
      console.log(action.payload);
      const { sessionId, operator } = action.payload
      draft.sessionId = sessionId
      draft.domain = operator.name
      draft.loading = false
    }),

    [auth.login.failure.toString()]: produce((draft, action) => {
      draft.error = action.payload
      draft.loading = false
    }),

    [auth.reg.success.toString()]: produce((draft, action) => {
      draft.token = action.payload
      draft.loading = false
    }),

    [auth.check.success.toString()]: produce((draft, action) => {
      draft.location = action.payload
      draft.loading = false
    }),

    [auth.reg.failure.toString()]: produce((draft, action) => {
      draft.error = action.payload
      draft.loading = false
    }),

    [auth.forget.success.toString()]: produce((draft, action) => {
      draft.forgetsuccess = true
      draft.loading = false
    }),

    [auth.forget.failure.toString()]: produce((draft, action) => {
      draft.error = action.payload
      draft.loading = false
    }),

    [auth.drop.toString()]: produce((draft, action) => {
      draft.token = null
      draft.location = null
      draft.sessionId = null
      draft.domain = null
      draft.forgetsuccess = false
      draft.loading = false
      draft.error = null
      draft.error = null
    }),
  },
  initialState
);
