import React from 'react'
import { Provider } from 'react-redux';

import LoginController from './controllers/LoginController'
import RegController from './controllers/RegController'
import RegCheckController from './controllers/RegCheckController'
import ForgetController from './controllers/ForgetController'
import AccountsController from './controllers/AccountsController'
import AudienceController from './controllers/AudienceController'
import ChatController from './controllers/ChatController'
import StatsController from './controllers/StatsController'
import MailingsController from './controllers/MailingsController'
import BotsController from './controllers/BotsController'
import KeywordsController from './controllers/KeywordsController'
import AutoMailingsController from './controllers/AutoMailingsController'
import PaymentController from './controllers/PaymentController'
import SettingsController from './controllers/SettingsController'
import ConstructorBotController from './controllers/ConstructorBotController'

import URLS from './const/urls'

import store from './store';

import './App.scss'

import {
  BrowserRouter as Router,
  Switch,
  Route,
} from 'react-router-dom'

const App: React.FC = () => {
  return (
    <Provider store={store}>
      <Router>
        <Switch>
          <Route
            exact
            path={URLS.auth}
            component={LoginController}
          />
          <Route
            exact
            path={URLS.login}
            component={LoginController}
          />
          <Route
            exact
            path={URLS.signup}
            component={RegController}
          />
          <Route
            exact
            path={URLS.signupcheck}
            component={RegCheckController}
          />
          <Route
            exact
            path={URLS.forgot}
            component={ForgetController}
          />
          <Route
            exact
            path={URLS.main}
            component={ConstructorBotController}
          />
          <Route
            exact
            path={URLS.accounts}
            component={AccountsController}
          />
          <Route
            exact
            path={URLS.audience}
            component={AudienceController}
          />
          <Route
            exact
            path={URLS.mailings}
            component={MailingsController}
          />
          <Route
            exact
            path={URLS.chat}
            component={ChatController}
          />
          <Route
            exact
            path={URLS.stats}
            component={StatsController}
          />
          <Route
            exact
            path={URLS.bots}
            component={BotsController}
          />
          <Route
            exact
            path={URLS.keywords}
            component={KeywordsController}
          />
          <Route
            exact
            path={URLS.automailings}
            component={AutoMailingsController}
          />
          <Route
            exact
            path={URLS.automailingId}
            component={AutoMailingsController}
          />
          <Route
            exact
            path={URLS.payment}
            component={PaymentController}
          />
          <Route
            exact
            path={URLS.settings}
            component={SettingsController}
          />
          <Route
            exact
            path={URLS.flowId}
            component={ConstructorBotController}
          />
          {/* <Route
            exact
            path={URLS.mailingId}
            component={ConstructorMailingController}
          /> */}
        </Switch>
      </Router>
    </Provider>
  );
}

export default App;
