import React from 'react';
import classnames from 'classnames';

import './DataTable.scss';

export default function DataTable({
  className,
  children,
}) {

  const tableClasses = classnames({
    'table': true,
    [className]: !!className,
  });

  return (
    <div
      className={tableClasses}
    >
      { children}
    </div>
  );
}
