const LOGIN = 'cb-toolkit'
const PASSWORD = 'YsB514ETczzDsCAaKjaU'

const hash = new Buffer(`${LOGIN}:${PASSWORD}`).toString('base64')

export const getData = async (url) => {
  let response = await fetch(url, {
    headers: {
      'Authorization': `Basic ${hash}`
    }
  });
  let json = await response.json();

  return json
}

export const uploadData = async (url, formData) => {
  let response = await fetch(url, {
    method: 'POST',
    body: formData,
    headers: {
      'Accept': '*/*',
      'Authorization': `Basic ${hash}`
    }
  })

  let json = await response.json();

  return json
}

export const postData = async (url, options) => {
  let response = await fetch(url, {
    method: 'POST',
    body: JSON.stringify({
      ...options
    }),
    headers: {
      "Content-type": "application/json; charset=UTF-8",
      'Accept': 'application/json',
      'Authorization': `Basic ${hash}`
    }
  })
  let json = await response.json();

  return json
}

export const upload = async (url, options) => {
  const formData = new FormData();
  formData.append('file', options.file)

  let response = await fetch(url, {
    method: 'POST',
    body: formData,
    headers: {
      'Accept': '*/*',
      'Authorization': `Basic ${hash}`
    }
  })
  let json = await response.json();

  return json
}