import React, { Fragment } from 'react';

const ElemMenuFileItem = ({
  file,
  onCopyItem,
  onRemoveItem,
  onMoveItem,
}) => {
  return (
    <Fragment>
      <div className="constructor-elemmenu__label">Файл</div>
      <div className='constructor-elemmenu__item constructor-elemmenu__upload'>
        <div className="constructor-elemmenu__operations">
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_delete"
            onClick={() => onRemoveItem(file.id)}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_up"
            onClick={() => onMoveItem(file.id, 'up')}
          />
          <div className="constructor-elemmenu__operations-item constructor-elemmenu__operations-item_down"
            onClick={() => onMoveItem(file.id, 'down')}
          />
        </div>
        <div className="constructor-elemmenu__upload-item constructor-elemmenu__upload-item_file">
          <div className='constructor-elemmenu__upload-file'>{file.name}</div>
        </div>
      </div>
    </Fragment>
  )

};

export default ElemMenuFileItem;