import { createActions } from 'redux-actions';
import { v4 as uuidv4 } from 'uuid';
import { getRandomInt } from '../const/utils'

export default createActions({
  ELEMS: {
    FETCH: {
      REQUEST: null,
      SUCCESS: (elems) => (elems),
      FAILURE: null
    },
    ADD: (elemType) => ({
      id: `node_${uuidv4()}`,
      msgId: `msg_${uuidv4()}`,
      actionId: `action_${uuidv4()}`,
      conditionId: `condition_${uuidv4()}`,
      delayId: `delay_${uuidv4()}`,
      elemType,
      position: { x: document.body.clientWidth / 2 - getRandomInt(0, 50), y: document.body.clientHeight / 2 - getRandomInt(0, 50) },
    }),
    ADDLINK: (type, isBtn) => ({
      id: `node_${uuidv4()}`,
      msgId: `msg_${uuidv4()}`,
      linkId: isBtn ? `btn_link_${uuidv4()}` : `link_${uuidv4()}`,
      type,
    }),
    COPY: (elemId) => ({ id: `node_${uuidv4()}`, elemId }),
    REMOVE: (elems) => (elems),
    REMOVECONFIRM: (confirm) => (confirm),
    SELECT: (elemId) => (elemId),
    LINK: (sourceId, targetId, isBtn) => ({ id: isBtn ? `btn_link_${uuidv4()}` : `link_${uuidv4()}`, sourceId, targetId }),
    UNLINK: (sourceId, targetId) => ({ sourceId, targetId }),
    COORDS: (elem) => (elem)
  },
  URL: {
    ADD: (url) => ({
      id: `url_${uuidv4()}`,
      url,
    }),
    REMOVE: null,
  },
  FIELDS: {
    CREATE: (label, value, type) => ({ id: `field_${uuidv4()}`, label, value, type }),
  },
  BUTTON: {
    SELECT: (btn) => (btn),
    ADD: (msgId) => ({ id: `btn_${uuidv4()}`, msgId }),
    REMOVE: (msgId) => (msgId),
    LINK: (targetId) => ({ id: `link_${uuidv4()}`, targetId }),
    RENAME: (text) => (text),
  },
  ANSWER: {
    ADD: (answer) => (answer),
    REMOVE: (msgId) => (msgId),
  },
  TITLE: {
    EDIT: (edit) => (edit),
    CHANGE: (title) => (title),
  },
  MESSAGE: {
    ADD: () => ({ id: `msg_${uuidv4()}` }),
  },
  QUESTION: {
    ADD: () => ({ id: `quest_${uuidv4()}` }),
  },
  IMAGE: {
    UPLOAD: {
      REQUEST: (imgList) => ({ id: `img_${uuidv4()}`, img: imgList[0] }),
      SUCCESS: ({ id, img }) => ({ id, img }),
      FAILURE: null
    },
    REMOVE: (imgId) => (imgId),
  },
  FILE: {
    UPLOAD: {
      REQUEST: (fileList) => ({ id: `file_${uuidv4()}`, file: fileList[0] }),
      SUCCESS: ({ id, file }) => ({ id, file }),
      FAILURE: null
    },
    REMOVE: (fileId) => (fileId),
  },
  ITEM: {
    SELECT: (itemId) => (itemId),
    COPY: (itemId, buttonsNum) => {
      if (buttonsNum) {
        const arr = new Array(buttonsNum).fill('');
        const buttons = arr.map(item => {
          return `btn_${uuidv4()}`
        })

        return { id: `node_${uuidv4()}`, itemId, buttons }
      }

      return { id: `node_${uuidv4()}`, itemId }
    },
    REMOVE: (itemId) => (itemId),
    MOVE: (itemId, direction) => ({ itemId, direction }),
    CHANGE: (itemId, txt) => ({ itemId, txt }),
  },
  ACT: {
    ADD: (actionType) => ({ id: `${actionType}_${uuidv4()}`, actionType }),
    REMOVE: (actionId) => (actionId),
    TAG: {
      ADD: (itemId, text) => ({ id: `tag_${uuidv4()}`, itemId, text }),
      REMOVE: (tagId) => (tagId),
    },
    FIELD: {
      ADD: (itemId, field) => ({ itemId, field }),
      CHANGE: (itemId, fieldValue, type) => ({ itemId, fieldValue, type }),
    },
    EMAIL: {
      ADD: (itemId, text) => ({ id: `email_${uuidv4()}`, itemId, text }),
      REMOVE: (emailId) => (emailId),
    }
  },
  CONDITION: {
    FORM: (conditionForm) => (conditionForm),
    ITEM: {
      ADD: (conditionType, conditionOptions) =>
        ({ id: `${conditionType}_${uuidv4()}`, conditionType, conditionOptions }),
      REMOVE: (conditionId) => (conditionId),
    }
  },
  DELAY: {
    VALUE: (value) => (value),
    PERIOD: (period) => (period),
    EXACT: null,
    FROMTO: (field, value) => ({ field, value }),
    SCHEDULE: (schedule) => (schedule),
  },
  FLOATMENU: {
    OPEN: (top, left, bottom, type) => ({ top, left, bottom, type }),
    CLOSE: null
  },
  TEMPLATE: {
    SET: (template) => (template),
  },
  SAVEBOT: {
    REQUEST: null,
    SUCCESS: null,
    FAILURE: null
  },
});
